import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'customizeDateMonthYear'
})
export class CustomizeDateMonthYearPipe implements PipeTransform {
  /**
   * Liste des mois
   */
  private monthsComplet = {
    0: 'Janvier',
    1: 'Fevrier',
    2: 'Mars',
    3: 'Avril',
    4: 'Mai',
    5: 'Juin',
    6: 'Juillet',
    7: 'Août',
    8: 'Septembre',
    9: 'Octobre',
    10: 'Novembre',
    11: 'Decembre',
  };

  transform(value: any, ...args: any[]): any {
    const date = new Date(value);
    let dateFormat = '';
    dateFormat += this.getMonthComplet(date.getMonth()) + ' ' + date.getFullYear();
    return dateFormat;
  }

  public getMonthComplet(index: number) {
    return this.monthsComplet[index];
  }


}
