import {Component, OnInit, Input} from '@angular/core';
import {NavController} from '@ionic/angular';
import {TranslateService} from '@ngx-translate/core';
import {AfricleanControlRegistrationProvider} from 'src/providers/africlean-control-fields/africlean-control-registration.provider';
import {AfricleanHttpUserProvider} from 'src/providers/africlean-http/africlean-http-user.provider';
import {AfricleanModalProvider} from 'src/providers/africlean-modal/africlean-modal.provider';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanConfigProvider} from 'src/providers/africlean-config/africlean-config.provider';
import {ClientType, TraceAction} from '../../../../providers/utils/commonEnum';

@Component({
    selector: 'africlean-user-registration',
    templateUrl: './africlean-user-registration.component.html',
    styleUrls: ['./africlean-user-registration.component.scss', '../registration-theme.scss'],
})
export class AfricleanUserRegistrationComponent implements OnInit {

    /**
     * Message d'erreur à afficher si un problème survient
     */
    public errorMsg: string;

    /**
     * Etat du spinner de chargement
     */
    public hideSpinner = true;

    /**
     * Homme ou Femme
     */
    public gender = 1;

    /**
     * lastname de l'utilisateur
     */
    public lastName: string;

    /**
     * firstname de l'utilisateur
     */
    public firstName: string;

    /**
     *  Adresse mail de l'utilisateur
     */
    public email: string;

    /**
     * Numéro de téléphone de la personne
     */
    public phone: string;
    /**
     * Numéro de téléphone de la personne
     */
    public phoneCode = '224';

    /**
     * Mot de passe
     */
    public password: string;

    /**
     * Mot de passe confirmé
     */
    public confirmPassword: string;

    /**
     * Placeholder du mot de passe
     */
    public passwordPlaceholder: string;

    /**
     * Placeholder du mot de passe
     */
    public dateOfBirth: Date;

    /**
     * Source d'appel du component
     */
    @Input() public source = 'registration';
    /**
     * Type d'utilisateur
     */
    @Input() public userType: number;

    /**
     * Controler le click du bouton valider
     */
    public hideValidateButton = false;

    /**
     * Role de l'utilisateur
     */
    @Input() public role;
    /**
     * Action de l'utilisateur
     */
    @Input() public action: string;

    /**
     * utilisateur à modifier
     */
    @Input() public userUpdate: any;

    /**
     * Contrôle l'accès au mot de passe
     */
    public pwdCheck = false;

    /**
     * Teste la platform
     */
    public isWeb: boolean;
    public maxDate: string;

    // public maxDate = ((new Date()).getFullYear() - 16) + '-0' + ((new Date()).getUTCMonth() + 1) + '-' + ((new Date()).getUTCDate());

    /**
     * Type de compte
     */
    public youAre = 'Particulier';
    /**
     * Verifier si on affiche ou non le champ nom de la société
     */
    public displaySocietyName = true;

    /**
     * Nom de la société
     */
    public societyName: string;

    /**
     * Particulier ou Pro
     */
    @Input() public customer = 1;

    /**
     * Description de la société
     */
    public societyDescription: string;

    /**
     * La valeur de l'attribut acountType
     */
    public acountType = 'acountType';


    constructor(private navCtrl: NavController,
                private controlFiels: AfricleanControlRegistrationProvider,
                private translateService: TranslateService,
                private httpUser: AfricleanHttpUserProvider,
                private session: AfricleanSessionProvider,
                private modal: AfricleanModalProvider,
                private config: AfricleanConfigProvider) {
        this.getMessage('INFO_BULLE.REGISTRATION.INFO.PASSWORD', 'passwordPlaceholder');
    }


    /**
     * Initialisation de la page
     */
    ngOnInit() {
        // Test la platforme
        this.isWeb = this.config.isWeb();
        if (this.action === 'update') {
            this.init();
        }
        const date = new Date();
        const year = date.getFullYear() - 16;
        const month1 = date.getUTCMonth() + 1;
        const day = date.getUTCDate();
        const month = (month1 > 9) ? (date.getUTCMonth() + 1) : ('0' + month1);
        const dayFormat = (day > 9) ? date.getUTCDate() : ('0' + day);
        const dateMa = year + '-' + month + '-' + dayFormat;
        this.maxDate = dateMa.toString();
    }

    public init() {
        this.firstName = this.userUpdate.firstName;
        this.lastName = this.userUpdate.lastName;
        const p2 = this.userUpdate.phoneNumber.split(' ');
        this.phoneCode = (p2 && p2.length > 0) ? p2[0].replace('+', '') : '224';
        this.phone = (p2 && p2.length > 0) ? this.userUpdate.phoneNumber.replace(p2[0], '') : this.userUpdate.phoneNumber;
        // this.phone = (this.userUpdate.phoneNumber) ? this.userUpdate.phoneNumber : '' ;
        this.email = this.userUpdate.email;
        this.gender = this.userUpdate.genderOfUser;
        this.dateOfBirth = this.userUpdate.dateOfBirth;
        this.role = this.userUpdate.typeOfUser;
        this.customer = this.userUpdate && this.userUpdate.customerType ?
            this.userUpdate.customerType : ClientType.PARTICULIER;
        if (this.customer && this.customer === ClientType.PRO) {
            this.societyName = this.userUpdate.corporateName;
            this.societyDescription = this.userUpdate.description;
        }
    }

    /**
     * Récupère la date de naissance
     * @param event évenement émis
     */
    public getDate(event: any) {
        this.dateOfBirth = event.target.value;
    }

    public getGender(gender: number) {
        this.gender = gender;
    }

    /**
     * Enregistre un nouvel utilisateur
     */
    public async registration() {
        const phone = '+' + this.phoneCode + ' ' + this.phone.replace(' ', '');
        const users = await this.session.getItem('user');
        const entity = users.entity;
        // Test si les champs sont existants
        /*  if (this.lastName && this.password &&
              this.confirmPassword && this.gender &&
              this.firstName && phone && this.dateOfBirth) {

  */
        if (this.lastName && this.password &&
            this.confirmPassword && this.gender &&
            this.firstName && phone && this.dateOfBirth &&
            (this.customer === ClientType.PARTICULIER || (this.customer === ClientType.PRO
                && this.societyName && this.societyName.trim().length !== 0))) {

            const fieldsValues = {
                lastName: this.lastName,
                firstName: this.firstName,
                email: this.email,
                phoneNumber: phone,
                gender: this.gender,
                password: this.password,
                confirmPassword: this.confirmPassword,
                dateOfBirth: this.dateOfBirth,
                role: this.role,
            };
            const result = this.controlFiels.registration(fieldsValues);

            this.hideSpinner = result.hideSpinner;

            if (result.message != null) {
                this.getMessage(result.message, 'errorMsg');
            } else {
                this.errorMsg = result.message;

                const userId = await this.session.getItem('userId');
                const roleAuthor = await this.session.getItem('role');
                let userCreatedAuthor: {};

                if (roleAuthor !== null && userId !== null) {
                    userCreatedAuthor = users;
                }

                let body = {};
                if (this.email && this.email !== '') {
                    body = {
                        firstName: this.firstName,
                        lastName: this.lastName,
                        gender: this.gender,
                        email: this.email,
                        password: this.password,
                        phoneNumber: phone,
                        dateOfBirth: this.dateOfBirth,
                        userType: this.userType,
                        userCreatedAuthorId: userCreatedAuthor,

                        societyName: this.societyName,
                        description: this.societyDescription,
                        customerType: this.customer,
                        entity,
                    };
                } else {
                    body = {
                        firstName: this.firstName,
                        lastName: this.lastName,
                        gender: this.gender,
                        password: this.password,
                        phoneNumber: phone,
                        dateOfBirth: this.dateOfBirth,
                        userType: this.userType,
                        userCreatedAuthorId: userCreatedAuthor,

                        societyName: this.societyName,
                        description: this.societyDescription,
                        customerType: this.customer,
                        entity
                    };
                }
                console.log(body);
                this.hideValidateButton = true;
                this.httpUser.save('user/login/', {
                        phoneNumber: fieldsValues.phoneNumber,
                        role: fieldsValues.role
                    }
                ).subscribe(
                    (user1) => {
                        if (user1 && user1.length > 0) {
                            this.hideSpinner = true;
                            this.errorMsg = 'Un compte ' + fieldsValues.role + ' avec ce numero de télephone existe déjà';
                        } else {
                            this.hideSpinner = true;
                            if (this.role !== 5) {
                                this.saveEmploye(body).subscribe(
                                    (user) => {
                                        const role = (users && users.typeOfUser) ? users.typeOfUser : user.role;
                                        const data = {
                                            role,
                                            action: TraceAction.CREATE,
                                            user
                                        };
                                        this.postTrace(data);
                                        this.modal.dismiss({displayRegistrationConfirmation: true});
                                    });
                            } else {
                                this.saveClient(body).subscribe(
                                    (client) => {
                                        const role = (users && users.typeOfUser) ? users.typeOfUser : client.role;
                                        const data = {
                                            role,
                                            action: TraceAction.CREATE,
                                            user: client
                                        };
                                        this.postTrace(data);
                                        if (this.source === 'registration') {
                                            this.navCtrl.navigateForward('/', {
                                                queryParams: {displayRegistrationConfirmation: true}
                                            });
                                        } else {
                                            this.modal.dismiss({displayRegistrationConfirmation: true});
                                        }
                                    }
                                );
                            }
                        }
                    });
            }

        } else {
            this.getMessage('INFO_BULLE.EMPTY_FIELD', 'errorMsg');
        }

    }

    /**
     * Renvoie le bon message
     * @param message message à afficher
     * @param attribut attribut à utiliser pour stocker le message
     */
    private getMessage(message: string, attribut: string) {
        this.translateService.get(message).subscribe((res: string) => {
            if (attribut === 'errorMsg') {
                this.errorMsg = res;
            } else {
                this.passwordPlaceholder = res;
            }
        });
    }

    /**
     * Lance l'authentification
     */
    public sendAuthent() {
        const fields = {
            login: this.phone,
            password: this.password
        };
        this.httpUser.login('login/login-user', fields.login, fields.password).subscribe(
            (user) => {
            }
        );
    }


    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({displayRegistrationConfirmation: false});
    }

    /**
     * Enregistrer un client
     * @param body corps de la requête
     */
    private saveClient(body: {}) {
        console.log(body);
        return this.httpUser.saveItem('client/save', body);
    }

    /**
     * Enregistre l'historique
     * @param body corps de la requête
     */
    private postTrace(body: {}) {
        return this.httpUser.saveItem('trace/save', body).subscribe(
            (result) => {
                console.log(result);
            }
        );
    }

    /**
     * Enregistrer un intervennant
     * @param body corps de la requête
     */
    private saveIntervennant(body: {}) {
        return this.httpUser.save('user/employe/intervenant/save', body);
    }

    /**
     * Enregistrer un commercial
     * @param body corps de la requête
     */
    private saveCommercial(body: {}) {
        return this.httpUser.save('user/employe/commercial/save', body);
    }

    /**
     * Enregistrer un manager
     * @param body corps de la requête
     */
    private saveEmploye(body: {}) {
        return this.httpUser.saveItem('user/employe/save', body);
    }

    getUserType(role: number) {
        this.role = role;
    }


    /**
     * Modification d'un nouvel utilisateur
     */
    public async updateUser() {
        const userId = await this.session.getItem('userId');
        const roleAuthor = await this.session.getItem('role');
        const phone = '+' + this.phoneCode + ' ' + this.phone.replace(' ', '');
        const body = {
            userId: this.userUpdate.id,
            phoneNumber: this.userUpdate.phoneNumber,
            newPhoneNumber: phone,
            role: this.userUpdate.typeOfUser,
            firstname: this.firstName,
            lastname: this.lastName,
            password: this.userUpdate.password,
            email: this.email,
        };
        const url = 'user/update-informations';
        if (this.controlForm()) {
            this.httpUser.updateInfos(url, body).subscribe(
                (userUpdated) => {
                    // mettre à jour la session
                    if (this.userUpdate.typeOfUser === roleAuthor) {
                        this.session.setItem('user', userUpdated);
                        this.session.setItem('login', userUpdated.phoneNumber);
                    }
                    this.modal.dismiss({displayRegistrationConfirmation: true});
                }
            );
        } else {
            // TODO : Afficher un message d'erreur
        }
    }

    public controlForm() {
        if (this.firstName.trim() !== '' && this.lastName.trim() !== '' &&
            this.phone.trim() !== '') {

            // On vérifie si le mail existe et que son format est bon
            if (this.email && this.email.trim() !== '') {
                return this.controlFiels.controlEmail(this.email);
            }
            return true;
        }
        return false;
    }

    /**
     * Switch vers le mot de passe
     * @param event événement
     */
    public togglePassword(event: any) {
        this.pwdCheck = event.detail.checked;
    }

    private _filter(value: string, list: any[]): any[] {
        const filterValue = value;
        return list.filter(option => option.dialCode.indexOf(filterValue) === 0);
    }

    telInputObject(obj) {
        const list = obj.p;
        if (this.phone) {
            const country = this._filter(this.phoneCode.replace('+', ''), list);
            if (country.length > 0) {
                obj.setCountry(country[0].iso2);
                this.phoneCode = country[0].dialCode;
            }

        }

    }

    getNumber(event) {
    }

    onCountryChange(event: any) {
        const phone = this.phone;
        const p2 = phone.split(' ');
        // tslint:disable-next-line:max-line-length
        //  this.phone = (p2 && p2.length > 0 ) ? '+' + event.dialCode + ' ' + phone.replace(p2[0], '').trim() : event.dialCode + ' ' + phone.trim();
        this.phoneCode = event.dialCode;
    }

    /**
     * Recupere le type de client
     * @param customer
     */
    public getCustomer(customer: number) {
        this.customer = customer;
        if (this.action === 'create') {
            this.societyName = null;
            this.societyDescription = null;
        }
    }
}



