import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-africlean-configuration-manager',
  templateUrl: './africlean-configuration-manager.component.html',
  styleUrls: ['./africlean-configuration-manager.component.scss'],
})
export class AfricleanConfigurationManagerComponent implements OnInit {
  /**
   * Check pour la localisation
   */
  public localisationtionCheck = false;
  /**
   * Check pour le montant
   */
  public montantCheck = false;
  /**
   * Check pour le paiement
   */
  public paymentCheck = false;
  /**
   * Check pour la incident
   */
  public incidentCheck = false;

  /**
   * Check pour les établissements
   */
  public establishmentCheck = false;

  segment = 'entity';

  constructor() { }

  ngOnInit() {}

  toggleLocalisation(event: any) {
    this.localisationtionCheck = !this.localisationtionCheck;
    this.montantCheck = false;
    this.paymentCheck = false;
    this.incidentCheck = false;
    this.establishmentCheck = false;
  }
  toggleMontant(event: any) {
    this.montantCheck = !this.montantCheck;
    this.localisationtionCheck = false;
    this.paymentCheck = false;
    this.incidentCheck = false;
    this.establishmentCheck = false;
  }
  togglePaiment(event: any) {
    this.paymentCheck = !this.paymentCheck;
    this.montantCheck = false;
    this.localisationtionCheck = false;
    this.incidentCheck = false;
    this.establishmentCheck = false;
  }
  toggleIncident(event: any) {
    this.incidentCheck = !this.incidentCheck;
    this.montantCheck = false;
    this.paymentCheck = false;
    this.localisationtionCheck = false;
    this.establishmentCheck = false;
  }

  toggleEstablishment(event: any) {
    this.establishmentCheck = !this.establishmentCheck;
    this.montantCheck = false;
    this.paymentCheck = false;
    this.localisationtionCheck = false;
    this.incidentCheck = false;
  }

  segmentChanged(event: any) {
    this.segment = event.detail.value;
  /*  switch (this.segment) {
      case 'particulier':
        return  1;
      case 'pro':
        return  2;
      default:
        return 3;
    }*/

  }
}
