import { Pipe, PipeTransform } from '@angular/core';
import {FinanceCategory} from '../../providers/utils/commonEnum';
@Pipe({
  name: 'categoryType'
})
export class FinanceCategoryPipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    switch (value) {
      case FinanceCategory.EXPLOITATION:
        return 'Exploitation';
      case FinanceCategory.HOR_EXPLOITATION:
        return 'Hors exploitation';
      case FinanceCategory.FINANCIER:
        return 'Financier';
      default:
        return 'Autre';
    }
  }

}
