import {Component, Input, OnInit} from '@angular/core';
import {AfricleanHttpSubscribeProvider} from '../../../providers/africlean-http/africlean-http-subscribe.provider';
import {AfricleanSessionProvider} from 'src/providers/africlean-session/africlean-session.provider';
import {AfricleanHttpUserProvider} from 'src/providers/africlean-http/africlean-http-user.provider';
import {map, mergeMap} from 'rxjs/operators';
import {from} from 'rxjs';
import {NavController} from '@ionic/angular';
import {AfricleanModalProvider} from 'src/providers/africlean-modal/africlean-modal.provider';
import {AfricleanObservablesProvider} from 'src/providers/africlean-observables/africlean-observables.provider';
import {ActivatedRoute} from '@angular/router';
import {AfricleanHttpItemListProvider} from '../../../providers/africlean-http/africlean-http-item-list.provider';
import {StateAdress, UserRole} from '../../../providers/utils/commonEnum';

@Component({
    selector: 'africlean-abonnement-subscription',
    templateUrl: './africlean-abonnement-subscription.component.html',
    styleUrls: ['./africlean-abonnement-subscription.component.scss'],
})
export class AfricleanAbonnementSubscriptionComponent implements OnInit {

    public abonnement: any[];

    /**
     * Liste des adresses renseigner
     */
    public adresses: any[];

    /**
     * Liste des adresse choisis
     */
    private adressesChecked: any[] = [];

    /**
     * Tester si les adresses existent
     */
    public isAdressExist: boolean;

    /**
     * Afficher le spinner
     */
    public hideSpinner = false;

    /**
     * Source d'appel du component
     */
    @Input() public source = 'consult-abonnement';

    /**
     * id de l'utilisateur
     */
    @Input() public userId;

    /**
    * Controler le click du bouton valider
    */
    public hideValidateButton = false;

    /**
     * Message d'erreur
     */
    public errorMsg: string;

    /**
     * Télephone du client
     */
    public phone: string;
    /**
     * Numéro de téléphone de la personne
     */
    public phoneCode = '224';

    /**
     * Formulaire de recherche du client
     */
    @Input() public displayForm = false;
    /**
     * Client
     */
    public client: any;

    /**
     * client concerné
     */
    @Input() public userTMP: any;

    @Input() public role: number;


    // TODO: Gerer le cas ou les adressses ne sont pas renseigner dans l'espace MES INFORMATIONS
    constructor(private subscribeProvider: AfricleanHttpSubscribeProvider,
                private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private navCtrl: NavController,
                private modal: AfricleanModalProvider,
                private route: ActivatedRoute,
                private itemListProvider: AfricleanHttpItemListProvider,
                private observablesProvider: AfricleanObservablesProvider) {
    }

    ngOnInit() {
        this.adressesChecked = [];
        this.adresses = [];
        this.init();
        // this.getAllPayementTranche();
    }

    /**
     * Initialisation de la page
     */
    public async init() {
        this.role = await this.session.getItem('role');
        const user = await this.session.getItem('user');
        if (this.role === UserRole.CLIENT) {
            this.client = user;
            this.userId = user.id;
            this.session.setItem('userTMP', user);
            this.getAdresses().subscribe(
                (_) => {
                    if (this.adresses.length === 0) {
                        this.isAdressExist = false;
                        this.hideSpinner = true;
                    } else {
                        this.isAdressExist = true;
                        this.hideSpinner = true;
                    }
                }
            );
        } else {
            this.isAdressExist = false;
            this.hideSpinner = false;
        }
    }

    /**
     * Redirige vers la page informations
     */
    public async goToInformationsPage() {
        if (this.source === 'consult-abonnement') {
            await this.modal.dismiss();
            this.navCtrl.navigateForward('consult-informations');
        } else {
            this.hideSpinner = false;
            this.getClientAllAdresses().subscribe(
                (adressesList) => {
                    const props = {
                        adresses: adressesList,
                        user: this.client
                    };
                    /*  this.navCtrl.navigateForward('consult-subscribe-management', {
                          queryParams: props
                      });
                      this.modal.dismiss();*/
                }
            );
        }
    }

    /**
     * Adresse à lié à un abonnement
     * @param home homeCode
     */
    public adressChecked(home: any) {
        // Afficher la liste des abonnements aux quelles l'user est souscrit et les soubscriptions en cours
        // Implémenter le désabonnement
        // Revoir la fonction delete et update des adresses
        // Revoir model Notification (hiden and state le changer de place mettre dans l'object userId)

        if (home && home.id) {
            const serv = this._filterByAdress(home.id, this.adressesChecked);
            if (serv && serv.length === 0) {
                this.adressesChecked.push({home});
            } else {
                const index = this.adressesChecked.indexOf({home});
                this.adressesChecked.splice(index, 1);
            }
            console.log(this.adressesChecked);
           // this.adressesChecked.push({homeId: homId});
        }
    }

    private _filterByAdress(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.home.id.toString().indexOf(filterValue) === 0);
    }

    /**
     * Enregistre un abonnement
     */
    public async subscription() {
        let userId: number;
        if (this.source === 'consult-abonnement') {
            userId = await this.session.getItem('userId');
        } else {
            userId = this.userId;
        }
        let i;
        this.hideValidateButton = true;
        for (i = 0; i < this.adressesChecked.length; i++) {

            const home = this.adressesChecked[i].home;
            const body = {
                client: this.client,
                home,
                company: this.client.entity
            };
            console.log(body);
            this.subscribeProvider.save('abonnement/save', body).subscribe(
                (subscribe) => {
                    this.observablesProvider.subscriptionRequest.emit(true);
                    this.observablesProvider.loadAbonnement.emit(true);
                    this.modal.dismiss({displayAbonnementConfirmation: true});
                }
            );

        }
    }

    /**
     * Ferme le modal d'abonnement
     */
    public close() {
        this.modal.dismiss({displaySubscribeConfirmation: true});
    }


    /**
     * Renvoi la liste de toutes les abonnements
     */
    private getAllAbonnement() {
        this.subscribeProvider.getAll('abonnement').subscribe(
            (abonnement) => {
                this.abonnement = abonnement;
            }
        );
    }

    /**
     * Renvoie toutes les adresses
     */
    public getAdresses() {
        if (this.source === 'consult-abonnement') {
            return from(this.session.getItem('userId')).pipe(
                mergeMap(
                    (userId) => {
                       /* const url = 'user/free-adress/' + userId;
                        const body = {
                            role: 'CLIENT'
                        };*/
                        const url = 'user/adress/' + this.userId;
                        const body = {
                            role: UserRole.CLIENT,
                            type: StateAdress.FREE
                        };
                        // Recupere les adresses qui n'ont pas déja un abonnement
                        return from(this.userProvider.getAdress(url, body)).pipe(
                            map(
                                (adresses) => {
                                    this.adresses = adresses;
                                }
                            )
                        );
                    }
                )
            );
        } else {
            if (this.userId) {
               /* const url = 'user/free-adress/' + this.userId;
                const body = {
                    role: UserRole.CLIENT
                };
*/
                const url = 'user/adress/' + this.userId;
                const body = {
                    role: UserRole.CLIENT,
                    type: StateAdress.FREE
                };
                // Recupere les adresses qui n'ont pas déja un abonnement
                return from(this.userProvider.getAdress(url, body)).pipe(
                    map(
                        (adresses) => {
                            this.adresses = adresses;
                        }
                    )
                );
            }
        }
    }

    /**
     * Retourn  un client en fonction du argument body
     * @param body corps de la requête
     */
    private getClient(body: {}) {
        return this.userProvider.getClient('client/login', body);
    }

    contract() {

    }

    public findClient() {
        this.hideSpinner = false;
        let body = {};
        const phone = '+' + this.phoneCode + ' ' + this.phone.replace(' ', '');
        body = {
            phoneNumber: phone
        };
        return this.getClient(body).pipe(
            map(
                (user) => {
                    this.client = user;
                    this.userId = user.id;
                    this.session.setItem('userTMP', user);
                    console.log(user);
                }
            ),
            mergeMap(
                (_) => {
                    return this.getAdresses().pipe(
                        map(
                            () => {
                                if (this.adresses.length === 0) {
                                    this.isAdressExist = false;
                                    this.hideSpinner = true;
                                } else {
                                    this.isAdressExist = true;
                                    this.hideSpinner = false;
                                }
                            }
                        )
                    );

                }
            )
        ).subscribe(
            (_) => {
            }
        );
    }


    /**
     * Renvoie toutes les adresses
     */
    public getClientAllAdresses() {
        const url = 'user/adress/' + this.userId;
        const body = {
            role: UserRole.CLIENT
        };
        return this.userProvider.getAdress(url, body);
    }

    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({subscription: false});
    }

    private _filter(value: string, list: any[]): any[] {
        const filterValue = value;
        return list.filter(option => option.dialCode.indexOf(filterValue) === 0);
    }

    telInputObject(obj) {
        const list = obj.p;
        if (this.phone) {
            const country = this._filter(this.phoneCode.replace('+', ''), list);
            if (country.length > 0) {
                obj.setCountry(country[0].iso2);
                this.phoneCode = country[0].dialCode;
            }

        }

    }

    getNumber(event) {
    }

    onCountryChange(event: any) {
        const phone = this.phone;
        const p2 = phone.split(' ');
        this.phoneCode = event.dialCode;
    }
}
