import { Component, OnInit } from '@angular/core';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';

@Component({
  selector: 'app-africlean-forgot-password',
  templateUrl: './africlean-forgot-password.component.html',
  styleUrls: ['./africlean-forgot-password.component.scss'],
})
export class AfricleanForgotPasswordComponent implements OnInit {

  constructor(private modal: AfricleanModalProvider) { }

  ngOnInit() {}

    /**
     * Close la modal
     */
    public closeModal() {
      this.modal.dismiss({ displayRegistrationConfirmation: false });
  }

}
