import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {AfricleanHttpUserProvider} from '../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanSessionProvider} from '../../../providers/africlean-session/africlean-session.provider';
import {AfricleanAlertProvider} from '../../../providers/africlean-alert/africlean-alert.provider';
import {AfricleanModalProvider} from '../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanObservablesProvider} from '../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanPayementListComponent} from '../africlean-payement-list/africlean-payement-list.component';
import {AfricleanHttpPayementProvider} from '../../../providers/africlean-http/africlean-http-payement.provider';
import {AfricleanDateProvider} from '../../../providers/africlean-date/africlean-date.provider';
import {map} from 'rxjs/operators';
import {AfricleanAdressListComponent} from '../africlean-adress-list/africlean-adress-list.component';
import * as jspdf from 'jspdf';
import html2canvas from 'html2canvas';
import {AfricleanRecuPrintedComponent} from "./africlean-recu-printed/africlean-recu-printed.component";
import {CustomizeDateMonthYearPipe} from "../../pipes/customize-date-month-year.pipe";
import {CustomizeDatePipe} from "../../pipes/customize-date.pipe";
import {AfricleanDataProvider} from "../../../providers/africlean-data/africlean-data.provider";
import {AfricleanSubscribeModel} from "../../../models/abonnement/africlean-subscription.model";
import {AfricleanHttpSubscribeProvider} from "../../../providers/africlean-http/africlean-http-subscribe.provider";
import {UserRole} from "../../../providers/utils/commonEnum";

@Component({
    selector: 'app-africlean-payement-display',
    templateUrl: './africlean-payement-display.component.html',
    styleUrls: ['./africlean-payement-display.component.scss'],
})
export class AfricleanPayementDisplayComponent implements OnInit {

    /**
     * Theme lié au payement
     */
    @Input() public theme: string;


    /**
     * Affichage du bouton
     */
    @Input() public displayButton: boolean;

    /**
     * Affichage du checkbox
     */
    @Input() public displayCheckbox: boolean;

    /**
     * Emet le payementCode
     */
    @Output() public emitPayementId = new EventEmitter<any>();

    /**
     * Emet le home code
     */
    @Output() public emitHomeCode = new EventEmitter<any>();

    /**
     * Emet le home code
     */
    @Output() public emitPrinted = new EventEmitter<any>();

    /**
     * utilisateur concerné
     */
    @Input() public user: any;

    /**
     * Source d'appel du component
     */
    @Input() public source = 'consult-finance';

    /**
     * Payement
     */
    @Input() public payement: {
        user: any,
        payement: any
    };


    /**
     * Payement
     */
    @Input() public payementTMP: {
        user: any,
        payement: any
    };
    /**
     * titre1
     */
    @Input() public title: string;

    /**
     * titre 2
     */
    @Input() public title2: string;

    /**
     * titre 2
     */
    @Input() public role: number;

    /**
     * Action
     */
    @Input() public action: string;

    public adress: any[] = [];

    @Input() isWeb: boolean;

    @ViewChild('content', { static: true}) content: ElementRef;


    constructor(private userProvider: AfricleanHttpUserProvider,
                private session: AfricleanSessionProvider,
                private alert: AfricleanAlertProvider,
                private payementProvider: AfricleanHttpPayementProvider,
                private modal: AfricleanModalProvider,
                private dataService: AfricleanDataProvider,
                private subscribeProvider: AfricleanHttpSubscribeProvider,
                private observableProvider: AfricleanObservablesProvider) {
    }

    ngOnInit() {
        this.init();
    }

    public async init() {
        this.role = await this.session.getItem('role');
        // tslint:disable-next-line:max-line-length no-unused-expression
        // (this.action !== 'update') ? this.payement.payement.dateOfPayment = this.dateProvider.getStructNormalDate(this.payement.payement.dateOfPayment) : ' ';

    }

    /**
     * Choisit l'adresse comme un abonnement
     */
    public subscription() {
        this.emitPayementId.emit(this.payement.payement.objectId);
    }

    /**
     * Modifie une adresse
     */
    public async update() {
        const payementList: { user: any, payement: any }[] = [];
        payementList.push(this.payement);
        const props = {
            source: 'consult-subscribe-management',
            typePayement: 'finance',
            role: UserRole.CLIENT,
            theme: 'abonnement',
            subscribeCode: this.payement.payement.subscribeCode,
            userId: this.payement.payement.clientCode,
            payements: payementList,
            action: 'update',
        };
        await this.modal.create(AfricleanPayementListComponent, 'my-custom-modal-css', props);
        const data: { displayPayementConfirmation: boolean } = await this.modal.getData();
        if (data.displayPayementConfirmation) {
            this.observableProvider.loadPayement.emit(true);
        }
    }

    /**
     * Suppression d'un Payement
     */
    public delete() {
       /* const url = 'payement-employe/delete/' + this.payement.payement.objectId;
        this.subscribeProvider.delateSubscribe(url).subscribe(
            (intervention) => {
            }
        );*/
        const url = 'payement/delete/' + this.payement.payement.id;
        this.subscribeProvider.delateSubscribe(url).subscribe(
            (intervention) => {
            }
        );
    }

    /**
     * Affiche l'adresse de l'incident
     */
    public async showAdress() {
        let props = {};
        const userId = this.payement.user.id;
        const url = 'user/subscribe-adress/' + userId;
        const body = {
            role: 'CLIENT'
        };
        this.userProvider.getAdress(url, body).pipe(
            map(
                (adresses) => {
                    this.adress = adresses;
                    const adresseFiltre = this.filter(this.payement.payement.homeCode, this.adress);
                    props = {
                        adresses: adresseFiltre,
                        source: 'consult-incident',
                        action: 'payement',
                        payementObject: this.payement
                    };
                    this.modal.create(AfricleanAdressListComponent, 'my-custom-modal-css', props);
                }
            )
        ).subscribe(
            (adresses) => {
            });
    }

    private filter(value: string, list: any[]): any[] {
        const filterValue = value;
        return list.filter(option => option.idHome.indexOf(filterValue) === 0);
    }

    async print() {
        const props = {
            payement: this.payement,
        };
        await this.modal.create(AfricleanRecuPrintedComponent, 'my-custom-modal-css', props);
        const data: { displayPayementConfirmation: boolean } = await this.modal.getData();
        if (data.displayPayementConfirmation) {
            this.observableProvider.loadPayement.emit(true);
        }
    }


    getAdresses(payement) {
        const url = 'user/subscribe-adress-byHome';
        const body = {
            homeId: payement.homeCode
        };
        return  this.userProvider.getAdress(url, body).pipe(
            map(
                (adresses) => {
                    this.adress = adresses;
                    return adresses;
                }
            ));
    }

    printPdf(payment) {

        const paymentPdf = this.dataService.clone(payment);
        const pipe = new CustomizeDateMonthYearPipe();
        const pipe2 = new CustomizeDatePipe();
        paymentPdf.payement.dateToSubscribePayment = pipe.transform( paymentPdf.payement.dateToSubscribePayment);
        paymentPdf.payement.dateOfPayment = pipe2.transform( paymentPdf.payement.dateOfPayment);
        // const mht = 100 * (parseFloat(paymentPdf.payement.amountToPay.toString().replace(' ', '')) / 118);
        // mht.toFixed(0)
        const montant1 = {
            ttc: paymentPdf.payement.amountToPay,
            ht: paymentPdf.payement.amountToPay,
        };
        this.getAdresses(paymentPdf.payement).subscribe(
            (result) => {
                const body = {
                    user:  paymentPdf.user,
                    payement:  paymentPdf.payement,
                    montant: montant1,
                    adress: result[0]
                };
                const options = {
                    responseType: 'arraybuffer' as 'json'
                };
                this.payementProvider.getRecutPdf('file-manager/pdf', body, options).subscribe(
                    (res) => {
                        if (res) {
                            const newBlob = new Blob([res], {type: 'application/pdf'});
                            const fileURL = URL.createObjectURL(newBlob);
                            window.open(fileURL, '_blank');
                        }
                    });
            }
        );
    }

}
