import { Pipe, PipeTransform } from '@angular/core';
import {PaiementMean} from '../../providers/utils/commonEnum';
@Pipe({
  name: 'paiemntMean'
})
export class PaiementMeanPipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    switch (value) {
      case PaiementMean.ESPECE:
        return 'Espèce';
      case PaiementMean.CHEQUE:
        return 'Cheque';
      case PaiementMean.MOBILEMONEY:
        return 'Mobile money';
      case PaiementMean.BANCAIRE:
        return 'Bancaire';
      case PaiementMean.VIREMENT:
        return 'Virement';
      default:
        return 'Autre';
    }
  }

}
