import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AfricleanObservablesProvider} from '../../../../providers/africlean-observables/africlean-observables.provider';
import {AlertController, MenuController, NavController} from '@ionic/angular';
import {AfricleanConfigProvider} from '../../../../providers/africlean-config/africlean-config.provider';
import {AfricleanMessageFormComponent} from '../../africlean-message-form/africlean-message-form.component';
import {AfricleanModalProvider} from '../../../../providers/africlean-modal/africlean-modal.provider';

@Component({
  selector: 'app-africlean-left-menu',
  templateUrl: './africlean-left-menu.component.html',
  styleUrls: ['./africlean-left-menu.component.scss'],
})
export class AfricleanLeftMenuComponent implements OnInit {
  /**
   * Etat du menu
   * true = ouvert
   */
  @Input() isOpen: boolean;

  @Input() status = 'disconnected';

  /**
   * Teste si l'utilisateur est authentifier
   */
  public isAuth: boolean;

  /**
   * Le nombre de notifications non lu
   */
  public count: number;

  /**
   * Teste si le type de plateforme
   */
  @Input() public isWeb: boolean;

  /**
   * url du photo de profile
   */
  public src = './assets/icon/users.svg';

  /**
   * Event sur le bouton principal du header
   */
  @Output() clickButton = new EventEmitter<string>();

  /**
   * Lors du click des boutons du menu : pour contrôler le changement de couleur
   */
  public homePageClicked = false;
  public servicesPageClicked = false;
  public teamPageClicked = false;
  public clientPageClicked = false;
  public contactPageClicked = false;

  constructor(private navCtrl: NavController,
              private config: AfricleanConfigProvider,
              private menu: MenuController,
              public observableProvider: AfricleanObservablesProvider,
              private modal: AfricleanModalProvider,
              private alertController: AlertController) {
  }

  ngOnInit() {
   // this.isWeb = this.config.isWeb();
  //  console.log(this.isWeb);
  }

  /**
   * Ferme le menu lors du clique sur le bouton
   */
  public closeMenu() {
    this.menu.close();
  }


  /**
   * Traite le changement de focus (onglet active) et redirige vers la bonne page
   * @param target
   */
  public goToPage(target: string) {

    if (target === 'accueil') {
      this.homePageClicked = true;
      this.servicesPageClicked = false;
      this.teamPageClicked = false;
      this.clientPageClicked = false;
      this.contactPageClicked = false;
      this.navCtrl.navigateForward(target);
      this.closeMenu();
    }

    if (target === 'services') {
      this.homePageClicked = false;
      this.servicesPageClicked = true;
      this.teamPageClicked = false;
      this.clientPageClicked = false;
      this.contactPageClicked = false;
      this.clickButton.emit('services');
      this.observableProvider.ongletEmiter.emit('services');
      this.closeMenu();
    }

    if (target === 'equipe') {
      this.homePageClicked = false;
      this.servicesPageClicked = false;
      this.teamPageClicked = true;
      this.clientPageClicked = false;
      this.contactPageClicked = false;
      this.clickButton.emit('equipe');
      this.observableProvider.ongletEmiter.emit('equipe');
      this.closeMenu();
    }

    if (target === 'authentication') {
      this.homePageClicked = false;
      this.servicesPageClicked = false;
      this.teamPageClicked = false;
      this.clientPageClicked = true;
      this.contactPageClicked = false;
      this.navCtrl.navigateForward(target);
      this.closeMenu();
    }

    if (target === 'contact') {
      this.sendMessage();
      this.closeMenu();
    }
  }

  /**
   * Envoie un message
   */
  public async sendMessage() {
    const props = {
      title: 'Message sur africlean'
    };
    await this.modal.create(AfricleanMessageFormComponent, 'my-custom-modal-css', props);
  }

}
