import {Component, Input, OnInit} from '@angular/core';
import {AfricleanHttpItemListProvider} from '../../../../../providers/africlean-http/africlean-http-item-list.provider';

@Component({
    selector: 'app-manage-establishment',
    templateUrl: './manage-establishment.component.html',
    styleUrls: ['./manage-establishment.component.scss'],
})
export class ManageEstablishmentComponent implements OnInit {
    errorMsg: any;
    @Input() action: string;
    @Input() actionItem: string;
    showAddFormuler = true;
    showAddService = false;
    showAddAmount = false;
    showAddIncident = false;

    name: string;
    description: string;
    kbs: string;
    @Input() public entities = [];
    @Input() public services = [];
    @Input() public incidents = [];

    public selectedServices = [];

    currentEstablissement: any;
    title: any;
    value: any;
    devise: any;

    constructor(private itemListProvider: AfricleanHttpItemListProvider) {
    }

    ngOnInit() {
        this.init();
    }

    init() {
        this.showAddService = false;
        this.showAddFormuler = false;
        this.itemListProvider.getAll('company').subscribe(
            (result) => {
                if (result) {
                    console.log(result);
                    this.entities = result;
                    this.showAddFormuler = true;
                    this.name = '';
                    this.description = '';
                    this.kbs = null;
                }
            }
        );
      //  this.getService();
    }

    getService() {
        this.services = [];
        this.itemListProvider.getAll('service').subscribe(
            (result) => {
                if (result) {
                    this.incidents = [];
                    this.selectedServices = [];
                    this.services = result;
                    console.log(result);
                }
            }
        );
    }

    getIncidentType() {
        this.services = [];
        this.itemListProvider.getAll('incident-type').subscribe(
            (result) => {
                if (result) {
                    this.services = [];
                    this.selectedServices = [];
                    this.incidents = result;
                    console.log(result);
                }
            }
        );
    }

    updateEstablishment() {

    }

    add() {
        const data = {
            name: this.name,
            description: this.description,
            kbs: this.kbs
        };
        const url = 'company/save';
        if (this.name.trim() !== '' && this.description.trim() !== '') {
            this.itemListProvider.save(url, data).subscribe(
                (result) => {
                    this.init();
                }
            );
        }
    }

    addList() {
        this.name = '';
        this.description = '';
        this.kbs = null;
        this.showAddService = false;
        this.showAddAmount = false;
        this.showAddFormuler = !this.showAddFormuler;
    }

    update(item: any) {
        console.log(item);
    }

    delete(item: any) {

    }

    addService(item: any) {
        this.currentEstablissement = item;
        this.getService();
        this.showAddService = true;
        this.showAddAmount = false;
        this.showAddIncident = true;
    }

    selectService(item: any) {
        const serv = this._filter(item.id, this.selectedServices);
        if (serv && serv.length === 0) {
            this.selectedServices.push(item);
        } else {
            console.log(this.selectedServices);
            const index = this.selectedServices.indexOf(item);
            this.selectedServices.splice(index, 1);
        }
    }

    private _filter(value: string, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.id.toString().indexOf(filterValue) === 0);
    }

    saveAddService() {
        let data = {};
        let url: string;
        if (this.services.length > 0 && this.selectedServices.length > 0) {
            if (this.showAddAmount) {
                url = 'montant/save';
                data = {
                    company: this.currentEstablissement,
                    service: this.selectedServices[0],
                    title: this.title,
                    type: 1,
                    montant: this.value,
                    devise: this.devise
                };
            } else {
                data = {
                    services: this.selectedServices,
                    company: this.currentEstablissement,
                };
                url = 'company/service';
            }
            console.log(data);
            this.itemListProvider.save(url, data).subscribe(
                 (result) => {
                   console.log(result);
                   this.init();
                 }
             );
        }
        if (this.incidents.length > 0 && this.selectedServices.length > 0 && this.showAddIncident) {
            data = {
                incidents: this.selectedServices,
                company: this.currentEstablissement,
            };
            url = 'company/incident';
            console.log(data);
            this.itemListProvider.save(url, data).subscribe(
                (result) => {
                    console.log(result);
                    this.init();
                }
            );
        }
    }

    addAmount(item: any) {
        this.currentEstablissement = item;
        this.getService();
        this.showAddIncident = false;
        this.showAddService = true;
        this.showAddAmount = true;
    }
    addIncidenType(item: any) {
        this.currentEstablissement = item;
        this.getIncidentType();
        this.showAddService = false;
        this.showAddAmount = false;
        this.showAddIncident = true;
    }

    getServices(event) {
        console.log(event.target.value);
        const service = this._filter(event.target.value, this.selectedServices);
        console.log(service);
    }
}
