import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import { AfricleanModalProvider } from '../../../providers/africlean-modal/africlean-modal.provider';
import { AfricleanSessionProvider } from '../../../providers/africlean-session/africlean-session.provider';
import { AfricleanHttpPayementProvider } from '../../../providers/africlean-http/africlean-http-payement.provider';
import { AfricleanDateProvider } from '../../../providers/africlean-date/africlean-date.provider';
import { AfricleanObservablesProvider } from '../../../providers/africlean-observables/africlean-observables.provider';
import { AfricleanFilterProvider } from '../../../providers/africlean-filter/africlean-filter.provider';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {AfricleanConfigProvider} from '../../../providers/africlean-config/africlean-config.provider';
import * as jspdf from 'jspdf';
import html2canvas from 'html2canvas';
import { CustomizeDateMonthYearPipe } from 'src/app/pipes/customize-date-month-year.pipe';
import { CustomizeDatePipe } from 'src/app/pipes/customize-date.pipe';
import {UserRole} from '../../../providers/utils/commonEnum';


@Component({
  selector: 'app-africlean-payement-list',
  templateUrl: './africlean-payement-list.component.html',
  styleUrls: ['./africlean-payement-list.component.scss'],
})
export class AfricleanPayementListComponent implements OnInit {
  /**
   * Liste des adresses
   */
  @Input() public payements: any[] = [];

  @Input() public finances: any[] = [];

  @Input() public payementsTMP: any[] = [];

  configFinance = {
    id: 'custom8',
    itemsPerPage: 5,
    currentPage: 1,
    totalItems: 0
  };
  /**
   * Type de payement
   */
  @Input() public typePayement = 'abonnement';


  /**
   * Utilisateur concerné
   */
  @Input() public user: any;

  /**
   * client concerné
   */
  @Input() public userTMP: any;

  /**
   * D'ou est appelé le component
   */
  @Input() public source = 'consult-finance';

  /**
   * role de l'utilisateur
   */
  @Input() public role: number;

  /**
   * id de l'utilisateur
   */
  @Input() public userId: number;


  /**
   * Theme
   */
  @Input() public theme: string;

  /**
   * id de l'abonnement
   */
  @Input() public  subscribeCode: number;

  /**
   * action
   */
  @Input() public action: string;

  /**
   * payement actuelle
   */
  @Input() currentPayement: any;

  /**
   * Liste des filtres
   */
  public filters: {
    filter1: string,
    filter2: string[]
  }[];

  /**
   * Choix pour filtrer
   */
  public choice: {
    filter1: string,
    filter2: string
  }[];
  /**
   * Condition pour afficher les filtres
   */
  public showFiltre = false;
  /**
   * Spiner
   */
  public hideSpinner = false;


  /**
   * numéro des pages
   */
  public positions = [];

  /**
   * Position de la page courante
   */
  @Input() public currentPage = 1;

  /**
   * Position de la page courante
   */
  @Input() public pagination = true;

  /**
   * Annonces temporaires
   */
  @Input() public clientsTmp: Array<any>;

  /**
   * Le nombre de clients par pages
   */
  @Input() public nbrPaiementPerPage: any;

  /**
   * Emet la position de la page actuel
   */
  @Output() public position = new EventEmitter<any>();

  /**
   * reactilise le paiment
   */
  @Output() public loadPaiement = new EventEmitter<any>();

  searchControl = new FormControl();
  searches = [];
  allSearches = [];
  filteredSearches: Observable<string[]>;
  separatorKeysCodes: number[] = [];
  keyword: any;
  filteredOptions: Observable<any[]>;

  filterType: any;
  placeHolder = 'Rechercher par prenom/nom';
  showFilterField = false;
  @Input() isWeb: boolean;

  name = 'jsPDF Example';

  payement: any;
  printed = false;

  constructor(private modal: AfricleanModalProvider,
              private session: AfricleanSessionProvider,
              private payementProvider: AfricleanHttpPayementProvider,
              private observableProvider: AfricleanObservablesProvider,
              private filterProvider: AfricleanFilterProvider,
              private config: AfricleanConfigProvider) {}

  ngOnInit() {
    this.getUser();
    // tslint:disable-next-line:no-unused-expression
    (this.source === 'consult-subscribe-management') ? this.getPayements() : ' ';

    // Initialise le filtres
    this.initializeFilters();

    this.isWeb = this.config.isWeb();

    // Initialise le choix
    this.choice = [];
    // Dés qu'il y'a un chamgement au nivea des payement notament une mise a jour,
    // on récharge les données.
    this.observableProvider.loadPayement.subscribe(
        (load) => {
          if (load) {
          //  this.getPayements();
            this.loadPaiement.emit(load);
          }
        }
    );
   // this.payement = this.payements[0];
  }

  /**
   * Close la modal
   */
  public closeModal() {
    this.modal.dismiss({displayPaiement: false});
  }

  /**
   * Utilisateur temporaire
   */
  public async getUser() {
    this.role = await this.session.getItem('role');
    this.userId = await this.session.getItem('userId');
    this.userTMP = await this.session.getItem('userTMP');
  }


  /**
   * Liste de payement
   */
  public getPayements() {
    if (this.action === 'update') {

    } else {
      let url: string;
      if (this.typePayement === 'abonnement') {
        if (this.role === UserRole.CLIENT) {
          url = 'payement/client/' + this.userId;
        } else {
          url = 'payement/';
        }
      } else {
        if (this.role === UserRole.COMMERCIAL || this.role === UserRole.INTERVENANT) {
          url = 'payement-employe/employe/' + this.userId;
        } else {
          url = 'payement-employe/';
        }

      }
      this.payementProvider.getAll(url).subscribe(
          (payementList) => {
            if (this.source === 'consult-subscribe-management') {
              const payementsTMP = [];
              payementList.forEach(payement => {
                if (payement.payement.subscribeCode === this.subscribeCode) {
                  payementsTMP.push(payement);                    }
              });
              this.payements = payementsTMP;
            } else {
              this.payements = payementList;
            }
          }
      );
    }


  }

  /**
   * Actualise la liste despayement
   * @param event evenement émis
   */
  public actualizePayement(event: any) {
  }


  /**
   * VAlidation d'un payement
   */
  public confirmPayement() {
    const url = 'finance/confirm';
    const body = {
      payementCode: this.finances[0].id
    };
    this.payementProvider.save(url, body).subscribe(
        (payement) => {
          this.modal.dismiss({ displayPayementConfirmation: true });
        }
    );
  }

  /**
   * Annulation du payement
   */
  public cancelPayement() {
    const url = 'finance/cancel';
    const body = {
      payementCode: this.finances[0].id
    };
    this.payementProvider.save(url, body).subscribe(
        (payement) => {
          this.modal.dismiss({ displayPayementConfirmation: true });
        }
    );
  }

  /**
   * Ferme le modal d'abonnement
   */
  public close() {
    this.modal.dismiss({ displayPayementConfirmation: true });
  }

  /**
   * Recupere le choix pour filtrer les données
   * @param value évenement émis
   * @param type type de filtre
   */
  public getFilters(value: any, type: string) {
    const choice = {
      filter1: value.filter1,
      filter2: value.filter2
    };
    this.choice.push(choice);
    this.filter(choice);
  }

  /**
   * Rafraichir la recherche
   */
  public refresh() {
    this.choice = [];
    this.getPayements();
  }

  /**
   * Filtre la liste des employés
   */
  private filter(choice: {filter1: string, filter2: string}) {
    // Represente le resultat a un instant t
    const currentPayements: { user: any, payement: any}[] = this.payements;
    this.payements = null;
    this.sort(choice, currentPayements);

  }

  private sort(choice: {filter1: string, filter2: string}, currentPayements: { user: any,
                                                                               payement: any}[]) {

    if (choice.filter1 === 'Status') {
      this.payements = [];
      currentPayements.forEach(payement => {
        if (payement.payement.sateOfPayment === choice.filter2) {
          this.payements.push(payement);
        }
      });
    }
    if (choice.filter1 === 'Date de payement') {
      this.payements = currentPayements;
      if ( choice.filter2 === 'Croissant') {
        this.payements.sort(this.filterProvider.compareByPayementCreationDate);
      }
      if ( choice.filter2 === 'Décroissant') {
        this.payements.sort(this.filterProvider.compareByPayementCreationDate).reverse();
      }

    }
    if (choice.filter1 === 'Mois payer') {
      this.payements = currentPayements;
      if ( choice.filter2 === 'Croissant') {
        this.payements.sort(this.filterProvider.compareByDatePayed);
      }
      if ( choice.filter2 === 'Décroissant') {
        this.payements.sort(this.filterProvider.compareByDatePayed).reverse();
      }

    }
  }

  /**
   * Initialise l'objet filters
   */
  private initializeFilters() {
    this.filters = [];
    this.filters.push(
        {
          filter1: 'status',
          filter2: ['EN COURS', 'VALIDER']
        },
        {
          filter1: 'Date de payement',
          filter2: ['Croissant', 'Décroissant']
        },
        {
          filter1: 'Mois payer',
          filter2: ['Croissant', 'Décroissant']
        },
    );
  }
  public showFiltres() {
    this.showFiltre = this.showFiltre !== true;
  }

    sendPosition(position) {
        this.position.emit(position);
    }

  searchPaiement($event: KeyboardEvent) {
    if (this.keyword.trim() !== '') {
      this.filteredOptions = this.searchControl.valueChanges
          .pipe(
              startWith(''),
              map(item => item ? this._filter(item) : this.payements.slice()));
      this.filteredOptions.subscribe(
          (result) => {
            this.sendPosition(1);
            this.payements = result;
            // this.clientsTmp = result;
          });
    } else {
      this.payements = this.payementsTMP;
    }

  }

  getFilterType(value) {
    this.filterType = value;
    this.showFilterField = true;
    this.showFiltre = false;
    // tslint:disable-next-line:max-line-length
    this.placeHolder = (value === 'status') ? 'Rechercher par status' : ((value === 'phone') ? 'Rechercher par Tétéphone' : 'Rechercher par Nom/Prénom');
  }
  private _filter(name: any): any[] {
    const filterValue = name.toLowerCase();
    // tslint:disable-next-line:max-line-length
    return (this.filterType === 'name') ? (this.payementsTMP.filter(item => (item.user.lastName.toLowerCase().indexOf(filterValue) === 0
        // tslint:disable-next-line:max-line-length
        || item.user.firstName.toLowerCase().indexOf(filterValue) === 0))) : ((this.filterType === 'phone') ? (this.payementsTMP.filter(item => (item.user.phoneNumber.toLowerCase().indexOf(filterValue) === 0))) : (this.payementsTMP.filter(item => (item.payement.sateOfPayment.toLowerCase().indexOf(filterValue) === 0))));
  }

  htmlToPdf() {
    // const doc = new jspdf('p', 'pt', 'a4');
    const ta = document.getElementById('pdfSink');
  /*  const specialElementHandlers = {
      '#editor'(element, renderer) {
        return true;
      }
    };
   // doc.fromHTML(ta, 0, 0);
    doc.fromHTML(ta, 15, 15, {
      width: 190,
      elementHandlers: specialElementHandlers
    });*/
   // doc.save('demo.pdf');


    // tslint:disable-next-line:only-arrow-functions
    html2canvas(ta).then(function(canvas) {
      const img = canvas.toDataURL('image/png');
      const doc = new jspdf('p', 'mm', 'a4');
      doc.addImage(img, 'JPEG', 20, 20);
      doc.save('test.pdf');
    });
  }

  onPrint() {
    const ta = (this.isWeb) ? document.getElementById('print-section') : document.getElementById('print-section-mobile');

    // tslint:disable-next-line:only-arrow-functions
    html2canvas(ta).then(function(canvas) {
      const img = canvas.toDataURL('image/png');
      const doc = new jspdf('p', 'pt', 'a4');
      doc.addImage(img, 'JPEG', 20, 20);
      doc.save('test.pdf');
     // window.open(doc.output('bloburl'), '_blank');
    });
  }


  /**
   * Ovre le document pour le telecharger
   * @param pdfFile
   */
  openDocument(pdfFile) {
    const newBlob = new Blob([pdfFile], {type: 'application/pdf'});
    const fileURL = URL.createObjectURL(newBlob);
    window.open(fileURL, '_blank');
  }


  public printPDF() {
    const domElement = (this.isWeb) ? document.getElementById('print-section') : document.getElementById('print-section-mobile');

    html2canvas(domElement, {
      onclone: (document) => {
        document.getElementById('print-section').style.visibility = 'hidden'
      }
    })
        .then((canvas) => {
          const img = canvas.toDataURL('image/png');
          const pdf = new jspdf();
          pdf.addImage(img, 'JPEG', 0, 0, 20, 20);
          pdf.save('your-filename.pdf');
        });
  }
}
