import {Component, Input, OnInit} from '@angular/core';
import {AfricleanHttpItemListProvider} from '../../../../../providers/africlean-http/africlean-http-item-list.provider';

@Component({
  selector: 'app-manage-service',
  templateUrl: './manage-service.component.html',
  styleUrls: ['./manage-service.component.scss'],
})
export class ManageServiceComponent implements OnInit {
  errorMsg: any;
  @Input() action: string;
  @Input() actionItem: string;
  showAddFormuler = true;
  name: string;
  description: string;
  kbs: string;
  @Input() public entities = [];

  constructor(private itemListProvider: AfricleanHttpItemListProvider) { }

  ngOnInit() {
    this.init();
  }

  init() {
    this.itemListProvider.getAll('service').subscribe(
        (result) => {
          if (result) {
            this.entities = result;
            this.showAddFormuler = true;
            this.name = '';
            this.description = '';
          }
        }
    );
  }

  updateEstablishment() {

  }

  add() {
    const data = {
      name: this.name,
      description: this.description,
    };
    const url = 'service/save';
    if (this.name.trim() !== '' && this.description.trim() !== '') {
      this.itemListProvider.save(url, data).subscribe(
          (result) => {
            this.init();
          }
      );
    }
  }

  addList() {
    this.name = '';
    this.description = '';
    this.kbs = null;
    this.showAddFormuler = !this.showAddFormuler;
  }

  update(item: any) {
    console.log(item);
  }

  delete(item: any) {

  }
}
