/**
 * Model de données prospection
 */
export class AfricleanProspectionModel {
    /**
     * Identifiant du prospection
     */
    public objectId: string;

    /**
     * Date de debut prospection
     */
    public dateCreateProspect: Date;

    /**
     * Date de debut prospection
     */
    public dateStartProspect: Date;

    /**
     * Date de prospection
     */
    public prospectionDate: Date;

    /**
     * Date de fin du prospection
     */
    public dateEndProspect: Date;
    /**
     * horaire de prospection
     */
    public timeProspect: string;

    /**
     * Nom de l'utilisateur
     */
    public firstName: string;

    /**
     * Prenom de l'utilisateur
     */
    public lastName: string;
    /**
     * Telephone portalble de l'utilisateur
     */
    public phoneNumber: string;

    /**
     * Genre de l'utilisateur (masculin ou fiminin)
     */
    public genderOfUser: string;

    /**
     * Resultat du prospection
     */
    public prospectResult: string;
    /**
     * commentaire du resultat
     */
    public commentResult: string;
    /**
     * Type de prospection
     */
    public typeOfProspection: string;

    /**
     * Etat de prospection (Plannifié, traiter)
     */
    public stateOfProspection: string;

    /**
     * adresse pour la prospection
     */
    public prospectionAdress: string;


    /************************code de liaison clé (clé etranger)*****/
    /**
     *Code (identifiant) de commercial
     */
    public commercialCode: string;

    /**
     * Code (identifiant) de l'auteur
     */
    public authorCode: { authorId: string; role: string };

}
