import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanObservablesProvider} from '../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanHttpUserProvider} from '../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanDateProvider} from '../../../../providers/africlean-date/africlean-date.provider';
// tslint:disable-next-line: max-line-length
import {AfricleanUserRegistrationComponent} from '../../africlean-registration/africlean-user-registration/africlean-user-registration.component';
import {AfricleanModalProvider} from 'src/providers/africlean-modal/africlean-modal.provider';
import {AfricleanConfigProvider} from 'src/providers/africlean-config/africlean-config.provider';
import {AfricleanFilterProvider} from 'src/providers/africlean-filter/africlean-filter.provider';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {UserRole} from '../../../../providers/utils/commonEnum';

@Component({
    selector: 'app-africlean-client-manager',
    templateUrl: './africlean-client-manager.component.html',
    styleUrls: ['./africlean-client-manager.component.scss'],
})
export class AfricleanClientManagerComponent implements OnInit {

    /**
     * Liste des clients
     */
    @Input() public clients: any[];

    /***
     * Auteur de la création de l'utilisateur
     */
    public userCreatedAuthorId: { authorId: number, role: number } = null;

    /**
     * Liste des filtres
     */
    public filters: {
        filter1: string,
        filter2: string[]
    }[];

    /**
     * Choix pour filtrer
     */
    public choice: {
        filter1: string,
        filter2: string
    }[];

    /**
     * adresse à rechercher
     */
    public adressToSearch: {
        country: any,
        region: any,
        commune: any,
        district: any,
        sector: any
    };

    /**
     * Test si la platforme est de type web
     */
    public isWeb: boolean;

    /**
     * Condition pour afficher les filtres
     */
    public showFiltre = false;

    showFilterField = false;

    /**
     * Action de l'utilisataion
     */
    @Input() public action: string;

    /**
     * Client selectionner
     */
    @Input() public clientSelectedList: any[] = [];

    /**
     * role de l'utilisateur
     */
    @Input() public role: number;

    /**
     * numéro des pages
     */
    public positions = [];

    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;

    /**
     * Position de la page courante
     */
    @Input() public pagination = true;

    /**
     * Annonces temporaires
     */
    @Input() public clientsTmp: Array<any>;

    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrClientsPerPage: any;

    /**
     * Le nombre de clients par pages
     */
    @Input() public source = 'modale';

    /**
     * Emet la position de la page actuel
     */
    @Output() public position = new EventEmitter<any>();
    /**
     * Emet la position de la page actuel
     */
    @Output() public validselection = new EventEmitter<any>();


    @ViewChild('content', {static: true}) public content: any;

    searchControl = new FormControl();
    searches = [];
    allSearches = [];
    filteredSearches: Observable<string[]>;
    separatorKeysCodes: number[] = [];
    keyword: any;
    filteredOptions: Observable<any[]>;

    filterType: any;
    placeHolder = 'Rechercher par prenom/nom';

    /**
     * Type de client
     */
    @Input() public customerType: number;

    /**
     * La limite pour récuperer les données
     */
    public limit = 14;

    /**
     * Données à ignorer
     */
    public skip = 0;

    public user: any;
    constructor(private userProvider: AfricleanHttpUserProvider,
                private session: AfricleanSessionProvider,
                private observableProvider: AfricleanObservablesProvider,
                private dateProvider: AfricleanDateProvider,
                private modal: AfricleanModalProvider,
                private config: AfricleanConfigProvider,
                private filterProvider: AfricleanFilterProvider) {
    }

    ngOnInit() {

        // Chargement des clients
        // tslint:disable-next-line:no-unused-expression
      //  (this.action === 'planning') ? this.getClients() : ' ';

        // Test la platforme
        this.isWeb = this.config.isWeb();

        // Initialise le filtres
        this.initializeFilters();

        // Initialise le choix
        this.choice = [];

        // Dés qu'il y'a un chamgement au niveau des clients notament une mise a jour,
        // on récharge les données.
        /* this.observableProvider.loadClient.subscribe(
               (load) => {
                   if (load) {
                       this.getClients();
                   }
               }
           );*/
        // Initialisation du role
        this.session.getItem('role').then(
            (role) => {
                this.role = role;
            }
        );

    }


    /**
     * Retourne tous les clients de l'entreprise
     */
    public async getClients() {
        const userId = await this.session.getItem('userId');
        const roleAuthor = await this.session.getItem('role');

        this.user = await this.session.getItem('user');
        const entity =  (this.user) ? this.user.entity : null;
        const id = (entity) ? entity.id : null;
        let body: any = {};
        body = {
            limit: this.limit,
            skip: this.skip,
            customerType: this.customerType,
            entity: id
        };
        this.userProvider.save('client/clients', body).subscribe(
            (clients) => {
                if (!this.clients && !this.clientsTmp) {
                    this.clients = [];
                    this.clientsTmp = [];
                    this.positions = [];
                }
                this.clients = this.clients.concat(clients.slice());
                this.clientsTmp = this.clientsTmp.concat(clients.slice());
            }
        );

      /*
        const url = 'client/';
        this.userProvider.getAll(url).subscribe(
            (clientsList) => {
                this.clients = clientsList;
            }
        );*/
        // this.getClientsByFilter();

    }

    /**
     * Crée une modal pour enregistrer un nouvel utilisateur
     */
    public async addClient() {
        const props = {
            source: 'clients-management',
            role: UserRole.CLIENT,
            userType: UserRole.CLIENT,
            customer: this.customerType,
            action: 'create'
        };
        await this.modal.create(AfricleanUserRegistrationComponent, 'my-custom-modal-css', props);
        const data: { displayRegistrationConfirmation: boolean } = await this.modal.getData();
        if (data && data.displayRegistrationConfirmation) {
            this.clients = null;
            this.observableProvider.loadClient.emit(true);
           // this.getClients();
        }
    }

    /**
     * Recupere le choix pour filtrer les données
     * @param value évenement émis
     * @param type type de filtre
     */
    public getFilters(value: any, type: string) {

        const choice = {
            filter1: value.filter1,
            filter2: value.filter2
        };

        if (type === 'adresse') {
            this.adressToSearch = value;
        } else {
            this.choice.push(choice);
        }
        this.filter(choice);
    }

    /**
     * Rafraichir la recherche
     */
    public refresh() {
        this.choice = [];
        this.adressToSearch = null;
        this.getClients();
    }

    /**
     * Filtre la liste des employés
     */
    private filter(choice: { filter1: string, filter2: string }) {
        // Represente le resultat a un instant t
        const currentClients: any[] = this.clients;
        this.clients = null;
        this.sort(choice, currentClients);
    }

    private sort(choice: { filter1: string, filter2: string }, currentClients: any[]) {

        if (choice.filter1 === 'Nom') {
            this.clients = currentClients;
            if (choice.filter2 === 'Croissant (A -> Z)') {
                this.clients.sort(this.filterProvider.compareByLastName);
            }
            if (choice.filter2 === 'Décroissant (Z -> A)') {
                this.clients.sort(this.filterProvider.compareByLastName).reverse();
            }

        }

        if (choice.filter1 === 'Status') {
            this.clients = [];
            currentClients.forEach(client => {
                if (client.isSubscribe === choice.filter2) {
                    this.clients.push(client);
                }
            });
        }

        if (choice.filter1 === 'Date d\'inscription') {
            this.clients = currentClients;
            if (choice.filter2 === 'Croissant') {
                this.clients.sort(this.filterProvider.compareByCreationDate);
            }
            if (choice.filter2 === 'Décroissant') {
                this.clients.sort(this.filterProvider.compareByCreationDate).reverse();
            }

        }
    }

    /**
     * Initialise l'objet filters
     */
    private initializeFilters() {
        this.filters = [];
        this.filters.push(
            {
                filter1: 'Nom',
                filter2: ['Croissant (A -> Z)', 'Décroissant (Z -> A)']
            },
            {
                filter1: 'Status',
                filter2: ['NOUVEAU', 'EN COURS', 'ABONNEE', 'RESILIEE']
            },
            {
                filter1: 'Adresse',
                filter2: []
            },
            {
                filter1: 'Date d\'inscription',
                filter2: ['Croissant', 'Décroissant']
            },
        );
    }

    public showFiltres() {
        if (this.showFiltre === true) {
            this.showFiltre = false;
        } else {
            this.showFiltre = true;
            this.filterType = null;
            this.showFilterField = false;
        }
        /*    if (this.showFilterField === true) {
                this.showFilterField = false;
                this.showFiltre = false;
                this.filterType = null;
            } else {
                this.showFilterField = true;
                this.showFiltre = true;
                this.filterType = 'name';
            }*/
    }

    public selectClients(client: any) {
        this.clientSelectedList.push(client);
        this.validselection.emit(this.clientSelectedList);
    }

    public async validSelection() {
        this.validselection.emit(this.clientSelectedList);
        //  await this.modal.dismiss(this.clientSelectedList);
    }

    sendPosition(position) {
        this.position.emit(position);
    }


    private _filter(name: any): any[] {
        const filterValue = name.toLowerCase();

        return (this.filterType === 'name') ? (this.clientsTmp.filter(client => (client.lastName.toLowerCase().indexOf(filterValue) === 0
            // tslint:disable-next-line:max-line-length
            || client.firstName.toLowerCase().indexOf(filterValue) === 0))) : ((this.filterType === 'phone') ? (this.clientsTmp.filter(client => (client.phoneNumber.toLowerCase().indexOf(filterValue) === 0))) : (this.clientsTmp.filter(client => (client.isSubscribe.toLowerCase().indexOf(filterValue) === 0))));
    }


    searchClient($event: KeyboardEvent) {
        if (this.keyword.trim() !== '') {
         //   this.clients = [];
            this.filteredOptions = this.searchControl.valueChanges
                .pipe(
                    startWith(''),
                    map(proche => proche ? this._filter(proche) : this.clients.slice()));
            this.filteredOptions.subscribe(
                (result) => {
                    this.sendPosition(1);
                    this.clients = result;
                    // this.clientsTmp = result;
                });
        } else {
            this.clients = this.clientsTmp;
        }

    }

    getFilterType(value) {
        this.filterType = value;
        this.showFilterField = true;
        this.showFiltre = false;
        // tslint:disable-next-line:max-line-length
        this.placeHolder = (value === 'status') ? 'Rechercher par status' : ((value === 'phone') ? 'Rechercher par Tétéphone' : 'Rechercher par Nom/Prénom');
    }

}
