import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import { AfricleanUserModel } from '../../../../models/africlean-user.model';
import { AfricleanHttpUserProvider } from '../../../../providers/africlean-http/africlean-http-user.provider';
import { AfricleanSessionProvider } from '../../../../providers/africlean-session/africlean-session.provider';
import { AfricleanObservablesProvider } from '../../../../providers/africlean-observables/africlean-observables.provider';
import { AfricleanDateProvider } from '../../../../providers/africlean-date/africlean-date.provider';
import { AfricleanModalProvider } from '../../../../providers/africlean-modal/africlean-modal.provider';
// tslint:disable-next-line:max-line-length
import { AfricleanUserRegistrationComponent } from '../../africlean-registration/africlean-user-registration/africlean-user-registration.component';
import { AfricleanConfigProvider } from '../../../../providers/africlean-config/africlean-config.provider';
import { AfricleanCountryModel } from '../../../../models/localisation/country.model';
import { AfricleanRegionModel } from '../../../../models/localisation/region.model';
import { AfricleanTownModel } from '../../../../models/localisation/town.model';
import { AfricleanDistrictModel } from '../../../../models/localisation/district.model';
import { AfricleanSectorModel } from '../../../../models/localisation/sector.model';
import { AfricleanFilterProvider } from '../../../../providers/africlean-filter/africlean-filter.provider';
import {map, startWith} from "rxjs/operators";
import {FormControl} from "@angular/forms";
import {Observable} from "rxjs";

@Component({
  selector: 'app-africlean-employe-manager',
  templateUrl: './africlean-employe-manager.component.html',
  styleUrls: ['./africlean-employe-manager.component.scss'],
})
export class AfricleanEmployeManagerComponent implements OnInit {
  /**
   * Liste des employes
   */
  @Input() public employes: AfricleanUserModel[] = [];

  /**
   * Liste des filtres
   */
  public filters: {
    filter1: string,
    filter2: string[]
  }[];

  /**
   * Choix pour filtrer
   */
  public choice: {
    filter1: string,
    filter2: string
  }[];

  /**
   * adresse à rechercher
   */
  public adressToSearch: {
    country: AfricleanCountryModel,
    region: AfricleanRegionModel,
    commune: AfricleanTownModel,
    district: AfricleanDistrictModel,
    sector: AfricleanSectorModel
  };


  /**
   * Test si la platforme est de type web
   */
  public isWeb: boolean;

  /**
   * Condition pour afficher les filtres
   */
  public showFiltre = false;


  /**
   * numéro des pages
   */
  public positions = [];

  /**
   * Position de la page courante
   */
  @Input() public currentPage = 1;

  /**
   * Position de la page courante
   */
  @Input() public pagination = true;

  /**
   * Annonces temporaires
   */
  @Input()public employesTmp: Array<any>;

  /**
   * Le nombre de clients par pages
   */
  @Input() public nbrEmployesPerPage: any;
  /**
   * Emet la position de la page actuel
   */
  @Output() public position = new EventEmitter<any>();
  searchControl = new FormControl();
  searches = [];
  allSearches = [];
  filteredSearches: Observable<string[]>;
  separatorKeysCodes: number[] = [];
  keyword: any;
  filteredOptions: Observable<any[]>;

  filterType: any;
  placeHolder = 'Rechercher par prenom/nom';
  showFilterField = false;

  constructor(private userProvider: AfricleanHttpUserProvider,
              private session: AfricleanSessionProvider,
              private observableProvider: AfricleanObservablesProvider,
              private dateProvider: AfricleanDateProvider,
              private modal: AfricleanModalProvider,
              private config: AfricleanConfigProvider,
              private filterProvider: AfricleanFilterProvider) { }

  ngOnInit() {
    // Chargement des employés
   // this.getEmployes();

    // Test la platforme
    this.isWeb = this.config.isWeb();

    // Initialise le filtres
    this.initializeFilters();

    // Initialise le choix
    this.choice = [];

    // Dés qu'il y'a un chamgement au niveau des employés notament une mise a jour,
    // on récharge les données.
    this.observableProvider.loadEmploye.subscribe(
        (load) => {
          if (load) {
            this.getEmployes();
          }
        }
    );
  }



  /**
   * Retourne tous les employer de l'entreprise
   */
  public async getEmployes() {
    const url = 'user/employe/';
    this.userProvider.getAll(url).subscribe(
        (employesList) => {
          this.employes = employesList;
        }
    );
  }

  /**
   * Crée une modal pour enregistrer un nouvel utilisateur
   */
  public async addEmploye() {
    const props = {
      source: 'employe-management',
      userType: 'employe',
      action: 'create'
    };
    await this.modal.create(AfricleanUserRegistrationComponent, 'my-custom-modal-css', props);
    const data: { displayRegistrationConfirmation: boolean } = await this.modal.getData();
    if (data.displayRegistrationConfirmation) {
      this.employes = null;
      this.getEmployes();
    }
  }


  /**
   * Recupere le choix pour filtrer les données
   * @param value évenement émis
   * @param type type de filtre
   */
  public getFilters(value: any, type: string) {

    const choice = {
      filter1: value.filter1,
      filter2: value.filter2
    };

    if (type === 'adresse') {
      this.adressToSearch = value;
    } else {
      this.choice.push(choice);
    }
    this.filter(choice);
  }

  /**
   * Rafraichir la recherche
   */
  public refresh() {
    this.choice = [];
    this.adressToSearch = null;
    this.getEmployes();
  }

  /**
   * Filtre la liste des employés
   */
  private filter(choice: {filter1: string, filter2: string}) {
    // Represente le resultat a un instant t
    const currentEmployes: AfricleanUserModel[] = this.employes;
    this.employes = null;
    this.sort(choice, currentEmployes);

  }

  private sort(choice: {filter1: string, filter2: string}, currentEmployes: AfricleanUserModel[]) {

    if (choice.filter1 === 'Nom') {
      this.employes = currentEmployes;
      if ( choice.filter2 === 'Croissant (A -> Z)') {
        this.employes.sort(this.filterProvider.compareByLastName);
      }
      if ( choice.filter2 === 'Décroissant (Z -> A)') {
        this.employes.sort(this.filterProvider.compareByLastName).reverse();
      }

    }

    if (choice.filter1 === 'Type d\'employer') {
      this.employes = [];
      currentEmployes.forEach(employe => {
        if (employe.typeOfUser === choice.filter2) {
          this.employes.push(employe);
        }
      });
    }

    if (choice.filter1 === 'Date d\'inscription') {
      this.employes = currentEmployes;
      if ( choice.filter2 === 'Croissant') {
        this.employes.sort(this.filterProvider.compareByCreationDate);
      }
      if ( choice.filter2 === 'Décroissant') {
        this.employes.sort(this.filterProvider.compareByCreationDate).reverse();
      }

    }
  }

  /**
   * Initialise l'objet filters
   */
  private initializeFilters() {
    this.filters = [];
    this.filters.push(
        {
          filter1: 'Nom',
          filter2: ['Croissant (A -> Z)', 'Décroissant (Z -> A)']
        },
        {
          filter1: 'Type d\'employer',
          filter2: ['MANAGER', 'INTERVENANT', 'COMMERCIAL']
        },
        {
          filter1: 'Adresse',
          filter2: []
        },
        {
          filter1: 'Date d\'inscription',
          filter2: ['Croissant', 'Décroissant']
        },
    );
  }

  public showFiltres() {
    if (this.showFiltre === true) {
      this.showFiltre = false;
    } else {
      this.showFiltre = true;
    }
  }

  sendPosition(position) {
    this.position.emit(position);
  }


  private _filter(name: any): any[] {
    const filterValue = name.toLowerCase();

    return (this.filterType === 'name') ? (this.employesTmp.filter(employe => (employe.lastName.toLowerCase().indexOf(filterValue) === 0
        // tslint:disable-next-line:max-line-length
        || employe.firstName.toLowerCase().indexOf(filterValue) === 0))) : ((this.filterType === 'phone') ? (this.employesTmp.filter(employe => (employe.phoneNumber.toLowerCase().indexOf(filterValue) === 0))) : (this.employesTmp.filter(employe => (employe.typeOfUser.toLowerCase().indexOf(filterValue) === 0))));
  }


  searchEmploye($event: KeyboardEvent) {
    if (this.keyword.trim() !== '') {
      this.filteredOptions = this.searchControl.valueChanges
          .pipe(
              startWith(''),
              map(proche => proche ? this._filter(proche) : this.employes.slice()));
      this.filteredOptions.subscribe(
          (result) => {
            this.sendPosition(1);
            this.employes = result;
            // this.clientsTmp = result;
          });
    } else {
      this.employes = this.employesTmp;
    }

  }

  getFilterType(value) {
    this.filterType = value;
    this.showFilterField = true;
    // tslint:disable-next-line:max-line-length
    this.placeHolder = (value === 'userType') ? 'Rechercher par Type d\'utilisateur' : 'Rechercher par Nom/Prénom';
  }

}
