import {Component, Input, OnInit} from '@angular/core';
import {AfricleanHttpItemListProvider} from '../../../../../providers/africlean-http/africlean-http-item-list.provider';
@Component({
  selector: 'app-manage-incident-type',
  templateUrl: './manage-incident-type.component.html',
  styleUrls: ['./manage-incident-type.component.scss'],
})
export class ManageIncidentTypeComponent implements OnInit {
  errorMsg: any;
  @Input() action: string;
  @Input() actionItem: string;
  showAddFormuler = true;
  title: string;
  @Input() public incidents = [];

  constructor(private itemListProvider: AfricleanHttpItemListProvider) { }

  ngOnInit() {
    this.init();
  }

  init() {
    this.itemListProvider.getAll('incident-type').subscribe(
        (result) => {
          if (result) {
            this.incidents = result;
            this.showAddFormuler = true;
            this.title = '';
          }
        }
    );
  }

  updateIncidentType() {

  }

  addIncidentType() {
    const data = {
      title: this.title,
    };
    const url = 'incident-type/save';
    if (this.title.trim() !== '') {
      this.itemListProvider.save(url, data).subscribe(
          (result) => {
            this.init();
          }
      );
    }
  }

  addList() {
    this.title = '';
    this.showAddFormuler = !this.showAddFormuler;
  }

  update(item: any) {
    console.log(item);
  }

  delete(item: any) {

  }

}
