import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AfricleanDateProvider} from 'src/providers/africlean-date/africlean-date.provider';
import {AfricleanModalProvider} from 'src/providers/africlean-modal/africlean-modal.provider';
import {AfricleanAdressListComponent} from 'src/app/components/africlean-adress-list/africlean-adress-list.component';
import {AfricleanHttpUserProvider} from 'src/providers/africlean-http/africlean-http-user.provider';
import {AfricleanSessionProvider} from '../../../../../providers/africlean-session/africlean-session.provider';
// tslint:disable-next-line:max-line-length
import {AfricleanUserRegistrationComponent} from '../../../africlean-registration/africlean-user-registration/africlean-user-registration.component';
import {AfricleanObservablesProvider} from '../../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanHttpInterventionProvider} from '../../../../../providers/africlean-http/africlean-http-intervention.provider';
import {AfricleanHttpSubscribeProvider} from '../../../../../providers/africlean-http/africlean-http-subscribe.provider';
import {InterventionType, StateAction, UserRole} from '../../../../../providers/utils/commonEnum';

@Component({
    selector: 'africlean-display-client',
    templateUrl: './africlean-display-client.component.html',
    styleUrls: ['./africlean-display-client.component.scss'],
})
export class AfricleanDisplayClientComponent implements OnInit {

    /**
     * Client
     */
    @Input() public client: any;

    /**
     * Evenement clientCode émis lors du click du bouton
     */
    @Output() clientSelected = new EventEmitter<any>();
    /**
     * Theme du client
     */
    @Input() public theme: string;

    /**
     * Test si la plateforme est de type web ou non
     */
    @Input() public isWeb: boolean;

    /**
     * Liste des adresses d'un utilisateur donné
     */
    public adresses: any[];
    /**
     * Action de l'utilisataion
     */
    @Input() public action: string;

    /**
     * role de l'utilisateur
     */
    @Input() public role: number;

    /**
     * role de l'utilisateur
     */
    @Input() interventionId: number;

    @Input() public stateIntervention: number;

    @Input() public interventionType: any;

    @Input() public intervention: any;

    /**
     * Action de modification selectionner
     */
    public actionSelected: number;
    /**
     * Liste possible des actions
     */
    @Input() public actionList: number[];

    /**
     * le code du client
     */
    @Input() public clientCode: number;


    constructor(private dateManager: AfricleanDateProvider,
                private modal: AfricleanModalProvider,
                private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private interventionProvider: AfricleanHttpInterventionProvider,
                private observableProvider: AfricleanObservablesProvider,
                private subscribeProvider: AfricleanHttpSubscribeProvider) {
    }

    ngOnInit() {

        if (this.client.dateOfBirth) {
            //     this.client.dateOfBirth = this.dateManager.format(this.client.dateOfBirth);
        }
        // this.client.dateOfCreation = this.dateManager.format(this.client.dateOfCreation);
        // this.dateManager.formatDateTime(this.client.dateOfCreation);
        this.init();
        // Dés qu'il y'a un chamgement au niveau d'une intervention chez un client notament une mise a jour,
        // on récharge les données.
        this.observableProvider.loadIntervention.subscribe(
            (loadInIntervention) => {
                if (loadInIntervention) {
                    this.getStateIntervention();
                }
            });
    }

    public init() {
        // Initialisation du role
        this.session.getItem('role').then(
            (role) => {
                this.role = role;
            }
        );
        this.actionList = [StateAction.CARRYOUT, StateAction.PLANIFIED, StateAction.CANCEL, StateAction.ABSENT];
        /* if (this.action === 'intervention') {
             this.stateIntervention = 0;
             this.getStateIntervention();
         }*/
    }

    public delete() {
        const url = 'client/delete/' + this.client.id;
        this.subscribeProvider.delateSubscribe(url).subscribe(
            (intervention) => {
            }
        );
    }

    /**
     * Modification d'un client
     */
    public async update() {
        const props = {
            source: 'client-management',
            userUpdate: this.client,
            action: 'update',
            userType: 'client'
        };
        await this.modal.create(AfricleanUserRegistrationComponent, 'my-custom-modal-css', props);
        const data: { displayRegistrationConfirmation: boolean } = await this.modal.getData();
        if (data && data.displayRegistrationConfirmation) {
            this.observableProvider.loadClient.emit(true);
        }
    }

    /**
     * Affiche la liste des adresses
     */
    public async showAdress() {
        await this.session.setItem('userTMP', this.client);
        this.getAdresses().subscribe(
            (adresses) => {
                this.adresses = adresses;
                const props = {
                    adresses: this.adresses,
                    user: this.client,
                    source: 'consult-client',
                };
                this.modal.create(AfricleanAdressListComponent, 'my-custom-modal-css', props);
            }
        );
    }

    public subscribeUser() {

    }

    /**
     * Renvoie toutes les adresses
     */
    public getAdresses() {
        const url = 'user/adress/' + this.client.id;
        const body = {
            role: UserRole.CLIENT
        };
        return this.userProvider.getAdress(url, body);
    }

    public selectClient(client) {
        this.clientSelected.emit(client);
    }

    public async getStateIntervention() {
        const url = 'intervention/user-intervention-state';
        let body = {};
        body = {
            clientCode: {userId: this.clientCode, role: UserRole.CLIENT},
            interventionCode: this.interventionId
        };
        this.interventionProvider.updateStatus(url, body).subscribe(
            (intervetion) => {
                this.stateIntervention = intervetion;
            });
    }

    public async getActionSelect(event: any) {
        this.stateIntervention = parseInt(event.target.value);
        const url = 'intervention/update-state';
        let body = {};
        body = {
            interventionCode: this.interventionId,
            stateIntervention: this.stateIntervention,
            userCode: {userId: this.clientCode, role: UserRole.CLIENT}
        };

        this.interventionProvider.save(url, body).subscribe(
            (intervenant) => {
                if (this.intervention && this.intervention.incident &&
                    this.intervention.interventionType === InterventionType.INCIDENT) {
                    this.updateState();
                } else {
                    this.observableProvider.loadIntervention.emit(true);
                    this.closeModal();
                }
            });
    }

    updateState() {
        let state = StateAction.TRAITED;
        if (this.stateIntervention === StateAction.CARRYOUT) {
            state = StateAction.TRAITED;
        } else if (this.stateIntervention === StateAction.PLANIFIED) {
            state = StateAction.PLANIFIED;
        } else {
            state = StateAction.INPROGRESS;
        }
        const body = {
            stateOfIncident: state
        };
        const url = 'incident/update-state/' + this.intervention.incident.id;
        this.interventionProvider.save(url, body).subscribe(
            (incident) => {
                this.observableProvider.loadIntervention.emit(true);
                this.closeModal();
            });
    }


    closeModal() {
        this.modal.dismiss(true);
    }
}
