import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AfricleanHttpLocalisationProvider } from 'src/providers/africlean-http/africlean-http-localisation.provider';
import { AfricleanHttpUserProvider } from 'src/providers/africlean-http/africlean-http-user.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanTranslateProvider } from 'src/providers/africlean-translate/africlean-translate.provider';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';

@Component({
  selector: 'africlean-adress',
  templateUrl: './africlean-adress.component.html',
  styleUrls: ['./africlean-adress.component.scss'],
})
export class AfricleanAdressComponent implements OnInit {

  /**
   * Liste des  pays
   */
  public countries: any[];

  /**
   * Liste des regions
   */
  public regions: any[];

  /**
   * Liste des communes
   */
  public communes: any[];

  /**
   * Liste des des quartiers
   */
  public districts: any[];

  /**
   * Liste des secteurs
   */
  public sectors: any[];

  /**
   * Id du secteur
   */
  private sectorId: number;

  /**
   * Permet de s'avoir si c'est un SAVE ou un UPDATE à faire
   */
  @Input() public action = 'save';

  /**
   * Source d'appel du component
   */
  @Input() public source = 'consult-informations';

  /**
   * id de l'utilisateur
   */
  @Input() public userId: number;


  /**
   * role de l'utilisateur
   */
  @Input() public role: number;

  /**
   * adresse à émetre
   */
 @Input() public adressToEmit: any;
    /**
     * adresse à émetre
     */
    @Input() public adressToPlanning: any;

  /**
   * adresse à rechercher
   */
  @Input() public adressToSearch: {
    country: any,
    region: any,
    commune: any,
    district: any,
    sector: any
  };
  /**
   * Message
   */
  public errorMsg: string;

  /**
   * Affiche le bouton ou non
   */
  @Input() public displayButton = false;

  constructor(private localisation: AfricleanHttpLocalisationProvider,
              private userProvider: AfricleanHttpUserProvider,
              private session: AfricleanSessionProvider,
              private translate: AfricleanTranslateProvider,
              private modal: AfricleanModalProvider) { }

  ngOnInit() {
    // Initialisation de l'adresse
    this.adressToEmit = {
      adresseId: 0,
      idHome: 0,
      country: '',
      region: '',
      commune: '',
      district: '',
      sector: '',
      home: ''
    };
      // Initialisation de l'adresse
    this.adressToPlanning = {
          countryId: '',
          regionId: '',
          communeId: '',
          districtId: '',
          sectorId: '',
      };
    this.getCountries();
  }

  /**
   * Enregistre une adresse et l'affiche
   */
  public async add() {

    // Verifier si l'adresse est bien valide
    if (this.control()) {

      this.errorMsg = null;

      // Recuperation de l'idUser
      let userId: number;
      let roleUser: number;
      if (this.source === 'consult-informations') {
        userId = await this.session.getItem('userId');
        roleUser = await this.session.getItem('role');

      } else {
        const  userTMP = await this.session.getItem('userTMP');
        userId = (this.userId) ? this.userId : userTMP.id;
        roleUser = (this.role) ? this.role : userTMP.typeOfUser;
      }

        // corps de la requete
      const body = {
            nameOfHome: this.adressToEmit.home,
            sectorCode: this.sectorId,
            role: roleUser
        };
      const url = 'home-user/save-adress/' + userId;

        // Enregistrer l'adresse
      this.userProvider.saveAdress(url, body).subscribe(
            (homeUser) => {

                // Recuperer l'id de l'adresse (HomeUser) et homeCode
                this.adressToEmit.adresseId = homeUser.id;
                this.adressToEmit.idHome = homeUser.homeCode;
            }
        );
      await this.modal.dismiss(this.adressToEmit);
    } else {
        // Afficher un message
        this.errorMsg = this.translate.getMessageInstant('INFO_BULLE.EMPTY_FIELD');
    }
  }

  /**
   * Modifie une adresse
   */
  public async update() {

    // Verifier si l'adresse est bien valide
    if (this.control()) {

      this.errorMsg = null;

      // corps de la requete
      const body = {
        nameOfHome: this.adressToEmit.home,
        sectorCode: this.sectorId
      };
      // transmet le corps de la méthode
      await this.modal.dismiss(body);
    } else {
      // Afficher un message
      this.errorMsg = this.translate.getMessageInstant('INFO_BULLE.EMPTY_FIELD');
    }

  }

  /**
   * Recherche une adresse
   */
  public async search() {
      await this.modal.dismiss(this.adressToSearch);
  }

    /**
     * Adress de prospection
     */
    public async planning() {
      const adress = {
            adresseId: this.sectorId,
            adressInfo: this.adressToEmit
      };
      await this.modal.dismiss(adress);
    }

    /**
     * Récupère le numéro de l'id secteur
     * @param event évenemt émis
     */
    public getSectorId(event: any) {

        // Recupère le nom du sector choisit
        const sectors = (event && event.target && event.target.value) ? this._filter(event.target.value.toString(), this.sectors) : [];
        if (sectors.length === 1) {
            this.adressToEmit.sector = sectors[0].nameOfSector;
            this.adressToSearch.sector = sectors[0];
        }
        /*   this.sectors.map(sector => {
               if (sector.id === event.target.value) {
                   this.adressToEmit.sector = sector.nameOfSector;
                   this.adressToSearch.sector = sector;
               }
           });*/

        this.sectorId = event.target.value;
    }

    /**
     * Renvoi tous les secteurs d'un quartier
     * @param event évenement émis
     */
    public getSectors(event: any) {

        // Pour reinitialiser les données
        this.sectors = null;
        this.adressToEmit.adresseId = '';
        this.adressToEmit.idHome = '';
        this.adressToEmit.sector = '';
        this.adressToEmit.home = '';

        this.adressToSearch.sector = null;

        // Recupère le nom de la commune choisit
        const districts = (event && event.target && event.target.value) ? this._filter(event.target.value.toString(), this.districts) : [];
        if (districts.length === 1) {
            this.adressToEmit.district = districts[0].nameOfDistrict;
            this.adressToSearch.district = districts[0];
        }
        /*   this.districts.map(district => {
               if (district.id === event.target.value) {
                   this.adressToEmit.district = district.nameOfDistrict;
                   this.adressToSearch.district = district;
               }
           });*/

        const url = 'quartier/secteurs/' + event.target.value;
        this.localisation.getSectorsByDistrictId(url).subscribe(
            (sectors) => {
                this.sectors = sectors;
            }
        );
    }

    /**
     * Renvoi tous les quartiers d'une commune
     * @param event évenement
     */
    public getDistricts(event: any) {

        // Pour reinitialiser les données
        this.districts = null;
        this.sectors = null;
        this.adressToEmit.adresseId = '';
        this.adressToEmit.idHome = '';
        this.adressToEmit.district = '';
        this.adressToEmit.sector = '';
        this.adressToEmit.home = '';

        this.adressToSearch.district = null;
        this.adressToSearch.sector = null;

        // Recupère le nom de la commune choisit
        const communes = (event && event.target && event.target.value) ? this._filter(event.target.value.toString(), this.communes) : [];
        if (communes.length === 1) {
            this.adressToEmit.commune = communes[0].nameOfTown;
            this.adressToSearch.commune = communes[0];
        }
        /*  this.communes.map(commune => {
              if (commune.id === event.target.value) {
                  this.adressToEmit.commune = commune.nameOfTown;
                  this.adressToSearch.commune = commune;
              }
          });*/

        const url = 'commune/quartiers/' + event.target.value;
        this.localisation.getDistrictsByCommuneId(url).subscribe(
            (districts) => {
                this.districts = districts;
            }
        );
    }

    /**
     * Renvoi toutes les communes d'une région
     * @param event évenemnt emis
     */
    public getCommunes(event: any) {

        // Pour reinitialiser les données
        this.communes = null;
        this.districts = null;
        this.sectors = null;
        this.adressToEmit.adresseId = '';
        this.adressToEmit.idHome = '';
        this.adressToEmit.commune = '';
        this.adressToEmit.district = '';
        this.adressToEmit.sector = '';
        this.adressToEmit.home = '';

        this.adressToSearch.commune = null;
        this.adressToSearch.district = null;
        this.adressToSearch.sector = null;

        // Recupère le nom de la region choisit
        const regions = (event && event.target && event.target.value) ? this._filter(event.target.value.toString(), this.regions) : [];
        if (regions.length === 1) {
            this.adressToEmit.region = regions[0].regionName;
            this.adressToSearch.region = regions[0];
        }
        /* this.regions.map(region => {
             if (region.id === event.target.value) {
                 this.adressToEmit.region = region.regionName;
                 this.adressToSearch.region = region;
             }
         });*/

        const url = 'region/communes/' + event.target.value;
        this.localisation.getCommunesByRegionId(url).subscribe(
            (communes) => {
                this.communes = communes;
            }
        );
    }

    /**
     * Recupere toutes les regions d'un pays
     * @param event évenement émis
     */
    public getRegions(event: any) {

        // Pour reinitialiser les données
        this.regions = null;
        this.communes = null;
        this.districts = null;
        this.sectors = null;
        this.adressToEmit = {
            adresseId: '',
            idHome: '',
            country: '',
            region: '',
            commune: '',
            district: '',
            sector: '',
            home: ''
        };

        this.adressToSearch = {
            country: null,
            region: null,
            commune: null,
            district: null,
            sector: null
        };

        const counries = (event && event.target && event.target.value) ? this._filter(event.target.value.toString(), this.countries) : [];
        // Recupère le nom du pays choisit
        if (counries.length === 1) {
            this.adressToEmit.country = counries[0].countryName;
            this.adressToSearch.country = counries[0];
        }
        /*this.countries.map(country => {
            if (country.id.toString === event.target.value.toString()) {
                this.adressToEmit.country = country.countryName;
                this.adressToSearch.country = country;
            }
        });*/

        // Recupères toutes les regions
        const url = 'country/regions/' + event.target.value;
        this.localisation.getRegionsByCountryId(url).subscribe(
            (regions) => {
                this.regions = regions;
            }
        );
    }

  /**
   * Récupère tous les pays
   */
  private getCountries() {
    this.localisation.getCountries('country').subscribe(
      (countries) => {
        this.countries = countries;
      }
    );
  }

  /**
   * Contrôle les champs de l'adrese
   */
  private control() {
      if (this.adressToEmit) {
        if (this.adressToEmit.country.trim() !== '' && this.adressToEmit.region.trim() !== '' &&
          this.adressToEmit.commune.trim() !== '' && this.adressToEmit.district.trim() !== '' &&
          this.adressToEmit.sector.trim() !== '' && this.adressToEmit.home.trim() !== '') {
            return true;
        }
      }
      return false;
  }

    /**
     * Ferme la modal
     */
    public closeModal() {
        this.modal.dismiss();
    }

    private _filter(value: string, list: any[]): any[] {
        const filterValue = value;
        return list.filter(option => option.id.toString().indexOf(filterValue) === 0);
    }
}
