import {Component, OnInit, EventEmitter, Input} from '@angular/core';
import {AfricleanHttpIncidentProvider} from '../../../providers/africlean-http/africlean-http-incident.provider';
import {AfricleanSessionProvider} from 'src/providers/africlean-session/africlean-session.provider';
import {AfricleanHttpUserProvider} from 'src/providers/africlean-http/africlean-http-user.provider';
import {AfricleanTranslateProvider} from 'src/providers/africlean-translate/africlean-translate.provider';
import {AfricleanAlertProvider} from 'src/providers/africlean-alert/africlean-alert.provider';
import {AfricleanConfigProvider} from 'src/providers/africlean-config/africlean-config.provider';
import {AfricleanObservablesProvider} from 'src/providers/africlean-observables/africlean-observables.provider';
import {map, mergeMap} from 'rxjs/operators';
import {AfricleanItemListModel} from '../../../models/services/africlean-item-list.model';
import {AfricleanHttpItemListProvider} from '../../../providers/africlean-http/africlean-http-item-list.provider';
import {AfricleanModalProvider} from '../../../providers/africlean-modal/africlean-modal.provider';
import {StateAdress, UserRole} from '../../../providers/utils/commonEnum';
import {from} from "rxjs";

@Component({
    selector: 'africlean-incident',
    templateUrl: './africlean-incident.component.html',
    styleUrls: ['./africlean-incident.component.scss'],
})
export class AfricleanIncidentComponent implements OnInit {

    @Input() public collapseCard = true;
    /**
     * Type d'incidents
     */
    public incidentsType = [];

    /**
     * Source d'appel du component
     */
    @Input() public source = 'report-incident';

    /**
     * Afficher le spinner
     */
    public hideSpinner = false;

    /**
     * id de l'utilisateur
     */
    @Input() public userId;


    /**
     * Télephone du client
     */
    public phone: string;

    /**
     * Numéro de téléphone de la personne
     */
    public phoneCode = '224';

    /**
     * Formulaire de recherche du client
     */
    @Input() public displayForm = false;
    /**
     * Client
     */
    public client: any;


    public incident: {
        clientCode: number,
        homeCode: number,
        typeOfAlert: string,
        phoneNumber: string,
        incidentDate: Date,
        message?: string
    };

    /**
     * Liste des incidents
     */
    public incidents: any[];

    /**
     * Liste des abonnemnts
     */
    public subscribes: any[];

    /**
     * Affiche un message
     */
    public errorMsg: string;

    /**
     * Type d'erreur
     */
    public errorType: string;

    /**
     * Permet de faire disparaitre le bouton d'envoie quand l'incident est envoyé
     */
    @Input() public hidden = false;

    /**
     * Controler le click du bouton valider
     */
    public hideValidateButton = false;

    /**
     * Permet de savoir c'est une plateforme web
     */
    public isWeb: boolean;

    /**
     * Role de l'utilisateur
     */
    private role: number;
    /**
     * Liste des item
     */
    public itemList: any [] = [];

    /**
     * Numero de téléphone de l'entreprise
     */
    public phoneNumber: string;

    currentIncidentType: any;
    currentSubscribe: any;
    currentHome: any;

    constructor(private incidentProvider: AfricleanHttpIncidentProvider,
                private itemListProvider: AfricleanHttpItemListProvider,
                private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private translate: AfricleanTranslateProvider,
                private alert: AfricleanAlertProvider,
                private config: AfricleanConfigProvider,
                private observableProvider: AfricleanObservablesProvider,
                private modal: AfricleanModalProvider) {
    }

    ngOnInit() {
        // Initialisation du role
        this.session.getItem('user').then(
            (user) => {
                this.role = user.typeOfUser;
                // Récupération des abonnements
                if (user.typeOfUser === UserRole.CLIENT) {
                    const p2 = user.phoneNumber.split(' ');
                    this.phoneCode = (p2 && p2.length > 0) ? p2[0].replace('+', '') : '224';
                    this.phone = (p2 && p2.length > 0) ? user.phoneNumber.replace(p2[0], '') : user.phoneNumber;
                    this.findIncident();
                }
            }
        );

    //    this.getAllIncidentsType();
        // Initialisation de l'incident
        this.init();


        // teste si c'est du web ou non
        this.isWeb = this.config.isWeb();

        this.phoneNumber = '+224 626853272';

    }

    /**
     * Initialisation de l'objet incident
     */
    private async init() {
        const userId = await this.session.getItem('userId');
        const user = await this.session.getItem('user');
        this.incident = {
            clientCode: userId,
            homeCode: 0,
            typeOfAlert: '',
            phoneNumber: user.phoneNumber,
            incidentDate: null,
            message: ''
        };
    }

    /**
     * Enregistrer un incident
     * @param incident corps de la requête
     */
    public async report() {
        if (this.controlIncidentFields()) {
            if (this.role === UserRole.CLIENT) {
                this.hidden = false;
                this.collapseCard = true;
            }
            this.hideValidateButton = true;
            const user = await this.session.getItem('user');
            const data = {
                client: this.client,
                home: this.currentHome,
                auteur: user,
                company: this.client.entity,
                incident_type: this.currentIncidentType,
                phoneNumber: this.incident.phoneNumber,
                incidentDate: this.incident.incidentDate,
                message: this.incident.message
            };
            return this.incidentProvider.save('incident/save', data).subscribe(
                (incident) => {
                    this.init();
                    this.hidden = false;
                    this.errorType = 'validate';
                    this.errorMsg = this.translate.getMessageInstant('COMPONENTS.AFRICLEAN_INCIDENT.MESSAGE.VALIDATE');
                    this.observableProvider.loadIncident.emit(true);
                    this.modal.dismiss({displayIncidentConfirmation: true});
                }
            );
        }
    }

    /**
     * Renvoi la liste de toutes les incidents
     */
    public getAllIncidents() {
        this.incidentProvider.getAll('incident').subscribe(
            (incident) => {
                this.incidents = incident;
            }
        );
    }

    /**
     * Renvoi la liste de toutes les incidentsType
     */
    public getAllIncidentsType() {
        const body = {
            typeOfItemList: 'INCIDENT TYPE'
        };
        this.itemListProvider.getItemListByTypeOfItem('itemList/itemlist-type', body).pipe(
            map(
                (itemList) => {
                    this.itemList = itemList;
                    this.getIncidentsTypeTransform(itemList);
                }
            ),
            mergeMap(
                (_) => {
                    return this.itemList;
                }
            )
        ).subscribe(
            (itemList) => {
            }
        );
    }

    /**
     * Recuperation de liste des incidentsType
     * @param itemList
     */
    public getIncidentsTypeTransform(itemList: AfricleanItemListModel[]) {
        const incidentsTypeTMP = [];
        itemList.forEach(item => {
            incidentsTypeTMP.push(item.itemListValue);
        });
        this.incidentsType = incidentsTypeTMP;
    }

    /**
     * Récupère le type d'incident
     * @param event évenement
     */
    public getIncidentType(event: any) {
       // this.incident.typeOfAlert = event.target.value;
        const id = event.target.value;
        const incident = this._filterById(id, this.incidentsType);
        if (incident && incident.length > 0) {
            this.currentIncidentType = incident[0];
        }
    }

    /**
     * Récupère l'abonnement (l'adresse)
     * @param event événement
     */
    public getSubscribe(event: any) {
        //  this.incident.homeCode = event.target.value;
        const id = event.target.value;
        const subscribes = this._filterByIdHome(id, this.subscribes);
        if (subscribes && subscribes.length > 0) {
            this.currentHome = subscribes[0].adresseId.home;
            this.currentSubscribe = subscribes[0].state;
        }
        console.log(this.currentSubscribe);
    }
    private _filterById(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.id.toString().indexOf(filterValue) === 0);
    }
    private _filterByIdHome(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.idHome.toString().indexOf(filterValue) === 0);
    }

    /**
     * Récupère la date de l'incident
     * @param event évenement émis
     */
    public getDate(event: any) {
        this.incident.incidentDate = event.target.value;
    }

    /**
     * Récupère les rémarques du client à propos de l'incident
     * @param event évenement émis
     */
    public getDetails(event: any) {
        this.incident.message = event.target.value;
    }

    /**
     * Renvoie toutes les adresses qui ont un abonnement
     */
    public async getAdresses() {
        let userId: string;
        this.role = await this.session.getItem('role');

        if (this.role === UserRole.CLIENT) {
            userId = await this.session.getItem('userId');
        } else {
            userId = this.userId;
        }
      //  const url = 'user/subscribe-adress/' + userId;
        const url = 'user/adress/' + userId;

        const body = {
            role: UserRole.CLIENT,
            type: StateAdress.SUBSCRIBE
        };
        this.userProvider.getAdress(url, body).subscribe(
            (adresses) => {
                console.log(adresses);
                this.subscribes = adresses;
              //  this.getValidateSubscribes(adresses);
            }
        );
    }

    /**
     * Recupère les adresses pour lesquelles les abonnements correpondant a VALIDER
     * @param adresses adresses
     */
    private getValidateSubscribes(adresses: any[]) {

        const abonnementsTMP = [];
        adresses.forEach(adresse => {
            if (adresse.state === 'VALIDER') {
                abonnementsTMP.push(adresse);
            }
        });
        this.subscribes = abonnementsTMP;
    }

    /**
     * Contrôle les champs de l'incident
     */
    private controlIncidentFields() {
        if (this.currentSubscribe && this.currentHome
            && this.currentIncidentType && this.incident.phoneNumber.trim() !== '' &&
            this.incident.incidentDate !== null) {
            return true;
        } else {
            this.errorType = 'error';
            this.errorMsg = this.translate.getMessageInstant('COMPONENTS.AFRICLEAN_INCIDENT.MESSAGE.ERROR');
            return false;
        }
    }

    /**
     * Retourn  un client en fonction du argument body
     * @param body corps de la requête
     */
    private getClient(body: {}) {
        return this.userProvider.getClient('client/login', body);
    }

    findIncident() {
        this.hideSpinner = false;
        const phone = '+' + this.phoneCode + ' ' + this.phone.replace(' ', '');

        let body = {};
        body = {
            phoneNumber: phone
        };
        return this.getClient(body).pipe(
            map(
                (user) => {
                    this.client = user;
                    this.userId = user.id;
                    if (user && user.entity && user.entity.incident_types) {
                        this.incidentsType = user.entity.incident_types;
                    }
                    console.log(user);
                }
            ),
            mergeMap(
                (_) => {
                    this.incident.phoneNumber = this.phone;
                    this.incident.clientCode = this.client.id;
                    return this.getAdresses();
                }
            )
        ).subscribe(
            (_) => {
            }
        );
    }

    private _filter(value: string, list: any[]): any[] {
        const filterValue = value;
        return list.filter(option => option.dialCode.indexOf(filterValue) === 0);
    }

    telInputObject(obj) {
        const list = obj.p;
        if (this.phone) {
            const country = this._filter(this.phoneCode.replace('+', ''), list);
            if (country.length > 0) {
                obj.setCountry(country[0].iso2);
                this.phoneCode = country[0].dialCode;
            }

        }

    }

    getNumber(event) {
    }

    onCountryChange(event: any) {
        const phone = this.phone;
        const p2 = phone.split(' ');
        this.phoneCode = event.dialCode;
    }

    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({subscription: false});
    }
}
