import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AfricleanAdressComponent } from '../africlean-adress/africlean-adress.component';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';
import { AfricleanCountryModel } from 'src/models/localisation/country.model';
import { AfricleanRegionModel } from 'src/models/localisation/region.model';
import { AfricleanTownModel } from 'src/models/localisation/town.model';
import { AfricleanDistrictModel } from 'src/models/localisation/district.model';
import { AfricleanSectorModel } from 'src/models/localisation/sector.model';

@Component({
  selector: 'africlean-filters',
  templateUrl: './africlean-filters.component.html',
  styleUrls: ['./africlean-filters.component.scss'],
})
export class AfricleanFiltersComponent implements OnInit {

  /**
   * Valeurs des select
   */
  @Input() public filters: {
    filter1: string,
    filter2: string[]
  }[];

  /**
   * Emet le choix de l'utilisateur
   */
  @Output() public emitChoice = new EventEmitter<{filter1: string, filter2: string}>();

  /**
   * Emet l'adresse choisit comme filtre
   */
  @Output() public emitAdressFilter = new EventEmitter<any>();

  /**
   * Signale si il faut rafraichir la recherche
   */
  @Output() public refreshEvent = new EventEmitter<boolean>();

  /**
   * Valeur du select 2 selectionnée
   */
  public filters2: string[];

  /**
   * Valeur selectionnée
   */
  private choice: {filter1: string, filter2: string};

  /**
   * adresse à rechercher
   */
  public adressToSearch: {
    country: AfricleanCountryModel,
    region: AfricleanRegionModel,
    commune: AfricleanTownModel,
    district: AfricleanDistrictModel,
    sector: AfricleanSectorModel
  };


  constructor(private modal: AfricleanModalProvider) { }

  ngOnInit() {
    // Initialisation du choix
    this.choice = {
      filter1: '',
      filter2: ''
    };
  }

  /**
   * Recupère le filtre choisit
   * @param event évenement emis
   * @param filtre filtre envoyé
   */
  public getFilter(event: any, filtre: string) {
    if (filtre === 'filter1') {
      this.choice.filter1 = event.target.value;
      this.filters2 = null;
      this.choice.filter1 === 'Adresse' ? this.getAdressFilter() : this.getFilter2(this.choice.filter1);
    }
    if (filtre === 'filter2') {
      this.choice.filter2 = event.target.value;
      this.emitChoice.emit(this.choice);
    }
  }

  /**
   * Modifie une adresse
   */
  public async getAdressFilter() {

    // On appel la modal pour rechercher une adresse
    const props = {
      displayButton: true,
      source: 'filter-component',
      action: 'search'
    };
    await this.modal.create(AfricleanAdressComponent, 'my-custom-modal-css', props);

    // On récupère les data
    const data = await this.modal.getData();

    // On stocke les données dans adressToSearch
    if (data) {
      this.adressToSearch = data;
      this.emitAdressFilter.emit(this.adressToSearch);
    }
  }

  /**
   * Rafraichir la recherche
   */
  public refresh() {
    this.refreshEvent.emit(true);
  }

  /**
   * Recupère les valeurs du filtre 2
   * @param filter1 filtre 1
   */
  private getFilter2(filter1: string) {
    let i;
    for (i = 0; i < this.filters.length; i++) {
      const filter = this.filters[i];
      if (filter.filter1 === filter1) {
        this.filters2 = filter.filter2;
        break;
      }
    }
  }

}
