import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AfricleanPayementComponent} from '../africlean-payement/africlean-payement.component';
import {AfricleanObservablesProvider} from '../../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanDateProvider} from '../../../../../providers/africlean-date/africlean-date.provider';
import {AfricleanModalProvider} from '../../../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanPayementModel} from '../../../../../models/finances/africlean-payement.model';
import {AfricleanHttpUserProvider} from '../../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanHttpSubscribeProvider} from '../../../../../providers/africlean-http/africlean-http-subscribe.provider';
import {AfricleanHttpPayementProvider} from '../../../../../providers/africlean-http/africlean-http-payement.provider';
import {AfricleanSessionProvider} from '../../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanUserModel} from '../../../../../models/africlean-user.model';


@Component({
    selector: 'app-africlean-pay-client',
    templateUrl: './africlean-pay-client.component.html',
    styleUrls: ['./africlean-pay-client.component.scss'],
})
export class AfricleanPayClientComponent implements OnInit {
    /**
     * Liste des payements
     */
    @Input() public payements: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];
    @Input() public payementsTMP: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];


    /**
     * numéro des pages
     */
    public positions = [];

    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;

    /**
     * Position de la page courante
     */
    @Input() public pagination = true;

    /**
     * Annonces temporaires
     */
    @Input() public clientsTmp: Array<any>;

    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrPaiementPerPage: any;

    /**
     * Emet la position de la page actuel
     */
    @Output() public position = new EventEmitter<any>();

    /**
     * Reactualise le paiement du client
     */
    @Output() public loadAbonnementPaiement = new EventEmitter<any>();

    @Input() isWeb: boolean;


    constructor(private observableProvider: AfricleanObservablesProvider,
                private dateProvider: AfricleanDateProvider,
                private userProvider: AfricleanHttpUserProvider,
                private subscribeProvider: AfricleanHttpSubscribeProvider,
                private payement: AfricleanHttpPayementProvider,
                private session: AfricleanSessionProvider,
                private modal: AfricleanModalProvider) {
    }

    ngOnInit() {
    }

    public async paySubscription() {
        const props = {
            source: 'consult-finance',
            typePayement: 'abonnement',
            displayForm: true
        };
        await this.modal.create(AfricleanPayementComponent, 'my-custom-modal-css', props);

        const data: { displayPayementConfirmation: boolean } = await this.modal.getData();
        if (data.displayPayementConfirmation) {
            this.payements = null;
            // this.getPayements();
            this.observableProvider.loadPayement.emit(data);
        }
    }


    /**
     * Liste de payement
     */
    public async getPayements() {
        let url: string;
        const role = await this.session.getItem('role');
        const userId = await this.session.getItem('userId');

        if (role === 'CLIENT') {
            url = 'payement/client/' + userId;
        } else {
            url = 'payement/';
        }
        this.payement.getAll(url).subscribe(
            (payementList) => {
                this.payements = payementList;
            }
        );
    }
    sendPosition(position) {
        this.position.emit(position);
    }
}
