import {Component, Input, OnInit} from '@angular/core';
import { AfricleanSessionProvider } from '../../../../../providers/africlean-session/africlean-session.provider';
import { AfricleanHttpUserProvider } from '../../../../../providers/africlean-http/africlean-http-user.provider';
import { AfricleanHttpSubscribeProvider } from '../../../../../providers/africlean-http/africlean-http-subscribe.provider';
import { AfricleanObservablesProvider } from '../../../../../providers/africlean-observables/africlean-observables.provider';
import { AfricleanModalProvider } from '../../../../../providers/africlean-modal/africlean-modal.provider';
import { map} from 'rxjs/operators';
import { AfricleanHttpInterventionProvider } from '../../../../../providers/africlean-http/africlean-http-intervention.provider';
// tslint:disable-next-line:max-line-length
import { UsersInterventionListManagementComponent } from '../users-intervention-list-management/users-intervention-list-management.component';
import { AfricleanAddInterventionComponent } from '../africlean-add-intervention/africlean-add-intervention.component';
import { AfricleanAdressListComponent } from 'src/app/components/africlean-adress-list/africlean-adress-list.component';
import {UserRole} from '../../../../../providers/utils/commonEnum';

@Component({
  selector: 'app-africlean-display-intervention',
  templateUrl: './africlean-display-intervention.component.html',
  styleUrls: ['./africlean-display-intervention.component.scss'],
})
export class AfricleanDisplayInterventionComponent implements OnInit {

  @Input() public intervention: any;

  @Input() public state: any;


  @Input() public theme: string;

  @Input() public interventionDate: any;

  public adress: any[] = [];

  /**
   * Source d'appel du component
   */
  @Input() public source = 'consult-service';

  /**
   * Role de l'utilisateur
   */
  @Input() public role: number;

  /**
   * Id de l'utilisateur
   */
  @Input() public userId: number;

  /**
   * action de l'utilisateur
   */
  @Input() public action: string;

  public  updated = false;

  /**
   * Intervention à modifier
   */
  private interventionUpdated: any;

  constructor(private session: AfricleanSessionProvider,
              private userProvider: AfricleanHttpUserProvider,
              private subscribeProvider: AfricleanHttpSubscribeProvider,
              private interventionProvider: AfricleanHttpInterventionProvider,
              private observableProvider: AfricleanObservablesProvider,
              private modal: AfricleanModalProvider) { }

  ngOnInit() {
    this.init();
   // this.customizeDate();
  }

  public async init() {
    this.userId = await this.session.getItem('userId');
    this.role = await this.session.getItem('role');
  }

  /**
   * Customise la date
   */
  private customizeDate() {
    const date = this.intervention.startDate.toString().split('T')[0];
    const splitDate = date.split('-');
    this.interventionDate = splitDate[2] + '-' + splitDate[1] + '-' + splitDate[0];
  }

  public async update() {
  //  this.getInterventionById();
    if (this.intervention.authorCode.authorId === this.userId ||
        this.role === UserRole.MANAGER || this.role === UserRole.MANAGER) {
      this.updated = true;
    }
    const props = {
      intervention: this.intervention,
      action: 'update',
      updated: this.updated
    };
    await this.modal.create(AfricleanAddInterventionComponent, 'my-custom-modal-css', props);
    const data: { displayInterventionConfirmation: boolean } = await this.modal.getData();
    if (data.displayInterventionConfirmation) {
      this.observableProvider.loadIntervention.emit(true);
    }
  }

  delete() {
      const url = 'intervention/delete-intervention/' + this.intervention.objectId;
      this.subscribeProvider.delateSubscribe(url).subscribe(
          (intervention) => {
          }
      );
  }

  /**
   * Affiche l'adresse de l'incident
   */
  public async showAdress(intervention: any) {
    if (intervention && intervention.adressCode && intervention.adressCode.adressType === 'INCIDENT') {
        let props = {};
        const url = 'user/subscribe-adress-byHome';
        const body = {
          homeId: intervention.adressCode.adressId
        };
        this.userProvider.getAdress(url, body).pipe(
            map(
                (adresses) => {
                  this.adress = adresses;
                  props = {
                    adresses: this.adress,
                    source: 'consult-incident',
                    action: 'payement'
                  };
                  this.modal.create(AfricleanAdressListComponent, 'my-custom-modal-css', props);
                }
            )
        ).subscribe(
            (adresses) => {
            });
    }

    if (intervention && intervention.adressCode && intervention.adressCode.adressType === 'INTERVENTION') {
        this.role = await this.session.getItem('role');
        this.userId = await this.session.getItem('userId');
        if (this.role === UserRole.CLIENT) {
            let props = {};
            const url = 'user/client-adress-bySector';
            const body = {
                clientId: this.userId,
                sectorId: intervention.adressCode.adressId
            };
            this.userProvider.getAdress(url, body).pipe(
                map(
                    (adresses) => {
                        this.adress = adresses;
                        props = {
                            adresses: this.adress,
                            source: 'consult-incident',
                            action: 'payement'
                        };
                        this.modal.create(AfricleanAdressListComponent, 'my-custom-modal-css', props);
                    }
                )
            ).subscribe(
                (adresses) => {
                });

        } else {
            let props = {};
            const url = 'user/subscribe-adress-bySector';
            const body = {
                sectorId: intervention.adressCode.adressId
            };
            this.userProvider.getAdress(url, body).pipe(
                map(
                    (adresses) => {
                        this.adress = adresses;
                        props = {
                            adresses: this.adress,
                            source: 'consult-incident',
                            action: 'payement'
                        };
                        this.modal.create(AfricleanAdressListComponent, 'my-custom-modal-css', props);
                    }
                )
            ).subscribe(
                (adresses) => {
                });
        }
    }

  }


  /**
   * Recupères les adresses pour lesquelles les abonnements correpondant a STATE
   * @param adresses adresses
   */
  private getAdresseOfIncident(adresses: any[]) {
    const adressTMP = [];
    adresses.forEach(adresse => {
    });
    this.adress = adressTMP;
  }

  public async updateUserIntervention() {
      if (this.intervention && this.intervention.userInterventions &&
          this.intervention.userInterventions.length > 0) {
          const users = this._filterByUserType('5', this.intervention.userInterventions);
          const employes = this._filterByEmploye('5', this.intervention.userInterventions);
          const props = {
              theme: 'consult-intervention',
              action: 'intervention',
              intervention: this.intervention,
              clients: users,
              employes
          };
          await this.modal.create(UsersInterventionListManagementComponent, 'my-custom-modal-css', props);
          const data: { displayIncidentConfirmation: boolean } = await this.modal.getData();
      }

    }

    private _filterByUserType(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.user.typeOfUser.toString().indexOf(filterValue) === 0);
    }
    private _filterByEmploye(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.user.typeOfUser.toString().indexOf(filterValue) !== 0);
    }
    /**
     * Renvoie toutes les adresses
     */
    public getAdresses(id) {
        const url = 'user/adress/' + id;
        const body = {
            role: UserRole.CLIENT
        };
        return this.userProvider.getAdress(url, body);
    }

}
