    private async init() {
        const userId = await this.session.getItem('userId');
        const role1 = await this.session.getItem('role');
        this.autheur = await this.session.getItem('user');
        this.currentEntity = (this.autheur) ? this.autheur.entity : null;
        this.entityId = (this.autheur) ? this.currentEntity.id : null;
        this.payementSubscribe = {
            balanceOfAbonnement: null,
            dateToSubscribePayment: null,
            amountToPay: null,
            meanOfPayment: '',
            compteNumber: '',
            paymentCode: '',
            subscribeCode: null,
            sateOfPayment: 'EN COURS',
            homeCode: null,
            clientCode: null,
            payementCreatedAuthorId: {authorId: userId, role: role1}
        };
        this.payementEmploye = {
            balanceOfAbonnement: null,
            dateToSubscribePayment: null,
            amountToPay: null,
            meanOfPayment: '',
            compteNumber: '',
            paymentCode: '',
            employeCode: {authorId: '', role: ''},
            payementCreatedAuthorId: {authorId: userId, role: role1}
        };

        if (role1 === 5) {
            // this.phone = user.phoneNumber;
            this.source = 'consult-abonnement';
            const p2 = this.autheur.phoneNumber.split(' ');
            this.phoneCode = (p2 && p2.length > 0) ? p2[0].replace('+', '') : '224';
            this.phone = (p2 && p2.length > 0) ? this.autheur.phoneNumber.replace(p2[0], '') : this.autheur.phoneNumber;

            this.findClient();
        } else {
            // this.getEmployes();
        }
        this.hideSpinner = false;
    }

