import {Component, Input, OnInit} from '@angular/core';
import { AfricleanDateProvider } from '../../../../../providers/africlean-date/africlean-date.provider';
import { AfricleanModalProvider } from '../../../../../providers/africlean-modal/africlean-modal.provider';
import { AfricleanHttpUserProvider } from '../../../../../providers/africlean-http/africlean-http-user.provider';
import { AfricleanAdressListComponent } from '../../../africlean-adress-list/africlean-adress-list.component';
import { AfricleanSessionProvider } from '../../../../../providers/africlean-session/africlean-session.provider';
// tslint:disable-next-line:max-line-length
import { AfricleanUserRegistrationComponent } from '../../../africlean-registration/africlean-user-registration/africlean-user-registration.component';
import {AfricleanObservablesProvider} from '../../../../../providers/africlean-observables/africlean-observables.provider';
import { AfricleanHttpSubscribeProvider } from 'src/providers/africlean-http/africlean-http-subscribe.provider';

@Component({
  selector: 'app-africlean-display-employe',
  templateUrl: './africlean-display-employe.component.html',
  styleUrls: ['./africlean-display-employe.component.scss'],
})
export class AfricleanDisplayEmployeComponent implements OnInit {
  /**
   * Client
   */
  @Input() public employe: any;

  /**
   * Client
   */
  @Input() public action = 'employe';

  /**
   * Theme du client
   */
  @Input() public theme: string;

  /**
   * Test si la plateforme est de type web ou non
   */
  @Input() public isWeb: boolean;

  /**
   * Liste des adresses d'un utilisateur donné
   */
  public adresses: any[];

  constructor(private dateManager: AfricleanDateProvider,
              private modal: AfricleanModalProvider,
              private session: AfricleanSessionProvider,
              private userProvider: AfricleanHttpUserProvider,
              private observableProvider: AfricleanObservablesProvider,
              private subscribeProvider: AfricleanHttpSubscribeProvider) { }

  ngOnInit() {
   // this.employe.dateOfCreation = this.dateManager.format(this.employe.dateOfCreation);
   // this.employe.dateOfBirth = this.dateManager.format(this.employe.dateOfBirth);
  }

  public delete() {
      const url = 'user/employe/intervenant/delete/' + this.employe.objectId;
      this.subscribeProvider.delateSubscribe(url).subscribe(
        (intervention) => {
          const url2 = 'login/delete/' + this.employe.objectId;
          this.subscribeProvider.delateSubscribe(url).subscribe(
              // tslint:disable-next-line:no-shadowed-variable
          (intervention) => {
          }
        ); }
      );
  }
  /**
   * Modification d'un emplyé
   */
  public async update() {
    const props = {
      source: 'employe-management',
      userUpdate: this.employe,
      action: 'update',
      userType: 'employe'
    };
    await this.modal.create(AfricleanUserRegistrationComponent, 'my-custom-modal-css', props);
    const data: { displayRegistrationConfirmation: boolean } = await this.modal.getData();
    if (data && data.displayRegistrationConfirmation) {
      this.observableProvider.loadEmploye.emit(true);
    }
  }

  /**
   * Affiche la liste des adresses
   */
  public async showAdress() {
    await this.session.setItem('userTMP', this.employe);
    this.getAdresses().subscribe(
        (adresses) => {
          this.adresses = adresses;
          const props = {
            adresses: this.adresses,
            user: this.employe,
            source: 'consult-employe'
          };
          this.modal.create(AfricleanAdressListComponent, 'my-custom-modal-css', props);
        }
    );
  }

  public subscribeUser() {

  }

  /**
   * Renvoie toutes les adresses
   */
  public getAdresses() {
    const url = 'user/adress/' + this.employe.objectId;
    const body = {
      role: this.employe.typeOfUser
    };
    return this.userProvider.getAdress(url, body);
  }
}
