import { Pipe, PipeTransform } from '@angular/core';
import {StateAction} from '../../providers/utils/commonEnum';

@Pipe({
  name: 'stateAction'
})
export class StateActionPipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    switch (value) {
      case StateAction.NEW:
        return 'NOUVEAU';
      case StateAction.PLANIFIED:
        return 'PLANIFIEE';
      case StateAction.CARRYOUT:
        return 'effectué';
      case StateAction.CANCEL:
        return 'annulée';
      case StateAction.ABSENT:
        return 'absent';
      case StateAction.CLOTURED:
        return 'cloturé';
      case StateAction.INPROGRESS:
        return 'EN COURS';
      case StateAction.TRAITED:
        return 'traité';
      default:
        return 'Autre';
    }
  }

}
