import { AfricleanHttpProvider } from './africlean-http.provider';
import { Observable } from 'rxjs';
// import { AfricleanInterventionModel } from '../../models/intervention/africlean-Intervention.model';
import { Injectable } from '@angular/core';
// import { AfricleanUserModel } from '../../models/africlean-user.model';
import { AfricleanInterventionModel } from 'src/models/intervention/africlean-intervention.model';
import { AfricleanUserModel } from 'src/models/africlean-user.model';

@Injectable()
export class AfricleanHttpInterventionProvider  extends AfricleanHttpProvider {

    /**
     * Récupère tous les Interventions d'un utilisateur
     * @param url url
     * @param options options
     */
    public getInterventionByIdUser(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }


    /**
     * Récupère tous les Interventions
     * @param url url
     * @param options options
     */
    public getAll(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Récupère une intervention à partir de son id
     * @param url url
     * @param options options
     */
    public getInterventionById(url: string, options?: {}): Observable<any> {
        return this.get(url, options);
    }

    /**
     * Récupère une description de tous les Interventions
     * @param url url
     * @param options options
     */
    public getAllInterventionsWithDescription(url: string, options?: {}): Observable< {
        client: AfricleanUserModel, Intervention: AfricleanInterventionModel}[]> {
        return this.get(url, options);
    }

    /**
     * Enrégistre un Intervention
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<any[]> {
        return this.post(url, body, options);
    }

    /**
     * return la liste des utilisateurs de l'intervention
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public getUserIntervention(url: string, body: {}, options?: {}): Observable<any[]> {
        return this.post(url, body, options);
    }


  public  updateStatus(url: string, body: {}, options?: {}): Observable<any> {
      return this.post(url, body, options);
  }
}
