import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AfricleanHttpProvider } from './africlean-http.provider';

@Injectable()
export class AfricleanHttpItemListProvider extends AfricleanHttpProvider {

    /**
     * Récupère un  à partir de son identifiant
     * @param url url
     * @param options options
     */
    public getItemListById(url: string, options?: {}): Observable<any> {
        return this.get(url, options);
    }

    /**
     * Enrégistre d'une ItemList
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }
    /**
     * Récupère toutes les ItemList
     * @param url url
     * @param options options
     */
    public getAll(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Retourn la liste de ItemList inscrit pâr un utilisateur
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public getItemListByTypeOfItem(url: string, body: {}, options?: {}): Observable<any[]> {
        return this.post(url, body, options);
    }


    /**
     * Suppression d'une ItemList
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public delate(url: string, options?: {}): Observable<any> {
        return this.delete(url);
    }

    putItem(url: string, body: {}, options?: {}): Observable<any> {
        return this.update(url, body, options);
    }
}
