import { Pipe, PipeTransform } from '@angular/core';
import {InterventionType} from '../../providers/utils/commonEnum';

@Pipe({
  name: 'interventionType'
})
export class InterventionTypePipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    switch (value) {
      case InterventionType.INCIDENT:
        return 'INCIDENT';
      case InterventionType.INTERVENTION:
        return 'INTERVENTION';
      case InterventionType.PRO:
        return 'PROFESIONNELLE';
      case InterventionType.PARTICULIER:
        return 'PARTICULIEE';
      case InterventionType.PERSONNALISER:
        return 'PERSONNALISEE';
      case InterventionType.PROSPECTION:
        return 'PROSPECTION';
      default:
        return 'Autre';
    }
  }

}
