import { Pipe, PipeTransform } from '@angular/core';
import {ClientType} from '../../providers/utils/commonEnum';
@Pipe({
  name: 'clientType'
})
export class ClientTypePipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    switch (value) {
      case ClientType.PRO:
        return 'Professionnel';
      case ClientType.PARTICULIER:
        return 'Particulier';
      default:
        return 'Autre';
    }
  }

}
