import {Component, Input, OnInit} from '@angular/core';
import {AfricleanSessionProvider} from '../../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanHttpUserProvider} from '../../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanHttpSubscribeProvider} from '../../../../../providers/africlean-http/africlean-http-subscribe.provider';
import {AfricleanModalProvider} from '../../../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanObservablesProvider} from '../../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanAbonnementListComponent} from '../../../africlean-abonnement-list/africlean-abonnement-list.component';
import {AfricleanPayementListComponent} from '../../../africlean-payement-list/africlean-payement-list.component';

@Component({
    selector: 'app-africlean-display-abonnement',
    templateUrl: './africlean-display-abonnement.component.html',
    styleUrls: ['./africlean-display-abonnement.component.scss'],
})
export class AfricleanDisplayAbonnementComponent implements OnInit {
    /**
     * Liste des abonnements
     */
    @Input() public subscribes: any[];
    /**
     * Cacher le spinner ou non
     */
    public hideSpinner = false;

    @Input() public subscribesDescription: any;

    @Input() public theme: string;

    @Input() public subscribeDate: string;

    @Input() public isWeb: boolean;

    /**
     * role de l'utilisateur
     */
    @Input() public role: number;

    /**
     * Type de client
     */
    @Input() public customerType: number;

    constructor(private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private subscribeProvider: AfricleanHttpSubscribeProvider,
                private modal: AfricleanModalProvider,
                private observableProvider: AfricleanObservablesProvider) {
    }

    ngOnInit() {
        this.customizeDate();
        // Initialisation du role
        this.session.getItem('role').then(
            (role) => {
                this.role = role;
            }
        );
    }

    /**
     * Customise la date
     */
    private customizeDate() {
        const date = this.subscribesDescription.dateOfSbscription.toString().split('T')[0];
        const splitDate = date.split('-');
        this.subscribeDate = splitDate[2] + '-' + splitDate[1] + '-' + splitDate[0];
    }

    public async update() {
        const subscribe = this.subscribesDescription;
        const props = {
            state: subscribe.stateOfSubscription,
            source: 'consult-subscribe-management',
            userId: this.subscribesDescription.user.id,
            currentSubscribeIdHome: subscribe.homeCode,
            subscribe,
        };
        await this.modal.create(AfricleanAbonnementListComponent, 'my-custom-modal-css', props);
        const data: { displaySubscribeConfirmation: boolean } = await this.modal.getData();
        if (data.displaySubscribeConfirmation) {
            this.getAbonnements();
        }
    }


    /**
     * Retourne tous les abonnements
     */
    public async getAbonnements() {
        const url = 'abonnement/';
        this.subscribeProvider.getAllSubscribeDescription(url).subscribe(
            (abonnementsList) => {
                this.subscribes = abonnementsList;
            }
        );
    }

    public delete() {
        const url = 'abonnement/delete/' + this.subscribesDescription.id;
        this.subscribeProvider.delateSubscribe(url).subscribe(
            (abonnement) => {
                console.log(abonnement);
            }
        );

        // const body = {
        //     homeCode: this.subscribesDescription.subscribe.homeCode,
        //     clientCode: this.subscribesDescription.subscribe.clientCode
        // };
        // const url = 'abonnement/cancel';
        // this.subscribeProvider.save(url, body).subscribe(
        //     (abonnement) => {
        //     }
        // );
      /*  const body = {
            homeCode: this.subscribesDescription.subscribe.homeCode,
            clientCode: this.subscribesDescription.subscribe.clientCode
        };
        const url = 'abonnement/cancel';
        this.subscribeProvider.save(url, body).subscribe(
            (abonnement) => {
            }
        );*/
    }

    public async showPayementList() {
        const props = {
            source: 'consult-subscribe-management',
            typePayement: 'abonnement',
            displayForm: true,
            role: 'CLIENT',
            theme: 'abonnement',
            subscribeCode: this.subscribesDescription.id,
            userId: this.subscribesDescription.user.id
        };
        await this.modal.create(AfricleanPayementListComponent, 'my-custom-modal-css', props);
    }
}
