import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AfricleanHttpProvider } from './africlean-http.provider';

@Injectable()
export class AfricleanHttpUserProvider extends AfricleanHttpProvider {

    /**
     * Récupère un utilisateur par son identifiant
     * @param url url
     * @param options options
     */
    public getUserById(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Récupère tous les utilisateurs
     * @param url url
     * @param options options
     */
    public getAll(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Enrégistre un user donnée
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<any[]> {
        return this.post(url, body, options);
    }

    /**
     * Loger une personne
     * @param url url
     * @param log login
     * @param pass mot de passe
     */
    public login(url: string, log: string, pass: string): Observable<any[]> {
        const body = {
            login: log,
            password: pass
        };
        return this.post(url, body);
    }

    /**
     * Récupération de l'objet User
     * @param url url
     * @param logId id
     */
    public loged(url: string, logId: string): Observable<any> {
        return this.get(url + logId);
    }

    /**
     * Enregistre une adresse
     * @param url url
     * @param body corps de la requête
     * @param options options
     */
    public saveAdress(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }

    /**
     * Enregistre une historique
     * @param url url
     * @param body corps de la requête
     * @param options options
     */
    public saveItem(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }

    /**
     * Met a jour les informations
     * @param url url
     * @param body corps de la requête
     * @param options options
     */
    public updateInfos(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }

    /**
     * Met a jour le mot de passe
     * @param url url
     * @param body corps de la requête
     * @param options options
     */
    public updatePassword(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }

    /**
     * Met a jour une adresse
     * @param url url
     * @param body corps de la requête
     * @param options options
     */
    public updateAdress(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }

    /**
     * Renvoie la liste d'adresse d'un user (adress)
     * Renvoie la liste d'adresses qui n'ont pas d'abonnement d'un user (free-adress)
     * Renvoie la liste d'adresses qui ont un abonnement d'un user (subscribe-adress)
     * @param url url
     * @param body corps de la requête
     * @param options options
     */
    public getAdress(url: string, body: {}, options?: {}): Observable<any[]> {
        return this.post(url, body, options);
    }

    /**
     * Supprime une adresse
     * @param url url
     * @param body corps de la requête
     * @param options options
     */
    public deleteAdress(url: string,  body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }

    /**
     * Return un client
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public getClient(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }


    /**
     * Retourn la liste de client inscrit pâr un utilisateur
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public getClientByUserAuthor(url: string, body: {}, options?: {}): Observable<any[]> {
        return this.post(url, body, options);
    }
}
