import { Pipe, PipeTransform } from '@angular/core';
import {StatePaiement} from '../../providers/utils/commonEnum';

@Pipe({
  name: 'statePaiement'
})
export class StatePaiementPipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    switch (value) {
      case StatePaiement.NEW:
        return 'NOUVEAU';
      case StatePaiement.IN_PROGRESS_PAIEMENT:
        return 'EN COURS';
      case StatePaiement.PAID:
        return 'Validé';
      case StatePaiement.CANCEL:
        return 'Annulé';
      case StatePaiement.SUSPENDED:
        return 'SUSPENDU';
      default:
        return 'Autre';
    }
  }

}
