import {Component, OnInit, Input, Inject, LOCALE_ID} from '@angular/core';
import {AlertController, NavController} from '@ionic/angular';
import { AfricleanDateProvider } from '../../../../providers/africlean-date/africlean-date.provider';
import { AfricleanSessionProvider } from '../../../../providers/africlean-session/africlean-session.provider';
import { AfricleanHttpUserProvider } from '../../../../providers/africlean-http/africlean-http-user.provider';
import { AfricleanHttpPlanningProvider } from '../../../../providers/africlean-http/africlean-http-planning.provider';
import { AfricleanObservablesProvider } from '../../../../providers/africlean-observables/africlean-observables.provider';
import { AfricleanModalProvider } from '../../../../providers/africlean-modal/africlean-modal.provider';
import { AfricleanAlertProvider } from '../../../../providers/africlean-alert/africlean-alert.provider';
import {UserRole} from "../../../../providers/utils/commonEnum";


@Component({
  selector: 'manage-calendar',
  templateUrl: './manage-calendar.component.html',
  styleUrls: ['./manage-calendar.component.scss'],
})
export class ManageCalendarComponent implements OnInit {

  /**
   * Début de l'événement
   */
  @Input() public start: Date;

  /**
   * Fin de l'événement
   */
  @Input() public end: Date;

  /**
   * Titre de l'événement
   */
  @Input() public title: string;

  /**
   * Description de l'événement
   */
  @Input() public description: string;

  public  collapseCard: boolean;


  /**
   * code de planning à modifier
   */
  @Input() public planningCode: number;


  /**
   * Verifie si l'utilisateur peut modifier un événement
   */
  @Input() public  updated: boolean;

  /**
   * Liste des utilisateurs inclue dans le planning
   */
  public users: any[] = [];

  /**
   * Liste des employés du planning
   */
 public employes: any[] = [];

  /**
   * Liste des employés du planning
   */
  public clients: any[] = [];

  constructor(private alertCtrl: AlertController,
              @Inject(LOCALE_ID) private locale: string,
              private dateManager: AfricleanDateProvider,
              private session: AfricleanSessionProvider,
              private userProvider: AfricleanHttpUserProvider,
              private observableProvider: AfricleanObservablesProvider,
              private modal: AfricleanModalProvider,
              private alert: AfricleanAlertProvider,
              private navCtrl: NavController,
              private planningProvider: AfricleanHttpPlanningProvider) { }

  ngOnInit() {
    // Initialisation
    this.getAllPlanning();
  }

  /**
   * Modifier d'une planning
   */
  public async update() {
    const role = await this.session.getItem('role');
    const path = 'planning/update';
    const body = {
      planningCode: this.planningCode,
      title: this.title,
      message: this.description,
      startDate: new Date(this.start),
      endDate: new Date(this.end),
    };
    this.planningProvider.save(path, body).subscribe(
        (planning) => {
          this.modal.dismiss( this.observableProvider.loadPlanning.emit(true));
        }
    );

  }

  /**
   * Suppression d'une planning
   */
  public async deletePlanning() {
    const role = await this.session.getItem('role');
    const path = 'planning/delete-planning';
    const body = {
      planningCode: this.planningCode
    };
    this.planningProvider.save(path, body).subscribe(
        (planning) => {
          this.observableProvider.loadPlanning.emit(true);
        }
    );
  }

  /**
   * Message de warning avant la supression d'une planing
   */
  public delete() {
    const message = 'Attention vous allez supprimer définitivement cette planning.';
    const css = '';
    const buttons = [

      {
        text: 'Supprimer',
        handler: () => {
          this.deletePlanning();
        }
      },
      {
        text: 'Annuler',
        handler: () => {
        }
      }
    ];
    this.alert.alertMessage(message, css, buttons);
  }



  /**
   * Retourn la liste de tous les planning
   */
  public async getAllPlanning() {
      const url = 'planning/users';
      const body = {
        planningCode: this.planningCode
      };
      this.planningProvider.getUserPlanning(url, body).subscribe(
          (usersList) => {
            this.users = usersList;
            this.users.forEach(user => {
              if (user.typeOfUser === UserRole.CLIENT) {
                this.clients.push(user);
              } else {
                this.employes.push(user);
              }
            });
          });
  }

    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({subscription: false});
    }
}
