import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AfricleanHttpProvider } from './africlean-http.provider';
import { AfricleanUserModel } from '../../models/africlean-user.model';
import { AfricleanProspectionModel } from '../../models/services/africlean-prospection.model';

@Injectable()
export class AfricleanHttpProspectionProvider extends AfricleanHttpProvider {

    /**
     * Récupère un commentaire à partir de son identifiant
     * @param url url
     * @param options options
     */
    public getProspectById(url: string, options?: {}): Observable<AfricleanProspectionModel> {
        return this.get(url, options);
    }

    /**
     * Enrégistre d'une Prospection
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<AfricleanProspectionModel> {
        return this.post(url, body, options);
    }
    /**
     * Récupère toutes les prospections
     * @param url url
     * @param options options
     */
    public getAll(url: string, options?: {}): Observable<AfricleanProspectionModel[]> {
        return this.get(url, options);
    }

    /**
     * Récupère la liste des auteurs
     * @param url url
     * @param options options
     */
    public getAllAuthor(url: string, options?: {}): Observable<AfricleanUserModel[]> {
        return this.get(url, options);
    }
    /**
     * Retourn la liste de prospections inscrit pâr un utilisateur
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public getProspectByUserAuthor(url: string, body: {}, options?: {}): Observable<any[]> {
        return this.post(url, body, options);
    }
}
