import {Component, OnInit, Input} from '@angular/core';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanHttpUserProvider} from '../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanHttpSubscribeProvider} from '../../../../providers/africlean-http/africlean-http-subscribe.provider';
import {AfricleanModalProvider} from '../../../../providers/africlean-modal/africlean-modal.provider';
import {map, mergeMap} from 'rxjs/operators';
import {AfricleanObservablesProvider} from '../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanIncidentsListComponent} from '../africlean-incidents-list.component';
import {AfricleanAdressListComponent} from '../../africlean-adress-list/africlean-adress-list.component';
import {StateAdress, UserRole} from '../../../../providers/utils/commonEnum';

@Component({
    selector: 'incident-item',
    templateUrl: './incident-item.component.html',
    styleUrls: ['./incident-item.component.scss'],
})
export class IncidentItemComponent implements OnInit {

    @Input() public incident: any;

    @Input() public theme: string;

    @Input() public incidentDate: any;
    /**
     * Test si la platforme est de type web
     */
    @Input() public isWeb = true;

    public adress: any[] = [];

    /**
     * Source d'appel du component
     */
    @Input() public source = 'report-incident';

    /**
     * Role de l'utilisateur
     */
    @Input() public role: number;

    /**
     * action de l'utilisateur
     */
    @Input() public action: string;

    constructor(private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private subscribeProvider: AfricleanHttpSubscribeProvider,
                private observableProvider: AfricleanObservablesProvider,
                private modal: AfricleanModalProvider) {
    }

    ngOnInit() {
        this.init();
        //  this.customizeDate();
    }

    public async init() {
        this.role = await this.session.getItem('role');
    }

    /**
     * Customise la date
     */
    private customizeDate() {
        const date = this.incident.incident.incidentDate.toString().split('T')[0];
        const splitDate = date.split('-');
        this.incidentDate = splitDate[2] + '-' + splitDate[1] + '-' + splitDate[0];
    }

    public async update() {
        const incidentList = [];
        incidentList.push(this.incident);
        const props = {
            source: 'report-incident',
            theme: 'consult-incident',
            incidents: incidentList,
            action: 'update',
            role: this.role
        };
        await this.modal.create(AfricleanIncidentsListComponent, 'my-custom-modal-css', props);
        const data: { displayIncidentConfirmation: boolean } = await this.modal.getData();
        if (data.displayIncidentConfirmation) {
            this.observableProvider.loadIncident.emit(true);
        }
    }

    delete() {
        const url = 'incident/delete/' + this.incident.incident.id;
        this.subscribeProvider.delateSubscribe(url).subscribe(
            (intervention) => {
            }
        );
    }

    /**
     * Affiche l'adresse de l'incident
     */
    public async showAdress(incident: any) {
        const url = 'user/adress/' + this.incident.client.id;
        const body = {
            role: UserRole.CLIENT,
            type: StateAdress.SUBSCRIBE
        };
        this.userProvider.getAdress(url, body).pipe(
            map(
                (adresses) => {
                    this.adress = this._filter(this.incident.home.id, adresses);
                   // this.getAdresseOfIncident(adresses);
                    this.incident = incident;
                    const props = {
                        adresses: this.adress,
                        user: this.incident.client,
                        source: 'consult-incident',
                        action: 'incident'
                    };
                    this.modal.create(AfricleanAdressListComponent, 'my-custom-modal-css', props);
                }
            )
        ).subscribe(
            (adresses) => {
            });
    }

    private _filter(value: number, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.idHome.toString().indexOf(filterValue) === 0);
    }

    /**
     * Recupères les adresses pour lesquelles les abonnements correpondant a STATE
     * @param adresses adresses
     */
    private getAdresseOfIncident(adresses: any[]) {
        const adressTMP = [];
        adresses.forEach(adresse => {
            if (this.incident.incident.homeCode === adresse.idHome) {
                adressTMP.push(adresse);
            }
        });
        this.adress = adressTMP;
    }
}
