import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
// tslint:disable-next-line:max-line-length
import { AfricleanPayementComponent } from '../../manager-components/africlean-finance-manager/africlean-payement/africlean-payement.component';
import { AfricleanModalProvider } from '../../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanHttpSubscribeProvider} from '../../../../providers/africlean-http/africlean-http-subscribe.provider';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanHttpUserProvider} from '../../../../providers/africlean-http/africlean-http-user.provider';
import {UserRole} from "../../../../providers/utils/commonEnum";

@Component({
  selector: 'africlean-item',
  templateUrl: './africlean-item.component.html',
  styleUrls: ['./africlean-item.component.scss'],
})
export class AfricleanItemComponent implements OnInit {
  /**
   * Type de theme (abonnement,employes....)
   */
  @Input() public theme: string;

  /**
   * Texte de l'item
   */
  @Input() public title: string;

  /**
   * Sous texte de l'item
   */
  @Input() public subTitle: string;

  /**
   * Si le mois à été payer ou non => Abonnement
   */
  @Input() public isPaid: boolean;

  /**
   * Texte de l'item
   */
  @Output() public clickItem = new EventEmitter<string>();

  constructor(private subscribeProvider: AfricleanHttpSubscribeProvider,
              private session: AfricleanSessionProvider,
              private userProvider: AfricleanHttpUserProvider,
              private modal: AfricleanModalProvider) { }

  ngOnInit() {}

  public async payeAbonnement() {
      const props = {
        source: 'consult-abonnement',
        typePayement: 'abonnement',
        role: UserRole.CLIENT,
        displayForm: true
      };
      await this.modal.create(AfricleanPayementComponent, 'my-custom-modal-css', props);
    }
  }
