import {Component, Input, OnInit} from '@angular/core';
import { NavController } from '@ionic/angular';
// tslint:disable-next-line:max-line-length
import { AfricleanControlRegistrationProvider } from '../../../../../providers/africlean-control-fields/africlean-control-registration.provider';
import { TranslateService } from '@ngx-translate/core';
import { AfricleanHttpUserProvider } from '../../../../../providers/africlean-http/africlean-http-user.provider';
import { AfricleanHttpProspectionProvider } from '../../../../../providers/africlean-http/africlean-http-prospection.provider';
import { AfricleanSessionProvider } from '../../../../../providers/africlean-session/africlean-session.provider';
import { AfricleanModalProvider } from '../../../../../providers/africlean-modal/africlean-modal.provider';
import { AfricleanUserModel } from '../../../../../models/africlean-user.model';

@Component({
  selector: 'app-africlean-add-prospection',
  templateUrl: './africlean-add-prospection.component.html',
  styleUrls: ['./africlean-add-prospection.component.scss'],
})
export class AfricleanAddProspectionComponent implements OnInit {


  /**
   * Message d'erreur à afficher si un problème survient
   */
  public errorMsg: string;

  /**
   * Etat du spinner de chargement
   */
  public hideSpinner = true;

  /**
   * Homme ou Femme
   */
  public gender = 'Homme';

  /**
   * lastname de l'utilisateur
   */
  public lastName: string;

  /**
   * firstname de l'utilisateur
   */
  public firstName: string;
  /**
   * Numéro de téléphone de la personne
   */
  public phone: string;

  /**
   * Date de debut de prospection
   */
  public dateStartProspect: Date;
  /**
   * Date de fin de prospection
   */
  public dateEndProspect: Date;

  /**
   * Horaire d'interventionn
   */
  public timeProspect: string;

  /**
   * Type de prospection
   */
  public typeOfProspection: string;

  /**
   * Resultat de prospection
   */
  public resultProspect: string;

  /**
   * Nom du foyer de prospection
   */
  public nameOfHome: string;

  /**
   * adresse à rechercher
   */
  @Input() public adressToProspect: {
    adresseId: string,
    idHome: string,
    country: string,
    region: string,
    commune: string,
    district: string,
    sector: string,
    home: string,
    state?: string
  };
  /**
   * Liste des employes
   */
  public employes: AfricleanUserModel[] = [];
  /**
   * Role de l'utilisateur
   */
  @Input() public role: string;

  /**
   * code de l'utilisateur
   */
  @Input() public commercialCode: string;

  /**
   * Adresse de prospection
   */
  @Input() public adresse: string;

  constructor(private navCtrl: NavController,
              private controlFiels: AfricleanControlRegistrationProvider,
              private translateService: TranslateService,
              private httpUser: AfricleanHttpUserProvider,
              private prospectProvider: AfricleanHttpProspectionProvider,
              private session: AfricleanSessionProvider,
              private modal: AfricleanModalProvider) {}


  /**
   * Initialisation de la page
   */
  ngOnInit() {
    this.init();
  }

public async init() {
  this.role = await this.session.getItem('role');
  this.getCommercials();
}
  /**
   * Retourne tous les employer de l'entreprise
   */
  public async getCommercials() {
    const url = 'user/employe/commercial/';
    this.httpUser.getAll(url).subscribe(
        (employesList) => {
          this.employes = employesList;
        }
    );
  }
  /**
   * Récupère la date de début
   * @param event évenement émis
   */
  public getStartDate(event: any) {
    this.dateStartProspect = event.target.value;
  }
  /**
   * Récupère la date de fin
   * @param event évenement émis
   */
  public getEndDate(event: any) {
    this.dateEndProspect = event.target.value;
  }

  public getGender(gender: string) {
    this.gender = gender;
  }

  /**
   * Close la modal
   */
  public closeModal() {
    this.modal.dismiss({ displayRegistrationConfirmation: false });
  }

  /**
   * Enregistrer un client
   * @param body corps de la requête
   */
  private saveClient(body: {}) {
    return this.httpUser.save('user/client/save', body);
  }
  /**
   * Code de payementEmployé à payer
   * @param event
   */
  public getCommercial(event: any) {
    this.commercialCode = event.target.value;
  }

 public async saveProspection() {
   const userId = await this.session.getItem('userId');
   const roleAuthor = await this.session.getItem('role');
   /*const adress = this.adressToProspect.commune + '' + this.adressToProspect.district + '' +
                   this.adressToProspect.sector + '' + this.adressToProspect.home;*/
   let comercialCode = null;
   if (this.role === 'COMMERCIAL') {
     comercialCode = userId;
   } else {
     comercialCode = this.commercialCode;
   }

   let body = {};
   body = {
       firstName: this.firstName,
       lastName: this.lastName,
       gender: this.gender,

       phoneNumber: this.phone,
       typeOfProspection: this.typeOfProspection,

       dateStartProspect: this.dateStartProspect,
       dateEndProspect: this.dateEndProspect,
       timeProspect: this.timeProspect,
       resultProspect: this.resultProspect,
       commercialCode: comercialCode,
       prospectionAdress: this.adresse,
       authorCode: { authorId: userId, role: this.role }
     };
   this.prospectProvider.save('prospection/save', body).subscribe(
       (prospection) => {
         this.modal.dismiss({ displayProspectionConfirmation: true });
       }
   );
  }
}
