import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import { AfricleanAdressComponent } from '../africlean-adress/africlean-adress.component';
import { AfricleanModalProvider } from '../../../providers/africlean-modal/africlean-modal.provider';

@Component({
  selector: 'app-africlean-open-button',
  templateUrl: './africlean-open-button.component.html',
  styleUrls: ['./africlean-open-button.component.scss'],
})
export class AfricleanOpenButtonComponent implements OnInit {

  /**
   * Titre du bouton
   */
  @Input() title: string;

  /**
   * Titre du bouton
   */
  @Input() public action: string;

  /**
   * Paramètre de configuration du visuel du bouton
   */
  @Input() theme: string;

  /**
   * id de l'utilisateur
   */
  @Input() public userId;

  /**
   * Role de l'utilisateur
   */
  @Input() public role;

  /**
   * source de la paage
   */
  @Input() public source;


  /**
   * Liste des adresses de l'utilisateurs
   */
  @Input() public adresses: {
    adresseId: string,
    idHome: string,
    country: string,
    region: string,
    commune: string,
    district: string,
    sector: string,
    home: string,
    state?: string
  }[];

  /**
   * adresse à rechercher
   */
  @Input() public adressToProspect: {
    adresseId: string,
    idHome: string,
    country: string,
    region: string,
    commune: string,
    district: string,
    sector: string,
    home: string,
    state?: string
  };

  /**
   * Evenement émis lors du click du bouton
   */
  @Output() clickButton = new EventEmitter<string>();

  constructor( private modal: AfricleanModalProvider) { }

  ngOnInit() {
  }
 public async onAction() {
    if (this.action === 'create') {
      // On appel la modal pour saisir une adresse
      const props = {
        displayButton: true,
        userId: this.userId,
        role: this.role,
        source: this.source
    };
      await this.modal.create(AfricleanAdressComponent, 'my-custom-modal-css', props);
      const data = await this.modal.getData();
      // tslint:disable-next-line: no-unused-expression
      !this.adresses ? this.adresses = [] : '';
      // tslint:disable-next-line: no-unused-expression
      data ? this.adresses.push(data) : '';
    } else  if (this.action === 'prospect') {
      // On appel la modal pour saisir une adresse
      const props = {
        displayButton: true,
        source: this.source
      };
      await this.modal.create(AfricleanAdressComponent, 'my-custom-modal-css', props);
      const data = await this.modal.getData();
      // tslint:disable-next-line: no-unused-expression
      !this.adressToProspect ? this.adressToProspect = null : '';
      // tslint:disable-next-line:no-unused-expression
      data ? this.adressToProspect : '';
    }
  }
}
