import {Component, Input, OnInit} from '@angular/core';
import {AfricleanSessionProvider} from 'src/providers/africlean-session/africlean-session.provider';
import {AfricleanHttpUserProvider} from 'src/providers/africlean-http/africlean-http-user.provider';
import {map, mergeMap} from 'rxjs/operators';
import {from, Observable} from 'rxjs';
import {AfricleanModalProvider} from 'src/providers/africlean-modal/africlean-modal.provider';
import {AfricleanUserModel} from '../../../../../models/africlean-user.model';
import {AfricleanHttpSubscribeProvider} from '../../../../../providers/africlean-http/africlean-http-subscribe.provider';
import {AfricleanItemListModel} from '../../../../../models/services/africlean-item-list.model';
import {AfricleanHttpItemListProvider} from '../../../../../providers/africlean-http/africlean-http-item-list.provider';
import {AfricleanHttpPayementProvider} from '../../../../../providers/africlean-http/africlean-http-payement.provider';
import { AfricleanConfigProvider } from 'src/providers/africlean-config/africlean-config.provider';
import { AfricleanDateProvider } from 'src/providers/africlean-date/africlean-date.provider';

@Component({
    selector: 'app-africlean-payement',
    templateUrl: './africlean-payement.component.html',
    styleUrls: ['./africlean-payement.component.scss'],
})
export class AfricleanPayementComponent implements OnInit {
    /**
     * Liste des adresses renseigner
     */
    @Input() public adresses: {
        adresseId: any,
        idHome: string,
        country: string,
        region: string,
        commune: string,
        district: string,
        sector: string,
        home: string,
        state?: string,
        subscribe: any,
        montant: any,
    }[];

    /**
     * Le payement de l'abonnement selectionner
     */
    public payementSubscribe: {
        balanceOfAbonnement: number,
        dateToSubscribePayment: Date,
        amountToPay: number,
        meanOfPayment: string,
        sateOfPayment: string,
        compteNumber: string
        paymentCode: string,
        subscribeCode: string,
        homeCode: string,
        clientCode: string,
        payementCreatedAuthorId?: { authorId: string; role: string }
    };

    /**
     * Le payement de l'employer
     */
    public payementEmploye: {
        balanceOfAbonnement: number,
        dateToSubscribePayment: Date,
        amountToPay: number,

        meanOfPayment?: string,
        paymentCode?: string

        employeCode: { authorId: string, role: string },
        compteNumber: string
        payementCreatedAuthorId?: { authorId: string; role: string }
    };
    /**
     * Liste des employes
     */
    public employes: AfricleanUserModel[] = [];

    /**
     * Controler le click du bouton valider
     */
    public hideValidateButton = false;

    /**
     * Code de l'abonnement
     */
    public subscribeCode: string;

    /**
     * Code de home de l'abonnement
     */
    public homeCode: string;

    /**
     * Tester si les adresses existent
     */
    @Input() public isAdressExist: boolean;

    /**
     * Afficher le spinner
     */
    public hideSpinner = false;

    /**
     * afficher le formulaire
     */
    @Input() public showFormuler = false;

    /**
     * Source d'appel du component
     */
    @Input() public source = 'consult-finance';

    /**
     * Type de payement
     */
    @Input() public typePayement = 'abonnement';

    /**
     * Id de l'utilisateur
     */
    @Input() public userId: string;

    /**
     * client
     */
    @Input() public client: AfricleanUserModel;

    /**
     * Message d'erreur
     */
    public errorMsg: string;

    /**
     * Télephone du client
     */
    public phone: string;

    /**
     * Numéro de téléphone du client
     */
    public phoneCode = '224';

    /**
     * Moyenne de payement
     */
    public meanPayement: any[];

    /**
     * Montan à payer
     */
    public montant: number;

    /**
     * code payement
     */
    public paymentCode = '';

    /**
     * date de payement
     */
    public dateOfPayment: Date;

    /**
     * role de l'Employe à payer
     */
    public employeRole: string;

    /**
     * les differents type d'employé
     */
    public employeTypes: string [];

    /**
     * Formulaire de recherche du client
     */
    @Input() public displayForm = false;

    /**
     * client concerné
     */
    @Input() public userTMP: AfricleanUserModel;

    /**
     * Permet de savoir c'est une plateforme web
     */
    public isWeb: boolean;
    /**
     * Etat de l'abonnment
     */
    @Input() public state = 'VALIDER';


    /**
     * Role de l'utilisateur
     */
    @Input() private role: string;
    /**
     * Liste des item
     */
    public itemList: AfricleanItemListModel[] = [];

    /**
     * Liste de tranche des payements
     */
    public payementTranches: any[];
    public meanPayementList: any[];

    public showClient = true;
    /**
     * Montant à payer
     */
    public amount: any;

    filteredOptions: Observable<string[]>;

    constructor(private payement: AfricleanHttpPayementProvider,
                private itemListProvider: AfricleanHttpItemListProvider,
                private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private subscribeProvider: AfricleanHttpSubscribeProvider,
                private modal: AfricleanModalProvider,
                private config: AfricleanConfigProvider,
                private dateProvider: AfricleanDateProvider) {
    }

    ngOnInit() {
        this.adresses = [];
        this.hideSpinner = true;
        this.employeTypes = ['MANAGER', 'INTERVENANT', 'COMMERCIAL'];
        this.isWeb = this.config.isWeb();
        this.getMeanPayementList();
        this.getAllPayementTranche();
        this.init();
    }

    /**
     * Initialisation de l'objet payement
     */
    private async init() {
        const userId = await this.session.getItem('userId');
        const role1 = await this.session.getItem('role');
        const user = await this.session.getItem('user');
        this.payementSubscribe = {
            balanceOfAbonnement: null,
            dateToSubscribePayment: null,
            amountToPay: null,
            meanOfPayment: '',
            compteNumber: '',
            paymentCode: '',
            subscribeCode: '',
            sateOfPayment: 'EN COURS',
            homeCode: '',
            clientCode: '',
            payementCreatedAuthorId: {authorId: userId, role: role1}
        };
        this.payementEmploye = {
            balanceOfAbonnement: null,
            dateToSubscribePayment: null,
            amountToPay: null,
            meanOfPayment: '',
            compteNumber: '',
            paymentCode: '',
            employeCode: {authorId: '', role: ''},
            payementCreatedAuthorId: {authorId: userId, role: role1}
        };

        if (role1 === 'CLIENT') {
            // this.phone = user.phoneNumber;
            this.source = 'consult-abonnement';
            const p2 = user.phoneNumber.split(' ');
            this.phoneCode = (p2 && p2.length > 0) ? p2[0].replace('+', '') : '224';
            this.phone = (p2 && p2.length > 0) ? user.phoneNumber.replace(p2[0], '') : user.phoneNumber;

            this.findClient();
        } else {
            // this.getEmployes();
        }
        this.hideSpinner = false;
    }

    /**
     * Retourne tous les employer de l'entreprise
     */
    public async getEmployes() {
        let url = 'user/employe/';
        if (this.employeRole === 'MANAGER') {
            url = url + 'manager';

        } else if (this.employeRole === 'INTERVENANT') {
            url = url + 'intervenant';
        } else if (this.employeRole === 'COMMERCIAL') {
            url = url + 'commercial';

        } else if (this.employeRole === 'ADMIN') {
            url = url + 'admin';
        }
        this.userProvider.getAll(url).subscribe(
            (employesList) => {
                this.employes = employesList;
            }
        );
    }

    /**
     * Ferme le modal d'abonnement
     */
    public close() {
        this.modal.dismiss();
    }

    /**
     * Renvoie les adresses
     */
    public getAdresses() {
        if (this.userId) {
            const url = 'user/subscribe-client/' + this.userId;
            const body = {
                role: 'CLIENT',
                state: 'VALIDER'
            };
            // Recupere les adresses qui ont un abonnement
            return from(this.userProvider.getAdress(url, body)).pipe(
                map(
                    (adresses) => {
                        this.adresses = adresses;
                        /*   const abonnementsTMP = [];
                           adresses.forEach(adresse => {
                               if (adresse.state === this.state) {
                                   abonnementsTMP.push(adresse);
                               }
                           });
                           this.adresses = abonnementsTMP;*/
                    }
                )
            );
        }
    }

    /**
     * Retourn  un client en fonction du argument body
     * @param body corps de la requête
     */
    private getClient(body: {}) {
        return this.userProvider.getClient('user/client/login', body);
    }

    /**
     * Recherche les abonnements du client
     */
    public findClient() {
        this.hideSpinner = false;
        this.showClient = false;
        const phone = '+' + this.phoneCode + ' ' + this.phone.replace(' ', '');
        let body = {};
        body = {
            phoneNumber: phone
        };
        return this.getClient(body).pipe(
            map(
                (user) => {
                    this.client = user;
                    this.userId = user.objectId;
                    this.session.setItem('userTMP', user);
                }
            ),
            mergeMap(
                (_) => {
                    return this.getAdresses().pipe(
                        map(
                            () => {
                                if (this.adresses.length === 0) {
                                    this.isAdressExist = false;
                                    this.hideSpinner = true;
                                    this.showClient = true;
                                } else {
                                    this.isAdressExist = true;
                                    this.hideSpinner = true;
                                    this.showClient = true;
                                }
                                this.showFormuler = true;
                            }
                        )
                    );

                }
            )
        ).subscribe(
            (_) => {
            }
        );
    }

    /**
     * Renvoi la liste de toutes les tranche des payements
     */
    public getAllPayementTranche() {
        if (this.typePayement === 'abonnement') {
            /* const body = {typeOfItemList: 'PRIX ABONNEMENT'};
             this.getItemListValue(body);*/
        } else {
            const body = {typeOfItemList: 'PRIX PAYEMENT'};
            this.getItemListValue(body);
        }
    }

    /**
     * Renvoi la liste de toutes les tranche des payements
     */
    public getMeanPayementList() {
        const body = {
            typeOfItemList: 'MOYENNE DE PAYEMENT'
        };
        this.getItemListValue(body);
    }

    /**
     * Renvoi la liste de toutes les itemList
     */
    public async getItemListValue(body: { typeOfItemList: string }) {
        const role1 = await this.session.getItem('role');
        this.itemListProvider.getItemListByTypeOfItem('itemList/itemlist-type', body).pipe(
            map(
                (itemList) => {
                    const payementTMP = [];
                    if (body.typeOfItemList === 'PRIX ABONNEMENT' || body.typeOfItemList === 'PRIX PAYEMENT') {
                        this.payementTranches = itemList;

                    } else if (body.typeOfItemList === 'MOYENNE DE PAYEMENT') {
                        this.meanPayementList = (role1 === 'CLIENT') ? this._filterByMean('ESPECE', itemList) : itemList;
                    }
                    this.itemList = itemList;
                }
            ),
            mergeMap(
                (_) => {
                    return this.itemList;
                }
            )
        ).subscribe(
            (itemList) => {
            }
        );
    }

    private _filterByMean(value: string, list: any[]): any[] {
        const filterValue = value.toLowerCase();
        return list.filter(option => option.itemListValue.toLowerCase().indexOf(filterValue) !== 0);
    }

    /**
     * Recupere le tranche de payement
     * @param event
     */
    public getPayementTranche(event: any) {
        this.payementSubscribe.amountToPay = event.target.value;
        this.payementEmploye.amountToPay = event.target.value;
    }

    /**
     * Recupere moyen payement
     * @param event
     */
    public getMeanPayement(event: any) {
        this.meanPayement = event.target.value;
        this.payementSubscribe.meanOfPayment = event.target.value;
        this.payementEmploye.meanOfPayment = event.target.value;

    }

    /**
     * Récupère la date de payement
     * @param event évenement émis
     */
    public getDate(event: any) {
        this.dateOfPayment = event.target.value;
        this.payementEmploye.dateToSubscribePayment = event.target.value;
        this.payementSubscribe.dateToSubscribePayment = event.target.value;
    }

    /**
     * Retourn le role de l'Employé à payer
     * @param event
     */
    public getEmployeRole(event: any) {
        this.payementEmploye.employeCode.role = event.target.value;
        this.employeRole = event.target.value;
        this.getEmployes();
    }

    /**
     * Code de payementEmployé à payer
     * @param event
     */
    public getEmploye(event: any) {
        this.payementEmploye.employeCode.authorId = event.target.value;
    }

    /**
     * L'adresse de l'abonnement à payer
     * @param event
     */
    public getSelectAdress(event: any) {
        const address = this._filter(event.target.value);
        this.payementSubscribe.subscribeCode = address[0].subscribe.objectId;
        this.payementSubscribe.homeCode = address[0].idHome;
        this.amount = '';
        this.amount = address[0].montant.itemListValue;
        this.payementTranches = [];
        this.payementTranches.push(address[0].montant);
        this.payementSubscribe.amountToPay = address[0].montant.itemListValue;
    }

    private _filter(value: string): any[] {
        const filterValue = value.toLowerCase();
        return this.adresses.filter(option => option.idHome.toLowerCase().indexOf(filterValue) === 0);
    }

    /**
     * Valider le payement
     */
    public async validPayement() {
        if (this.typePayement === 'abonnement') {
            const role1 = await this.session.getItem('role');
            if (role1 === 'CLIENT' || role1 === 'COMMERCIAL') {
                this.payementSubscribe.sateOfPayment = 'EN COURS';
            } else {
                this.payementSubscribe.sateOfPayment = 'VALIDER';
            }

            this.payementSubscribe.paymentCode = this.paymentCode;
            this.payementSubscribe.clientCode = this.client.objectId;

            if (this.payementSubscribe.paymentCode.length === 18  || this.payementSubscribe.meanOfPayment === 'ESPECE') {

                let currentMonth = this.dateProvider.getMonthComplet(
                        new Date(this.payementSubscribe.dateToSubscribePayment).getMonth());
                let currentYear = new Date(this.payementSubscribe.dateToSubscribePayment).getFullYear() + '';
                
                let options = {
                    payementCode: this.paymentCode,
                    clientCode: this.client.objectId
                }
                let body = {
                    month: currentMonth,
                    year: currentYear,
                    clientCode: this.client.objectId,
                    codeAbonnement: this.payementSubscribe.subscribeCode
                }
                this.verifyPaymentCode(options).subscribe(

                    (paymentCode) => {
                        this.hideValidateButton = true;
                        if (paymentCode && paymentCode.length === 0  || this.payementSubscribe.meanOfPayment === 'ESPECE') {

                            this.verifyPaymentMonth(body).subscribe(
                                (payementMonth) => {
                                    if (payementMonth && payementMonth.length === 0) {

                                        this.payement.save('payement/save', this.payementSubscribe).subscribe(
                                            (payement) => {
                                                this.errorMsg = null;
                                                this.modal.dismiss({displayPayementConfirmation: true});
                                            }
                                        );
                                    } else {
                                        this.errorMsg = 'Le mois selectionné a déja été payé'
                                    }
                                }
                            )
                        } else {
                            this.errorMsg = 'Le Code de paiement existe déja';
                        }
                    }
                )
            } else {
                this.errorMsg = 'Code de paiement doit contenir 18 caractères';
            }
        } else {
            this.payementEmploye.paymentCode = this.paymentCode;
            this.payement.save('payement-employe/save', this.payementEmploye).subscribe(
                (payement) => {
                    this.modal.dismiss({displayPayementConfirmation: true});
                }
            );
        }

    }

    /**
     * Verifie si le code de paiement existe deja ou pas
     * @param options
     */
    public verifyPaymentCode(options: any) {
        return this.payement.save('payement/verify-payement-code', options);
    }

    /**
     * Verifie si le à déja été payé ou pas
     * @param options
     */
    public verifyPaymentMonth(options: any) {
        return this.payement.save('payement/verify-payement-month', options);
    }

    /**
     * Retourn l'abonnement correspondant à l'adresse
     * @param homeCode
     */
    public getSubscribeyHomeCode(homeCode: any) {
        const url = 'abonnement/adress/' + homeCode;
        this.subscribeProvider.findSubscribeByAdress(url).subscribe(
            (subscribe) => {
                this.payementSubscribe.subscribeCode = subscribe[0].objectId;
            }
        );
    }

    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({displayRegistrationConfirmation: false});
    }

    telInputObject(obj) {
        const list = obj.p;
        if (this.phone) {
            const country = this.filter(this.phoneCode.replace('+', ''), list);
            if (country.length > 0) {
                obj.setCountry(country[0].iso2);
                this.phoneCode = country[0].dialCode;
            }

        }

    }

    getNumber(event) {
    }

    onCountryChange(event: any) {
        const phone = this.phone;
        const p2 = phone.split(' ');
        this.phoneCode = event.dialCode;
    }

    private filter(value: string, list: any[]): any[] {
        const filterValue = value;
        return list.filter(option => option.dialCode.indexOf(filterValue) === 0);
    }
}
