import {Component, Input, OnInit} from '@angular/core';
import {AfricleanModalProvider} from '../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanMailerProvider} from "../../../providers/utils/africlean-mailer.provider";
import {AfricleanConfigProvider} from "../../../providers/africlean-config/africlean-config.provider";

@Component({
  selector: 'app-africlean-message-form',
  templateUrl: './africlean-message-form.component.html',
  styleUrls: ['./africlean-message-form.component.scss'],
})
export class AfricleanMessageFormComponent implements OnInit {

  /**
   * Nom de l'utilisateur
   */
  public userName: string;

  /**
   * Mail de l'utilisateur
   */
  public email: string;

  /**
   * Message à envoyé
   */
  public message: string;

  /**
   * Annonce concernée
   */
  @Input() public title: string;

  /**
   * Afficher le spinner ou non
   */
  public displaySpinner: boolean;

  /**
   * Message de succcès
   */
  public successMsg: string;

  /**
   * Message d'erreur
   */
  public errorMsg: string;

  /**
   * Teste si le type de plateforme
   */
  @Input() public isWeb: boolean;

  constructor(private mailer: AfricleanMailerProvider,
              private config: AfricleanConfigProvider,
              private modal: AfricleanModalProvider) {

  }

  ngOnInit() {
   this.isWeb = this.config.isWeb();
  }

  /**
   * Récupère le message
   * @param event
   */
  public handleMessage(event) {
    this.message = event.target.value;
  }

  /**
   * Close la modal
   */
  public closeModal() {
    this.modal.dismiss({ displayRegistrationConfirmation: false });
  }

  /**
   * Envoie le message
   */
  public sendMessage() {
    this.displaySpinner = true;
    if (this.userName && this.userName.trim() !== '' &&
        this.message && this.message.trim() !== '') {
      this.errorMsg = null;
      const to = {
        toEmail: 'contact@africlean-services.com',
        toName: 'AFRICLEAN',
        replyTo: this.email
      };
      const from = {
        fromName: this.userName,
        fromEmail: this.email,
        from_: this.email
      };
      this.mailer.send(to, from, this.title, this.message).then((httpResponse) => {
        if (httpResponse.ok) {
          this.successMsg = 'Votre message à bien été envoyé. Veuillez vérifier régulièrement votre boîte mail.';
          this.displaySpinner = false;
          this.userName = null;
          this.email = null;
          this.message = null;
        } else {
          return httpResponse.text()
              .then(text => Promise.reject(text));
        }
      })
          .catch((error) => {
            console.log('Oops... ' + error);
            this.errorMsg = 'Votre message n\'a pas été envoyé';
            this.successMsg = null;
            this.displaySpinner = false;
          });


    } else {
      this.errorMsg = 'Attention veuillez renseigner tous les champs';
      this.successMsg = null;
      this.displaySpinner = false;
    }
  }

}
