/**
 * Le modèle de données User
 */
export class AfricleanUserModel {
    /**
     * id de l'utilisateur
     */
    public objectId: string;

    /**
     * nom de l'utilisateur
     */
    public firstName: string;

    /**
     * Prenom de l'utilisateur
     */
    public lastName: string;

    /**
     * date de naissance
     */
    public dateOfBirth: Date;

    /**
     * lieu de naissance
     */
    public cityOfBirth: string;

    /**
     * Pays de naissance
     */
    public countryOfBirth: string;

    /**
     * nom du Pere de l'utilisateur
     */
    public fatherFirstName: string;

    /**
     * Prenom du pere de l'utilisateur
     */
    public fatherLastName: string;

    /**
     * nom de la mere de l'utilisateur
     */
    public motherFirstName: string;

    /**
     * Prenom de la mere de l'utilisateur
     */
    public motherLarstName: string;

    /**
     * Telephone portalble de l'utilisateur
     */
    public phoneNumber: string;

    /**
     * Telephone fixe
     */
    public phoneFixedNumber: string;

    /**
     * adress de l'utilisateur
     */
    public adress: string;

    /**
     * codePostal l'utilisateur
     */
    public zipCode: string;

    /**
     * ville de l'adress l'utilisateur
     */
    public cityOfAdress: string;

    /**
     * fonction de l'utilisateur
     */
    public userJob: string;

    /**
     * pseudo de l'utilisateur
     */
    public pseudo: string;

    /**
     * Adresse mail de l'utilisateur
     */
    public email: string;

    /**
     * Mot de passe de l'utilisateur
     */
    public password: string;

    /**
     * date de creation du compte de l'utilisateur
     */
    public dateOfCreation: Date;

    /***
     * date de modification de compte de l'utilisateur
     */
    public dateOfUpdate: Date;

    /**
     * date de suppression de compte de l'utilisateur
     */
    public dateOfDeleted: Date;

    /**
     * genre de l'utilisateur (masculin ou fiminin)
     */
    public genderOfUser: string;

    /**
     * civilite de l'utilisateur (celibateur, marié, veuf, veuf ...)
     */
    public civilitOfUsers: string;
    /**
     * compte bancaire de l'utilisateur
     */
    public banqueCode: string;

    /**
     * Type d'utilisateur
     */
    public typeOfUser: string;

    /**
     * Si l'utilisateur est abonnée ou non
     */
    public isSubscribe: string;


    /**
     * auteur de la création de l'utilisateur dans la table
     */
    public userCreatedAuthorId: { authorId: string,  role: string };

    // ****************** LES CHAMPS CONCERNANT LES ADMINS **************

    /**
     * niveau de droit d'administration
     */
    public adminLevel: number;

    // ****************** CAS DES CLIENTS ET COMMERCIAL **********************

    /**
     * Relation avec le model de données intervention
     */
    public interventions: any[];

    /**
     * Relation avec le model de données du planning
     */
    public planning: any[];

    /**
     * Relation avec le model de données abonnement
     */
    public subscribeModel: any[];

    /**
     * Relation avec le model de données incident
     */
    public incidents: any[];

    // ****************** CAS DES EMPLOYES **********************

    /**
     * date de recrutement
     */
    public dateStartJob: Date;

    /**
     * nom du poste occupé
     */
    public positionName: string;

    /**
     * diplome obtenu
     */
    public lastDiplome: string;

    /**
     * date d'obtention
     */
    public dateObtentionLastDiplome: Date;

    /**
     * date d'obtention
     */
    public etablissmentOfLastDiplome: string;

    /**
     * autre diplome obtenu
     */
    public anotherDiplome: string;

    /**
     * date d'obtention du second diplôme
     */
    public dateObtentionAnotherDiplome: Date;

    /**
     * université d'obtention du dipôme
     */
    public etablissmentOfAnotherDiplome: string;

    /**
     * nombre d'année d'experince
     */
    public nbYearsExperiencies: number;

    /**
     * date d'obtention du diplôme
     */
    public lastJobName: string;

    /**
     * date d'obtention du diplôme
     */
    public lastJobEntreprise: string;

    // ************************** CAS DES INTERVENANTS *****************

    /**
     * code d'intervenant
     */
    public intervenantCode: string;


}
