import { Component, OnInit } from '@angular/core';
import {AfricleanModalProvider} from '../../../providers/africlean-modal/africlean-modal.provider';

@Component({
  selector: 'app-africlean-use-terms',
  templateUrl: './africlean-use-terms.component.html',
  styleUrls: ['./africlean-use-terms.component.scss'],
})
export class AfricleanUseTermsComponent implements OnInit {
  constructor( private modal: AfricleanModalProvider) { }

  ngOnInit() {}

  /**
   * Close la modal
   */
  public closeModal() {
    this.modal.dismiss({ displayRegistrationConfirmation: false });
  }


}
