import { Pipe, PipeTransform } from '@angular/core';
import {StateSubscribe} from '../../providers/utils/commonEnum';

@Pipe({
  name: 'subscribeState'
})
export class SubscribeStatePipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    switch (value) {
      case StateSubscribe.NEW:
        return 'NOUVEAU';
      case StateSubscribe.IN_PROGRESS_SUBSCRIB:
        return 'EN COURS';
      case StateSubscribe.SUBSCRIBE:
        return 'ABONNE';
      case StateSubscribe.IN_PROGRESS_TERMINATION:
        return 'EN COURS DE RESILIATION';
      case StateSubscribe.TERMINATION:
        return 'RESILIE';
      case StateSubscribe.SUSPENDED:
        return 'SUSPENDU';
      default:
        return 'Autre';
    }
  }

}
