import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {NavController, AlertController, MenuController} from '@ionic/angular';
import { AfricleanPopoverProvider } from 'src/providers/africlean-popover/africlean-popover.provider';
import { AfricleanNotificationsComponent } from '../africlean-notifications/africlean-notifications.component';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';
import { AfricleanConfigProvider } from 'src/providers/africlean-config/africlean-config.provider';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanObservablesProvider } from 'src/providers/africlean-observables/africlean-observables.provider';
import { AfricleanWebsocketProvider } from 'src/providers/africlean-websocket/africlean-websocket.provider';
import { AfricleanHttpNotificationProvider } from 'src/providers/africlean-http/africlean-http-notification.provider';
import { AfricleanNotificationModel } from 'src/models/abonnement/africlean-notification.model';
import {AfricleanMessageFormComponent} from '../africlean-message-form/africlean-message-form.component';

@Component({
  selector: 'africlean-header',
  templateUrl: './africlean-header.component.html',
  styleUrls: ['./africlean-header.component.scss'],
})
export class AfricleanHeaderComponent implements OnInit {

  /**
   * Icon du menu
   */
  @Input() icon: string;

  /**
   * Logo de l'application
   */
  @Input() logo: string;

  /**
   * Notification image
   */
  @Input() notification: string;

  /**
   * Home
   */
  @Input() home: string;

  /**
   * Activation de l'ombre
   * Si activée = true
   */
  @Input() shadow = true;

  /**
   * Event sur le bouton principal du header
   */
  @Output() clickButton = new EventEmitter<string>();

  /**
   * Notifications
   */
  private notifications: {
    notification: any,
    state: boolean
  }[];

  /**
   * Teste si l'utilisateur est authentifier
   */
  public isAuth: boolean;

  /**
   * Le nombre de notifications non lu
   */
  public count: number;
  @Input() public isWeb: boolean;

  @Input() status = 'disconnected';

  /**
   * url du photo de profile
   */
  public src = './assets/icon/users.svg';

  /**
   * Basculer vers le menu deroulant ou non
   */
  public toggle = false;

  /**
   * Lien de la page
   */
  public page: string;

  public user: any;

  /**
   * Lors du click des boutons du menu : pour contrôler le changement de couleur
   */
  public homePageClicked = false;
  public servicesPageClicked = false;
  public teamPageClicked = false;
  public clientPageClicked = false;
  public contactPageClicked = false;

  constructor(private navCtrl: NavController,
              private alertController: AlertController,
              public popoverController: AfricleanPopoverProvider,
              private config: AfricleanConfigProvider,
              public session: AfricleanSessionProvider,
              private observableProvider: AfricleanObservablesProvider,
              private websocket: AfricleanWebsocketProvider,
              private notificationsProvider: AfricleanHttpNotificationProvider,
              private modal: AfricleanModalProvider,
              private menu: MenuController) { }

  ngOnInit() {
    // Dans le cas ou on recharge la page
    this.activeSession();

    // Dans le cas ou on se connecte ou se deconnecte
    this.observableProvider.isAuth.subscribe(
      (active) => {
        this.isAuth = active;
      }
    );
  //  this.isWeb = this.config.isWeb();
  //  console.log(this.isWeb);

    // Récupere toutes les notifications
    this.getNotifications();

    // Ecoute un évenement venant du serveur
    // this.listenNewNotification();
    this.init();
  }

  /**
   * Ecoute et dès qu'il y'a une nouvelle notif, on la push dans la liste des notifs
   */
  private listenNewNotification() {
    this.websocket.receive('notification').subscribe(
      (notif: any) => {
        this.notifications.push(notif);
        this.getNotReadNotification();
      }
    );
  }

  /**
   * Retourne le nombre de notifications non lu
   */
  private getNotReadNotification() {
    let count = 0;
    this.notifications.forEach(notif => {
      if (notif.state === false) {
        count ++;
      }
    });
    this.count = count;
  }
  /**
   * Récupere toutes les notifications du user connecté
   */
  private async getNotifications() {
    const userId = await this.session.getItem('userId');
    const url = 'notification/user-notifications';
    const body = {
      userCode: userId
    };
    this.notificationsProvider.getNotificationsByIdUser(url, body).subscribe(
      (notifications) => {
        this.notifications = notifications;
        // On renverse la liste des notifications pour récupérer les plus récentes
        this.notifications = this.notifications.reverse();
        this.getNotReadNotification();
      }
    );
  }

  /**
   * Active les éléments de l'entête
   */
  private activeSession() {
    this.session.isExist().then(
      (exist) => {
        this.isAuth = exist;
      }
    );
  }

  /**
   * Rédirige l'utilisateur vers l'accueil
   */
  public goHome() {
    // tslint:disable-next-line: no-unused-expression
    this.isAuth ? this.navCtrl.navigateRoot('/home') : this.navCtrl.navigateRoot('/authentication');
  }

  /**
   * Renvoie les notifications
   */
  public goNotifications() {
    const props = {
      notifications: this.notifications
    };
    const isWeb = this.config.isWeb();
    const css = isWeb ? 'my-custom-popover-content' : 'my-custom-popover-content-mobile';
    this.popoverController.create(AfricleanNotificationsComponent, css, props);
  }

  /**
   * Déconnecte l'utilisateur et le redirige vers la page Authent
   */
  public logOut() {
    this.session.clear().then(
      (_) => {
        this.observableProvider.isAuth.emit(false);
        this.navCtrl.navigateForward('/');
      }
    );
  }
  /**
   * Affiche une dialog d'information
   */
  public async search() {

    const alert = await this.alertController.create({
      header: 'ALERT.ALERT_HOME.TITLE',
      cssClass: 'my-custom-alert',
      buttons: [
        {
          text: 'ALERT.ALERT_HOME.YES',
          handler: () => {
            this.navCtrl.navigateForward('/tabs/home');
          }
        },
        {
          text: 'ALERT.ALERT_HOME.NO'
        }
      ]
    });
    await alert.present();
  }


  public init() {
    this.session.getItem('user').then(
        (user) => {
          this.user = user;
          if (this.user) {
            this.status = 'connected';
          }
        });

  }

  /**
   * Ferme le menu lors du clique sur le bouton
   */
  public closeMenu() {
    this.menu.close();
  }


  /**
   * Permet d'être redirigé sur la page de la fonctionnalité sélectionné
   * @param page : string
   */
  public goToPage(page: string) {
    this.page = page;
    this.navCtrl.navigateForward(page);
  }

  /**
   * Envoie un message
   */
  public async sendMessage() {
    const props = {
      title: 'Message sur africlean'
    };
    await this.modal.create(AfricleanMessageFormComponent, 'my-custom-modal-css', props);
  }

  /**
   * Affiche le menu déroulant
   */
  public toggleSubMenu() {
    this.toggle = !this.toggle;
  }

  public async goToLoginPage() {
    this.closeMenu();
    const props = {
      display: true
    };
    this.navCtrl.navigateRoot('/authentication');
  }

}
