import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AfricleanHttpUserProvider} from '../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanObservablesProvider} from '../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanDateProvider} from '../../../../providers/africlean-date/africlean-date.provider';
import {AfricleanModalProvider} from '../../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanConfigProvider} from '../../../../providers/africlean-config/africlean-config.provider';
import {AfricleanFilterProvider} from '../../../../providers/africlean-filter/africlean-filter.provider';
import {AfricleanUserModel} from '../../../../models/africlean-user.model';
import {AfricleanCountryModel} from '../../../../models/localisation/country.model';
import {AfricleanRegionModel} from '../../../../models/localisation/region.model';
import {AfricleanTownModel} from '../../../../models/localisation/town.model';
import {AfricleanDistrictModel} from '../../../../models/localisation/district.model';
import {AfricleanSectorModel} from '../../../../models/localisation/sector.model';
import {AfricleanHttpProspectionProvider} from '../../../../providers/africlean-http/africlean-http-prospection.provider';
import {AfricleanProspectionModel} from '../../../../models/services/africlean-prospection.model';
import {AfricleanAddProspectionComponent} from './africlean-add-prospection/africlean-add-prospection.component';
import {map, startWith} from "rxjs/operators";
import {FormControl} from "@angular/forms";
import {Observable} from "rxjs";

@Component({
    selector: 'app-africlean-prospection',
    templateUrl: './africlean-prospection.component.html',
    styleUrls: ['./africlean-prospection.component.scss'],
})
export class AfricleanProspectionComponent implements OnInit {

    /**
     * Liste des prospections
     */
    @Input() public prospections: any[];

    /**
     * Liste des prospections
     */
    @Input() public prospectionsTMP: any[];


    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrProspectionPerPage: any;

    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;
    /**
     * Emet la position de la page actuel
     */
    @Output() public position = new EventEmitter<any>();

    /**
     * Reactialise la liste des prospections
     */
    @Output() public loadProspection = new EventEmitter<any>();


    /**
     * Liste des filtres
     */
    public filters: {
        filter1: string,
        filter2: string[]
    }[];

    /**
     * Choix pour filtrer
     */
    public choice: {
        filter1: string,
        filter2: string
    }[];

    /**
     * adresse à rechercher
     */
    public adressToSearch: {
        country: AfricleanCountryModel,
        region: AfricleanRegionModel,
        commune: AfricleanTownModel,
        district: AfricleanDistrictModel,
        sector: AfricleanSectorModel
    };

    /**
     * Test si la platforme est de type web
     */
    public isWeb: boolean;

    /**
     * Condition pour afficher les filtres
     */
    public showFiltre = false;
    /**
     * Spiner
     */
    public hideSpinner = false;

    /**
     * action de l'utilisateur
     */
    @Input() public action: string;

    /**
     * Gère l'affichage de l'information
     */
    @Input() public displayComment = false;

    /**
     * Resultat de prospection
     */
    @Input() resultOfProspection: string;

    /**
     * Liste possible Resultat de prospection
     */
    @Input() public resultList: string[];

    /**
     * Commentaire duResultat
     */
    @Input() public commentResult: string;


    searchControl = new FormControl();
    searches = [];
    allSearches = [];
    filteredSearches: Observable<string[]>;
    separatorKeysCodes: number[] = [];
    keyword: any;
    filteredOptions: Observable<any[]>;

    filterType: any;
    placeHolder = 'Rechercher par prenom/nom';
    showFilterField = false;


    constructor(private userProvider: AfricleanHttpUserProvider,
                private prosectProvider: AfricleanHttpProspectionProvider,
                private session: AfricleanSessionProvider,
                private observableProvider: AfricleanObservablesProvider,
                private dateProvider: AfricleanDateProvider,
                private modal: AfricleanModalProvider,
                private config: AfricleanConfigProvider,
                private prospectProvider: AfricleanHttpProspectionProvider,
                private filterProvider: AfricleanFilterProvider) {
    }

    ngOnInit() {
        this.resultList = ['SUCCES', 'ECHEC', 'ANNULER'];
        // Chargement des clients
        //   this.getProspections();

        // Test la platforme
        this.isWeb = this.config.isWeb();

        // Initialise le filtres
        this.initializeFilters();

        // Initialise le choix
        this.choice = [];

        // Dés qu'il y'a un chamgement au niveau des prospections notament une mise a jour,
        // on récharge les données.
        /*this.observableProvider.loadProspection.subscribe(
            (load) => {
                if (load) {
                    this.getProspections();
                }
            }
        );*/
    }

    /**
     * Retourne tous les clients de l'entreprise
     */
    public async getProspections() {
        const userId = await this.session.getItem('userId');
        const roleAuthor = await this.session.getItem('role');
        if (this.action === 'update') {

        } else {
            if (roleAuthor === 'COMMERCIAL') {
                const url = 'prospection/user-prospect';
                const body = {
                    authorCode: userId,
                };
                this.prosectProvider.getProspectByUserAuthor(url, body).subscribe(
                    (prospectionsList) => {
                        this.prospections = prospectionsList;
                        /* this.prospections.forEach(prospection => {
                           prospection.dateStartProspect = this.dateProvider.getStructNormalDate(prospection.dateStartProspect);
                           prospection.dateEndProspect = this.dateProvider.getStructNormalDate(prospection.dateEndProspect);
                           prospection.dateCreateProspect = this.dateProvider.getStructNormalDate(prospection.dateCreateProspect);

                         });*/
                    }
                );
            } else {
                const url = 'prospection/';
                this.prosectProvider.getAll(url).subscribe(
                    (prospectionsList) => {
                        this.prospections = prospectionsList;
                        /*  this.prospections.forEach(prospection => {
                            prospection.dateStartProspect = this.dateProvider.getStructNormalDate(prospection.dateStartProspect);
                            prospection.dateEndProspect = this.dateProvider.getStructNormalDate(prospection.dateEndProspect);
                            prospection.dateCreateProspect = this.dateProvider.getStructNormalDate(prospection.dateCreateProspect);

                          });*/
                    }
                );
            }

        }
    }

    public async addProspect() {
        /* const props = {
           source: 'clients-management',
           role : 'client'
         };*/
        await this.modal.create(AfricleanAddProspectionComponent, 'my-custom-modal-css');
        const data: { displayProspectionConfirmation: boolean } = await this.modal.getData();
        if (data.displayProspectionConfirmation) {
            this.prospections = null;
            // this.getProspections();
            // this.loadProspection.emit(data);
            this.observableProvider.loadProspection.emit(true);

        }
    }


    /**
     * Recupere le choix pour filtrer les données
     * @param value évenement émis
     * @param type type de filtre
     */
    public getFilters(value: any, type: string) {

        const choice = {
            filter1: value.filter1,
            filter2: value.filter2
        };

        if (type === 'adresse') {
            this.adressToSearch = value;
        } else {
            this.choice.push(choice);
        }
        this.filter(choice);
    }

    /**
     * Rafraichir la recherche
     */
    public refresh() {
        this.choice = [];
        this.adressToSearch = null;
        this.getProspections();
    }

    /**
     * Filtre la liste des employés
     */
    private filter(choice: { filter1: string, filter2: string }) {
        // Represente le resultat a un instant t
        const currentProspects: AfricleanProspectionModel[] = this.prospections;
        this.prospections = null;
        this.sort(choice, currentProspects);

    }

    private sort(choice: { filter1: string, filter2: string }, currentProspects: AfricleanProspectionModel[]) {

        if (choice.filter1 === 'Nom') {
            this.prospections = currentProspects;
            if (choice.filter2 === 'Croissant (A -> Z)') {
                this.prospections.sort(this.filterProvider.compareByLastName);
            }
            if (choice.filter2 === 'Décroissant (Z -> A)') {
                this.prospections.sort(this.filterProvider.compareByLastName).reverse();
            }

        }

        if (choice.filter1 === 'Status') {
            this.prospections = [];
            currentProspects.forEach(prospect => {
                if (prospect.stateOfProspection === choice.filter2) {
                    this.prospections.push(prospect);
                }
            });
        }
        if (choice.filter1 === 'Resultat') {
            this.prospections = [];
            currentProspects.forEach(prospect => {
                if (prospect.prospectResult === choice.filter2) {
                    this.prospections.push(prospect);
                }
            });
        }

        if (choice.filter1 === 'Date de creation') {
            this.prospections = currentProspects;
            if (choice.filter2 === 'Croissant') {
                this.prospections.sort(this.filterProvider.compareByCreationDate);
            }
            if (choice.filter2 === 'Décroissant') {
                this.prospections.sort(this.filterProvider.compareByCreationDate).reverse();
            }

        }
    }

    /**
     * Initialise l'objet filters
     */
    private initializeFilters() {
        this.filters = [];
        this.filters.push(
            {
                filter1: 'Nom',
                filter2: ['Croissant (A -> Z)', 'Décroissant (Z -> A)']
            },
            {
                filter1: 'status',
                filter2: ['PLANIFIER', 'ANNULER', 'EFFECTUER']
            },
            {
                filter1: 'Resultat',
                filter2: ['SUCCES', 'ANNULER', 'ECHEC']
            },
            {
                filter1: 'Date de creation',
                filter2: ['Croissant', 'Décroissant']
            },
        );
    }

    public showFiltres() {
        if (this.showFiltre === true) {
            this.showFiltre = false;
        } else {
            this.showFiltre = true;
        }
    }

    public close() {
        this.displayComment = false;
        this.modal.dismiss();
    }

    /**
     * Récupère les raisons
     * @param event événement émis
     */
    public getReasons(event: any) {
        this.commentResult = event.target.value;
    }

    /**
     * Permet d'afficher le texte de résiliaion
     */
    public getResult(event: any) {
        this.resultOfProspection = event.target.value;
        this.displayComment = true;
    }

    /**
     * Modification du resultat de prospection
     */
    public updateProspection() {
        let sateOfProspection: string;
        if (this.resultOfProspection === 'ANNULER') {
            sateOfProspection = 'ANNULER';
        } else {
            sateOfProspection = 'EFFECTUER';
        }
        let body = {};
        body = {
            prospectionCode: this.prospections[0].objectId,
            resultOfProspection: this.resultOfProspection,
            commentResult: this.commentResult,
            state: sateOfProspection
        };
        this.prospectProvider.save('prospection/updat-result', body).subscribe(
            (prospection) => {
                this.modal.dismiss({displayProspectionConfirmation: true});
            }
        );
    }

    sendPosition(position) {
        this.position.emit(position);
    }

    private _filter(name: any): any[] {
        const filterValue = name.toLowerCase();

        // tslint:disable-next-line:max-line-length
        return (this.filterType === 'name') ? (this.prospectionsTMP.filter(item => (item.lastName.toLowerCase().indexOf(filterValue) === 0
            || ((item.firstName) ? item.firstName.toLowerCase().indexOf(filterValue) === 0 : '')))) : ((this.filterType === 'phone') ? (this.prospectionsTMP.filter(item => (item.phoneNumber.toLowerCase().indexOf(filterValue) === 0))) : (this.prospectionsTMP.filter(item => (item.stateOfProspection.toLowerCase().indexOf(filterValue) === 0))));
    }


    searchProspect($event: KeyboardEvent) {
        if (this.keyword.trim() !== '') {
            this.filteredOptions = this.searchControl.valueChanges
                .pipe(
                    startWith(''),
                    map(proche => proche ? this._filter(proche) : this.prospections.slice()));
            this.filteredOptions.subscribe(
                (result) => {
                    this.sendPosition(1);
                    this.prospections = result;
                    // this.clientsTmp = result;
                });
        } else {
            this.prospections = this.prospectionsTMP;
        }

    }

    getFilterType(value) {
        this.filterType = value;
        this.showFilterField = true;
        // tslint:disable-next-line:max-line-length
        this.placeHolder = (value === 'status') ? 'Rechercher par status' : ((value === 'phone') ? 'Rechercher par Tétéphone' : 'Rechercher par Nom/Prénom');
    }

    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({displayPaiement: false});
    }
}
