import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import { AfricleanSessionProvider } from '../../../providers/africlean-session/africlean-session.provider';

@Component({
  selector: 'africlean-abo-payments',
  templateUrl: './africlean-abo-payments.component.html',
  styleUrls: ['./africlean-abo-payments.component.scss'],
})
export class AfricleanAboPaymentsComponent implements OnInit {

  /**
   * Expansion des paiements avenir
   */
  public expansionLaterPayement = false;

  /**
   * Expansion de l'historique des paiements
   */
  public expansionArchive = false;

  public userId: number;
  private role: number;
  private userTMP: any;

  /**
   * Liste des payements
   */
  @Input() public payements: { user: any, payement: any } [];
  @Input() public payementsTMP: { user: any, payement: any } [];

  /**
   * Position de la page courante
   */
  @Input() public currentPage = 1;

  /**
   * Le nombre de clients par pages
   */
  @Input() public nbrPaiementPerPage: any;

  /**
   * Emet la position de la page actuel
   */
  @Output() public position = new EventEmitter<any>();

  /**
   * Reactualise le paiement du client
   */
  @Output() public loadAbonnementPaiement = new EventEmitter<any>();
  @Input() isWeb: boolean;


  constructor(private session: AfricleanSessionProvider) { }

  ngOnInit() {
    this.getUser();
  }

  /**
   * Utilisateur temporaire
   */
  public async getUser() {
    this.role = await this.session.getItem('role');
    this.userId = await this.session.getItem('userId');
    this.userTMP = await this.session.getItem('userTMP');
  }
  /**
   * Etent ou non la partie paiment à venir
   */
  public toogleLaterPayement() {
    this.expansionLaterPayement = !this.expansionLaterPayement;
  }

  /**
   * Etent ou non la partie historique des paiments
   */
  public toogleArchive() {
    this.expansionArchive = !this.expansionArchive;
  }
  sendPosition(position) {
    this.position.emit(position);
  }
}
