import { Component, OnInit, Input } from '@angular/core';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';
import { AfricleanAdressComponent } from '../africlean-adress/africlean-adress.component';
import {AfricleanSessionProvider} from '../../../providers/africlean-session/africlean-session.provider';
import {AfricleanObservablesProvider} from '../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanHttpUserProvider} from '../../../providers/africlean-http/africlean-http-user.provider';
import {StateAdress} from '../../../providers/utils/commonEnum';

@Component({
  selector: 'africlean-adress-list',
  templateUrl: './africlean-adress-list.component.html',
  styleUrls: ['./africlean-adress-list.component.scss'],
})
export class AfricleanAdressListComponent implements OnInit {

  /**
   * Liste des adresses
   */
  @Input() public adresses: any[];

  /**
   * Utilisateur concerné
   */
  @Input() public user: any;

  /**
   * client concerné
   */
  @Input() public userTMP: any;

  /**
   * D'ou est appelé le component
   */
  @Input() public source;

  /**
   * D'ou est appelé le component
   */
  @Input() action: string;

  /**
   * Object du paiement
   */
  @Input() payementObject: any;

  constructor(private modal: AfricleanModalProvider,
              private session: AfricleanSessionProvider,
              private userProvider: AfricleanHttpUserProvider,
              private observableProvider: AfricleanObservablesProvider) { }

  ngOnInit() {
    this.getClient();

    // Dés qu'il y'a un chamgement au nivea des adresses notament une mise a jour,
    // on récharge les données.
    this.observableProvider.loadAdresses.subscribe(
        (load) => {
          if (load) {
            this.getAdresses();
          }
        }
    );
  }

  /**
   * Ferme la modal
   */
  public closeModal() {
    this.modal.dismiss();
  }

  /**
   * Utilisateur temporaire
   */
  public async getClient() {
    this.userTMP = await this.session.getItem('userTMP');
  }


  /**
   * Ajoute une nouvelle adresse
   */
  public async addAdress() {
    await this.modal.dismiss();
    // On appel la modal pour saisir une adresse
    const props = {
      displayButton: true,
      userId: this.userTMP.id
    };
    await this.modal.create(AfricleanAdressComponent, 'my-custom-modal-css', props);
    const data = await this.modal.getData();
    // tslint:disable-next-line:no-unused-expression
    !this.adresses ? this.adresses = [] : '';
    // tslint:disable-next-line: no-unused-expression
    data ? this.adresses.push(data) : '';
  }

  /**
   * Actualise la liste des adresses
   * @param event evenement émis
   */
  public actualizeAdress(homeCode: any) {
    const adressesTMP = [];
    this.adresses.forEach(adress => {
      if (adress.idHome !== homeCode) {
        adressesTMP.push(adress);
      }
    });
    this.adresses = adressesTMP;
  }

  /**
   * Renvoie toutes les adresses
   */
  public async getAdresses() {
    /*const userId = await this.session.getItem('userId');
    const userRole = await this.session.getItem('role');*/
    const url = 'user/adress/' + this.user.id;
    console.log(this.user);
    const body = {
      role: this.userTMP.typeOfUser,
      type: StateAdress.ALL
    };
    this.userProvider.getAdress(url, body).subscribe(
        (adresses) => {
          this.adresses = adresses;
        }
    );
  }
}
