import { Component, OnInit, Input } from '@angular/core';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanDateProvider} from '../../../../providers/africlean-date/africlean-date.provider';

@Component({
  selector: 'africlean-display-comment',
  templateUrl: './africlean-display-comment.component.html',
  styleUrls: ['./africlean-display-comment.component.scss'],
})
export class AfricleanDisplayCommentComponent implements OnInit {

  /**
   * Nom de l'utilisateur
   */
  @Input() public name: string;

  /**
   * Date de publication
   */
  @Input() public date: any;

  /**
   * Commentaire
   */
  @Input() public comment: string;

  /**
   * Theme du commentaire (service, ....)
   */
  @Input() public theme: string;

  constructor(private dateProvider: AfricleanDateProvider) { }

  ngOnInit() {
    const now = new Date();
    const day = this.dateProvider.getDifference(this.date, now);
    this.date = this.dateProvider.getStructDate(day, this.date);
  }

}
