import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AfricleanSubscribeModel} from '../../../../models/abonnement/africlean-subscription.model';
import {AfricleanHttpSubscribeProvider} from '../../../../providers/africlean-http/africlean-http-subscribe.provider';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanObservablesProvider} from '../../../../providers/africlean-observables/africlean-observables.provider';
// tslint:disable-next-line:max-line-length
import {AfricleanAbonnementSubscriptionComponent} from '../../africlean-abonnement-subscription/africlean-abonnement-subscription.component';
import {AfricleanModalProvider} from '../../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanConfigProvider} from '../../../../providers/africlean-config/africlean-config.provider';
import {AfricleanFilterProvider} from '../../../../providers/africlean-filter/africlean-filter.provider';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';

@Component({
    selector: 'app-africlean-abonnement-manager',
    templateUrl: './africlean-abonnement-manager.component.html',
    styleUrls: ['./africlean-abonnement-manager.component.scss'],
})
export class AfricleanAbonnementManagerComponent implements OnInit {
    /**
     * Liste des abonnements
     */
    public abonnements: any[] = [];
    @Input() abonnementsTMP: any[] = [];

    @Input() public subscribesDescription: any[];

    /**
     * Liste des filtres
     */
    public filters: {
        filter1: string,
        filter2: string[]
    }[];

    /**
     * Choix pour filtrer
     */
    public choice: {
        filter1: string,
        filter2: string
    }[];

    /**
     * adresse à rechercher
     */
    public adressToSearch: any;
    /**
     * User
     */
    public user: any;

    /**
     * Test si la platforme est de type web
     */
    public isWeb: boolean;

    /**
     * Condition pour afficher le filtre
     */
    public showFiltre = false;

    showFilterField = false;


    /**
     * role de l'utilisateur
     */
    @Input() public role: string;

    /**
     * Le nombre d'abonnement par pages
     */
    @Input() public nbrSubscribesPerPage: any;

    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;

    /**
     * Emet la position de la page actuel
     */
    @Output() public position = new EventEmitter<any>();

    searchControl = new FormControl();
    searches = [];
    allSearches = [];
    filteredSearches: Observable<string[]>;
    separatorKeysCodes: number[] = [];
    keyword: any;
    filteredOptions: Observable<any[]>;

    filterType: any;
    placeHolder = 'Rechercher par prenom/nom';
    /**
     * Type de client
     */
    @Input() public customerType: number;

    constructor(private abonnementProvider: AfricleanHttpSubscribeProvider,
                private session: AfricleanSessionProvider,
                private observableProvider: AfricleanObservablesProvider,
                private modal: AfricleanModalProvider,
                private config: AfricleanConfigProvider,
                private filterProvider: AfricleanFilterProvider) {
    }

    ngOnInit() {
        // Chargement des abonnement
        // this.getAbonnements();

        // Test la platforme
        this.isWeb = this.config.isWeb();

        // Initialise le filtres
        this.initializeFilters();

        // Initialise le choix
        this.choice = [];
        // Initialisation du role
        this.session.getItem('role').then(
            (role) => {
                this.role = role;
            }
        );
        console.log(this.subscribesDescription);
    }

    /**
     * Retourne tous les abonnements
     */
    public async getAbonnements() {
        const url = 'abonnement/';
        this.abonnementProvider.getAllSubscribeDescription(url).subscribe(
            (abonnementsList) => {
                this.subscribesDescription = abonnementsList;
            }
        );
    }

    /**
     *
     */
    public async addSubscribe() {
        const props = {
            source: 'consult-subscribe-management',
            displayForm: true
        };
        this.modal.create(AfricleanAbonnementSubscriptionComponent, 'my-custom-modal-css', props);
        const data: { displaySubscribeConfirmation: boolean } = await this.modal.getData();
        if (data && data.displaySubscribeConfirmation) {
            this.subscribesDescription = null;
            this.getSubscribesByFilter();

           // this.getAbonnements();
        }
    }


    private getSubscribesByFilter() {
        let body: any = {};
        this.nbrSubscribesPerPage = 8;
        body = {
            limit: 16,
            skip: 0
        };
        const url = 'abonnement/subscribes';
        this.abonnementProvider.save(url, body).subscribe(
            (subscribes) => {
                this.subscribesDescription = subscribes;
            }
        );
    }


    /**
     * Recupere le choix pour filtrer les données
     * @param value évenement émis
     * @param type type de filtre
     */
    public getFilters(value: any, type: string) {

        const choice = {
            filter1: value.filter1,
            filter2: value.filter2
        };

        if (type === 'adresse') {
            this.adressToSearch = value;
        } else {
            this.choice.push(choice);
        }
        this.filter(choice);
    }

    /**
     * Rafraichir la recherche
     */
    public refresh() {
        this.choice = [];
        this.adressToSearch = null;
        this.getAbonnements();
    }

    /**
     * Filtre la liste des abonnements
     */
    private filter(choice: { filter1: string, filter2: string }) {
        // Represente le resultat a un instant t
        const currentAbonnement = this.subscribesDescription;
        this.subscribesDescription = null;
        this.sort(choice, currentAbonnement);

    }

    private sort(choice: { filter1: string, filter2: string }, currentAbonnement: any[]) {

        if (choice.filter1 === 'Nom') {
            this.subscribesDescription = currentAbonnement;
            if (choice.filter2 === 'Croissant (A -> Z)') {
                this.subscribesDescription.sort(this.filterProvider.compareByClientLastName);
            }
            if (choice.filter2 === 'Décroissant (Z -> A)') {
                this.subscribesDescription.sort(this.filterProvider.compareByClientLastName).reverse();
            }

        }

        if (choice.filter1 === 'Status') {
            this.subscribesDescription = [];
            currentAbonnement.forEach(abonnement => {
                if (abonnement.subscribe.stateOfSubscription === choice.filter2) {
                    this.subscribesDescription.push(abonnement);
                }
            });
        }

        if (choice.filter1 === 'Date d\'abonnement') {
            this.subscribesDescription = currentAbonnement;
            if (choice.filter2 === 'Croissant') {
                this.subscribesDescription.sort(this.filterProvider.compareBySubscribeCreationDate);
            }
            if (choice.filter2 === 'Décroissant') {
                this.subscribesDescription.sort(this.filterProvider.compareBySubscribeCreationDate).reverse();
            }

        }
    }


    /**
     * Initialise l'objet filters
     */
    private initializeFilters() {
        this.filters = [];
        this.filters.push(
            {
                filter1: 'Nom',
                filter2: ['Croissant (A -> Z)', 'Décroissant (Z -> A)']
            },
            {
                filter1: 'Status',
                filter2: ['EN COURS', 'VALIDER', 'RESILIEE', 'EN COURS DE RESILIATION']
            },
            {
                filter1: 'Adresse',
                filter2: []
            },
            {
                filter1: 'Date d\'abonnement',
                filter2: ['Croissant', 'Décroissant']
            },
        );
    }

    public showFiltres() {
        if (this.showFiltre === true) {
            this.showFiltre = false;
        } else {
            this.showFiltre = true;
            this.filterType = null;
            this.showFilterField = false;
        }
    }

    sendPosition(position) {
        this.position.emit(position);
    }


    searchSubscribes($event: KeyboardEvent) {
        if (this.keyword.trim() !== '') {
            this.filteredOptions = this.searchControl.valueChanges
                .pipe(
                    startWith(''),
                    map(item => item ? this._filter(item) : this.subscribesDescription.slice()));
            this.filteredOptions.subscribe(
                (result) => {
                    this.sendPosition(1);
                    this.subscribesDescription = result;
                    // this.clientsTmp = result;
                });
        } else {
            this.subscribesDescription = this.abonnementsTMP;
        }

    }

    getFilterType(value) {
        this.filterType = value;
        this.showFilterField = true;
        this.showFiltre = false;
        // tslint:disable-next-line:max-line-length
        this.placeHolder = (value === 'status') ? 'Rechercher par status' : ((value === 'phone') ? 'Rechercher par Tétéphone' : 'Rechercher par Nom/Prénom');
    }
    private _filter(name: any): any[] {
        const filterValue = name.toLowerCase();
        // tslint:disable-next-line:max-line-length
        return (this.filterType === 'name') ? (this.abonnementsTMP.filter(item => (item.client.lastName.toLowerCase().indexOf(filterValue) === 0
            // tslint:disable-next-line:max-line-length
            || item.client.firstName.toLowerCase().indexOf(filterValue) === 0))) : ((this.filterType === 'phone') ? (this.abonnementsTMP.filter(item => (item.client.phoneNumber.toLowerCase().indexOf(filterValue) === 0))) : (this.abonnementsTMP.filter(item => (item.subscribe.stateOfSubscription.toLowerCase().indexOf(filterValue) === 0))));
    }
}
