import {Component} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {Platform, NavController, MenuController} from '@ionic/angular';
import {SplashScreen} from '@ionic-native/splash-screen/ngx';
import {StatusBar} from '@ionic-native/status-bar/ngx';
import {AfricleanConfigProvider} from 'src/providers/africlean-config/africlean-config.provider';
import {AfricleanSessionProvider} from 'src/providers/africlean-session/africlean-session.provider';
import {ActivatedRoute, RouterStateSnapshot} from '@angular/router';
import {AfricleanWebsocketProvider} from 'src/providers/africlean-websocket/africlean-websocket.provider';

// import { AfricleanWebsocketProvider } from 'src/providers/africlean-websocket/africlean-websocket.provider';

@Component({
    selector: 'app-root',
    templateUrl: 'app.component.html',
    styleUrls: ['app.component.scss']
})
export class AppComponent {
    public stateMenu = false;
    public isWeb: boolean;
    constructor(
        private platform: Platform,
        private splashScreen: SplashScreen,
        private statusBar: StatusBar,
        private translate: TranslateService,
        private config: AfricleanConfigProvider,
        private session: AfricleanSessionProvider,
        private navCtrl: NavController,
        private route: ActivatedRoute,
        private menu: MenuController,
        private websocket: AfricleanWebsocketProvider
    ) {
        this.initializeApp();
    }

    initializeApp() {
        this.platform.ready().then(() => {
            this.statusBar.styleDefault();
            this.splashScreen.hide();
            this.configTranslate();
            this.setPlatform();
        });
    }

    public setPlatform() {
        const platforms = this.platform.platforms();
        this.config.setPlatform(platforms[0]);
        this.isWeb = this.config.isWeb();
    }

    /**
     * Configurer le service de traduction
     */
    public configTranslate() {
        this.translate.addLangs(['fr', 'en']);
        this.translate.setDefaultLang('fr');
    }

    menuOpening() {
        this.menu.isOpen().then((state: boolean) => {
            if (state) {
                this.menu.close();
            } else {
                this.menu.open();
            }
        });
    }

    /**
     * Pour changer l'etat du menu et appliquer le transparent gaussien sur l'ensemble de page
     * @param state l'etat du menu
     */
    public menuChanging(state: boolean) {
        this.stateMenu = state;
    }

    /**
     * TODO : Base pour les websocket a utiliser plus tard pour les notification
     * Execute les web socket pour communiquer avec le serveur
     */
    public handleWebSocket() {

        // reçoit l'etat de l'utilisateur (du client par exemple)
        this.websocket.receive('user-state').subscribe(
            (clientState) => {
                this.session.isExist().then(
                    (active) => {
                        if (active) {
                            this.session.getItem('role').then(
                                (role) => {
                                    if (role === 'CLIENT') {
                                        this.session.setItem('isSubscribe', clientState);
                                    }
                                }
                            );
                        }
                    }
                );
            });
    }
}
