import {Component, Input, OnInit} from '@angular/core';
import {AfricleanHttpUserProvider} from '../../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanSessionProvider} from '../../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanObservablesProvider} from '../../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanDateProvider} from '../../../../../providers/africlean-date/africlean-date.provider';
import {AfricleanModalProvider} from '../../../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanConfigProvider} from '../../../../../providers/africlean-config/africlean-config.provider';
import {AfricleanFilterProvider} from '../../../../../providers/africlean-filter/africlean-filter.provider';
import {AfricleanHttpInterventionProvider} from '../../../../../providers/africlean-http/africlean-http-intervention.provider';
import {StateSubscribe, UserRole} from '../../../../../providers/utils/commonEnum';

@Component({
    selector: 'app-users-intervention-list-management',
    templateUrl: './users-intervention-list-management.component.html',
    styleUrls: ['./users-intervention-list-management.component.scss'],
})
export class UsersInterventionListManagementComponent implements OnInit {

    /**
     * Liste des clients
     */
    @Input() public clients: any[] = [];

    /**
     * Intervention en cours
     */
    @Input() public intervention: any;

    /***
     * Auteur de la création de l'utilisateur
     */
    public userCreatedAuthorId: { authorId: number, role: number } = null;

    /**
     * Liste des utilisateurs de l'intervention
     */
    public users: any[] = [];

    /**
     * Liste des employés de l'intervention
     */
    @Input() public employes: any[] = [];

    /**
     * Liste des filtres
     */
    public filters: {
        filter1: string,
        filter2: string[]
    }[];

    /**
     * Choix pour filtrer
     */
    public choice: {
        filter1: string,
        filter2: string
    }[];

    /**
     * adresse à rechercher
     */
    public adressToSearch: {
        country: any,
        region: any,
        commune: any,
        district: any,
        sector: any
    };

    /**
     * Test si la platforme est de type web
     */
    public isWeb: boolean;

    /**
     * Condition pour afficher les filtres
     */
    public showFiltre = false;
    /**
     * Action de l'utilisataion
     */
    @Input() public action: string;

    /**
     * Client selectionner
     */
    @Input() public clientSelectedList: any[] = [];

    /**
     * role de l'utilisateur
     */
    @Input() public role: number;

    configClient = {
        id: 'custom',
        itemsPerPage: 4,
        currentPage: 1,
        totalItems: 0
    };

    configEmploye = {
        id: 'custom1',
        itemsPerPage: 4,
        currentPage: 1,
        totalItems: 0
    };

    constructor(private userProvider: AfricleanHttpUserProvider,
                private session: AfricleanSessionProvider,
                private observableProvider: AfricleanObservablesProvider,
                private dateProvider: AfricleanDateProvider,
                private modal: AfricleanModalProvider,
                private config: AfricleanConfigProvider,
                private interventionProvider: AfricleanHttpInterventionProvider,
                private filterProvider: AfricleanFilterProvider) {
    }

    ngOnInit() {

        // Chargement des clients
       // this.getClients();

        // Test la platforme
        this.isWeb = this.config.isWeb();

        // Initialise le filtres
        //  this.initializeFilters();

        // Initialise le choix
        this.choice = [];

        // Dés qu'il y'a un chamgement au niveau des clients notament une mise a jour,
        // on récharge les données.
        this.observableProvider.loadClient.subscribe(
            (load) => {
                if (load) {
                    this.getClients();
                }
            }
        );
        // Initialisation du role
        this.session.getItem('role').then(
            (role) => {
                this.role = role;
            });
    }


    /**
     * Retourne tous les clients de l'intervention
     */
    public async getClients() {
        /* if (this.intervention && this.intervention.userInterventions &&
             this.intervention.userInterventions.length > 0) {
           this.clients = this.intervention.userInterventions;
           console.log(this.clients);

         }*/
        const body = {
            interventionCode: this.intervention.id
        };
        const url = 'intervention/users';
        this.interventionProvider.getUserIntervention(url, body).subscribe(
            (usersList) => {
                this.users = usersList;
                this.users.forEach(user => {
                    if (user.typeOfUser === UserRole.CLIENT && user.isSubscribe === StateSubscribe.SUBSCRIBE) {
                        this.clients.push(user);
                    } else {
                        this.employes.push(user);
                    }
                });
                console.log(this.employes);
                this.configClient.totalItems = this.clients.length;
                this.configEmploye.totalItems = this.employes.length;
            });
    }

    /**
     * Crée une modal pour enregistrer un nouvel utilisateur
     */
    public async addClient() {
        const props = {
            source: 'clients-management',
            role: UserRole.CLIENT,
            userType: UserRole.CLIENT,
            action: 'create'
        };
    }

    /**
     * Recupere le choix pour filtrer les données
     * @param value évenement émis
     * @param type type de filtre
     */
    public getFilters(value: any, type: string) {

        const choice = {
            filter1: value.filter1,
            filter2: value.filter2
        };

        if (type === 'adresse') {
            this.adressToSearch = value;
        } else {
            this.choice.push(choice);
        }
        this.filter(choice);
    }

    /**
     * Rafraichir la recherche
     */
    public refresh() {
        this.choice = [];
        this.adressToSearch = null;
        this.clients = [];
        this.getClients();
    }

    /**
     * Filtre la liste des employés
     */
    private filter(choice: { filter1: string, filter2: string }) {
        // Represente le resultat a un instant t
        const currentClients: any[] = this.clients;
        this.clients = null;
        this.sort(choice, currentClients);

    }

    private sort(choice: { filter1: string, filter2: string }, currentClients: any[]) {

        if (choice.filter1 === 'Nom') {
            this.clients = currentClients;
            if (choice.filter2 === 'Croissant (A -> Z)') {
                this.clients.sort(this.filterProvider.compareByLastName);
            }
            if (choice.filter2 === 'Décroissant (Z -> A)') {
                this.clients.sort(this.filterProvider.compareByLastName).reverse();
            }

        }

        if (choice.filter1 === 'Status') {
            this.clients = [];
            currentClients.forEach(client => {
                if (client.isSubscribe === choice.filter2) {
                    this.clients.push(client);
                }
            });
        }

        if (choice.filter1 === 'Date d\'inscription') {
            this.clients = currentClients;
            if (choice.filter2 === 'Croissant') {
                this.clients.sort(this.filterProvider.compareByCreationDate);
            }
            if (choice.filter2 === 'Décroissant') {
                this.clients.sort(this.filterProvider.compareByCreationDate).reverse();
            }

        }
    }

    /**
     * Initialise l'objet filters
     */
    private initializeFilters() {
        this.filters = [];
        this.filters.push(
            {
                filter1: 'Nom',
                filter2: ['Croissant (A -> Z)', 'Décroissant (Z -> A)']
            },
            {
                filter1: 'Status',
                filter2: ['NOUVEAU', 'EN COURS', 'ABONNEE', 'RESILIEE']
            },
            {
                filter1: 'Adresse',
                filter2: []
            },
            {
                filter1: 'Date d\'inscription',
                filter2: ['Croissant', 'Décroissant']
            },
        );
    }

    public showFiltres() {
        this.showFiltre = this.showFiltre !== true;
    }

    public selectClients(client: any) {
        this.clientSelectedList.push(client);
    }

    public async validSelection() {
        await this.modal.dismiss(this.clientSelectedList);
    }

    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({displayIntervention: false});
    }

}
