import {Component, OnInit, Input} from '@angular/core';
import {AfricleanSessionProvider} from 'src/providers/africlean-session/africlean-session.provider';
import {AfricleanHttpUserProvider} from 'src/providers/africlean-http/africlean-http-user.provider';
import {AfricleanHttpSubscribeProvider} from 'src/providers/africlean-http/africlean-http-subscribe.provider';
import {AfricleanModalProvider} from 'src/providers/africlean-modal/africlean-modal.provider';
import {AfricleanObservablesProvider} from 'src/providers/africlean-observables/africlean-observables.provider';
import {AfricleanHttpItemListProvider} from '../../../providers/africlean-http/africlean-http-item-list.provider';
import {
    FinanceCategory, FinanceType,
    PaiementMean, RessourceType,
    StateAdress, StatePaiement,
    StateSubscribe,
    UserRole
} from '../../../providers/utils/commonEnum';

@Component({
    selector: 'app-africlean-abonnement-list',
    templateUrl: './africlean-abonnement-list.component.html',
    styleUrls: ['./africlean-abonnement-list.component.scss'],
})
export class AfricleanAbonnementListComponent implements OnInit {

    /**
     * Gère l'affichage de l'information
     */
    public displayInfo = false;

    /**
     * Gère l'affichage du text de resiliation
     */
    public displayResiliation = false;

    /**
     * Cacher le spinner ou non
     */
    public hideSpinner = false;

    /**
     * Etat de l'abonnement
     */
    @Input() public state = 3;

    /**
     * l'id de l'utilisateur (client)
     */
    @Input() public userId;

    /**
     * Source d'appel du component
     */
    @Input() public source = 'consult-abonnement';

    /**
     * l'abonnement du client
     */
    @Input() public currentSubscribeIdHome: number;

    @Input() public subscribe: any;


    /**
     * Liste des abonnements de l'utilisateur
     */
    @Input() public abonnements: any[];

    /**
     * Les id des abonnemnent a desouscrire
     */
    public abonnementToCancel: any;

    /**
     * Abonnment à confirmer
     */
    public abonnementToConfirm: any;
    /**
     * Stock les raisons
     */
    private raisonOfTermination = '';

    @Input() public role: number;
    private amountToPay;
    private payementTranches = [];

    private services = [];
    currentService: any;
    currentMontant: any;
    currentSubscribe: any;

    montants = [];
    currentClient: any;
    currentEntity: any;
    prestationNumber = 2;


    /**
     * Moyenne de payement
     */
    public meanPayement: any;

    /**
     * Montan à payer
     */
    public montant: any;

    /**
     * devise du montant
     */
    public devise: any;

    public meanPayementLists: any[];

    public user: any;

    constructor(private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private subscribeProvider: AfricleanHttpSubscribeProvider,
                private modal: AfricleanModalProvider,
                private itemListProvider: AfricleanHttpItemListProvider,
                private observableProvider: AfricleanObservablesProvider) {
    }

    ngOnInit() {
        if (this.subscribe && this.subscribe.home) {
            this.getSubscribesAdresse();
        } else {
            this.getAdresses();
        }
        this.meanPayementLists = [PaiementMean.ESPECE, PaiementMean.MOBILEMONEY,
            PaiementMean.BANCAIRE, PaiementMean.CHEQUE, PaiementMean.VIREMENT, PaiementMean.OTHER];
        //  this.getItemListValue();
    }

    /**
     * Accepte la résiliation
     */
    public async agree() {
        // TODO : faire une requête pour signaler que le client veut résilier
        // let i;
        let user: any;
        if (this.source === 'consult-abonnement') {
            user = await this.session.getItem('user');
        } else {
            user = await this.session.getItem('user');
            // userId = this.userId;
        }

        if (this.currentSubscribe && this.currentSubscribe.company) {
            const body = {
                client: this.currentClient,
                subscribe: this.currentSubscribe,
                company: this.currentSubscribe.company,
                raisonOfTermination: this.raisonOfTermination,
                auteur: user
            };
            console.log(body);
            // const url = 'abonnement/cancel';
            const url = 'resiliation/save';
            this.subscribeProvider.save(url, body).subscribe(
                (cancelSub) => {
                    //   this.close();
                    const data = {
                        subscribe: this.subscribe,
                        state: StateSubscribe.IN_PROGRESS_TERMINATION
                    };
                    const url1 = 'abonnement/update-state';
                    this.subscribeProvider.save(url1, data).subscribe(
                        (res) => {
                            this.displayInfo = true;
                            this.modal.dismiss({displaySubscribeConfirmation: true});
                            this.observableProvider.loadAbonnement.emit(true);
                        }
                    );
                }
            );
        }

    }

    /**
     * Fermer la modal
     */
    public close() {
        this.displayResiliation = false;
        this.observableProvider.subscriptionRequest.emit(true);
        this.observableProvider.loadAbonnement.emit(true);
        this.modal.dismiss({displaySubscribeConfirmation: true});
    }

    /**
     * Récupère les raisons
     * @param event événement émis
     */
    public getReasons(event: any) {
        this.raisonOfTermination = event.target.value;
    }

    /**
     * Permet d'afficher le texte de résiliaion
     */
    public displayText() {
        this.displayResiliation = true;
    }

    /**
     * Permet de récupérer les abonnements selectionner pour la résiliation
     * @param abonnement Numéro correspondant à l'abonnement
     */
    public resiliation(abonnement: any) {
        //  console.log(abonnement);
        this.abonnementToCancel = abonnement;
        this.currentSubscribe = abonnement.state;
        this.currentClient = this.currentSubscribe.user;
    }

    /**
     * Renvoie toutes les adresses qui ont un abonnement
     */
    public async getAdresses() {
        let userId: number;
        const user = await this.session.getItem('user');
        this.user = await this.session.getItem('user');

        if (this.source === 'consult-abonnement') {
            userId = await this.session.getItem('userId');
        } else {
            userId = this.userId;
        }
        //  const url = 'user/subscribe-client/' + userId;
        const url = 'user/subscribe-adress/' + userId;
        const body = {
            role: UserRole.CLIENT,
            type: this.state
        };
        this.abonnements = [];
        this.userProvider.getAdress(url, body).subscribe(
            (adresses) => {
                this.hideSpinner = true;
                //  this.getAdresseOfStateSubscription(adresses);
                // tslint:disable-next-line:max-line-length
                this.abonnements = (this.source !== 'consult-abonnement') ? this._filterHome(this.currentSubscribeIdHome, adresses) : adresses;
                // tslint:disable-next-line:max-line-length
                /*  (adresses.filter(item => (item.state.toLowerCase().indexOf(this.state.toLowerCase()) === 0 && item.idHome.toLowerCase().indexOf(this.currentSubscribeIdHome.toLowerCase())) === 0 )) :
                  (adresses.filter(item => item.state.toLowerCase().indexOf(this.state.toLowerCase()) === 0));*/
                if (this.state !== StateAdress.IN_PROGRESS_TERMINATION && this.abonnements && this.abonnements.length > 0) {
                    // tslint:disable-next-line:prefer-for-of
                   /* for (let i = 0; i < this.abonnements.length; i++) {
                        this.abonnementToCancel[this.abonnements[i].idHome] = false;
                    }*/
                }
            }
        );
    }

    /**
     * Recupères les adresses pour lesquelles les abonnements correpondant a STATE
     * @param adresses adresses
     */
    private getAdresseOfStateSubscription(adresses: any[]) {

        let i;
        const abonnementsTMP = [];

        adresses.forEach(adresse => {
            if (adresse.state === this.state) {

                if (this.source !== 'consult-abonnement') {
                    if (this.currentSubscribeIdHome === adresse.idHome) {
                        abonnementsTMP.push(adresse);
                    }
                } else {
                    abonnementsTMP.push(adresse);
                }
            }
        });
        this.abonnements = abonnementsTMP;
        // Initialisation des abonnements à résilier
        for (i = 0; i < this.abonnements.length; i++) {
            this.abonnementToCancel[this.abonnements[i].idHome] = false;
        }
    }

    private _filter(state: any, list: any[]): any[] {
        const filterValue = state.toLowerCase();
        return list.filter(item => (item.state.toLowerCase().indexOf(filterValue) === 0));
    }

    private _filterHome(home: any, list: any[]): any[] {
        const filterValue = home.toLowerCase();
        return list.filter(item => (item.idHome.toLowerCase().indexOf(filterValue) === 0));
    }

    /**
     * Confirmation d'un abonnement
     */
    subscribeConfirmation() {
        const body = {
            client: this.currentClient,
            amount: this.currentMontant,
            service: this.currentService,
            subscribe: this.currentSubscribe,
            prestationNumber: this.prestationNumber
        };
        if (this.currentSubscribe && this.currentMontant) {
            const url = 'abonnement/confirm';
            this.subscribeProvider.save(url, body).subscribe(
                (terminationSub) => {
                    if (this.montant && this.meanPayement && this.devise) {
                        const data = {
                            balanceOfAbonnement: null,
                            dateToSubscribePayment: undefined,
                            amountToPay: this.montant,
                            devise: this.devise,
                            paimentMean: this.meanPayement,
                            category: FinanceCategory.EXPLOITATION,
                            paymmentCode: undefined,

                            paiementType: FinanceType.SUBSCRIBE,
                            ressourceType: RessourceType.REVENU,
                            compteNumber: null,
                            title: null,
                            paiementState: StatePaiement.PAID,

                            auteur: this.user,
                            user: this.currentClient,
                            subscribe: this.subscribe,
                            entity: this.currentEntity
                        };
                        this.subscribeProvider.save('finance/save', data).subscribe(
                            (payement) => {
                            }
                        );
                    }
                    this.close();
                }
            );
        }

    }

    /**
     * Confirmation d'une résiliation
     */
    unsubscribeConfirmation() {
        if (this.currentSubscribe && this.currentSubscribe.termination) {
            const body = {
                termination: this.currentSubscribe.termination,
                client: this.currentClient,
                subscribe: this.currentSubscribe
            };
            const url = 'resiliation/confirm';
            this.subscribeProvider.save(url, body).subscribe(
                (terminationSub) => {
                    const data = {
                        subscribe: this.subscribe,
                        state: StateSubscribe.TERMINATION
                    };
                    const url1 = 'abonnement/update-state';
                    this.subscribeProvider.save(url1, data).subscribe(
                        (res) => {
                            this.close();
                        }
                    );
                });
        }

    }

    async getSubscribesAdresse() {
        this.user = await this.session.getItem('user');
        const body = {
            home: this.subscribe.home.id,
            id: this.subscribe.id,
        };
        //  console.log(body);
        const url = 'abonnement/adress';
        this.subscribeProvider.save(url, body).subscribe(
            (adresses) => {
                this.abonnements = adresses;
                if (adresses && adresses.length > 0) {
                    this.services = adresses[0].state.company.services;
                    this.currentEntity = adresses[0].state.company;
                    this.state = adresses[0].state.stateOfSubscription;
                }
                this.hideSpinner = true;
            }
        );
    }

    /**
     * Selectionne l'abonnement à confirmer
     * @param abonnement
     */
    getSubscribeToConfirm(abonnement: any) {
        this.abonnementToConfirm = abonnement;
        this.currentSubscribe = abonnement.state;
        this.currentClient = this.currentSubscribe.user;
    }

    cancelSubscribe() {
        if (this.currentSubscribe && this.currentSubscribe.home) {
            const body = {
                home: this.currentSubscribe.home,
                client: this.currentClient,
                subscribe: this.currentSubscribe
            };
            const url = 'abonnement/cancel';
            this.subscribeProvider.save(url, body).subscribe(
                (cancelSub) => {
                    this.close();
                }
            );
        }
    }

    /**
     * Annuler la demande de  résiliation
     */
    cancelSubscribeTermination() {
        if (this.currentSubscribe && this.currentSubscribe.termination) {
            const body = {
                client: this.currentClient,
                subscribe: this.currentSubscribe,
                termination: this.currentSubscribe.termination.id,
                state: StateSubscribe.SUBSCRIBE
            };
            //  console.log(body);
            const url = 'resiliation/cancel';
            // const url = 'resiliation/delete/' + body.termination;
            this.subscribeProvider.save(url, body).subscribe(
                (cancelSub) => {
                    //   this.close();
                    const data = {
                        subscribe: this.subscribe,
                        state: StateSubscribe.SUBSCRIBE
                    };
                    const url1 = 'abonnement/update-state';
                    this.subscribeProvider.save(url1, data).subscribe(
                        (res) => {
                            this.close();
                        }
                    );
                }
            );
        }

    }


    /**
     * Renvoi la liste de toutes les itemList
     */
    public getItemListValue() {
        const body = {typeOfItemList: 'PRIX ABONNEMENT'};
        this.itemListProvider.getItemListByTypeOfItem('itemList/itemlist-type', body).subscribe(
            (itemList) => {
                if (body.typeOfItemList === 'PRIX ABONNEMENT' || body.typeOfItemList === 'PRIX PAYEMENT') {
                    this.payementTranches = itemList;
                }
            }
        );
    }

    /**
     * Recupere le tranche de payement
     * @param event
     */
    public getPayementTranche(event: any) {
        this.amountToPay = event.target.value;
    }


    /**
     * Renvoi la liste de toutes les tranche des payements
     */
    public getAllPayementTranche() {
        const body = {typeOfItemList: 'PRIX ABONNEMENT'};
        // this.getItemListValue(body);
    }

    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({displayRegistrationConfirmation: false});
    }

    getServices(event: any) {
        const id = event.target.value;
        //   console.log(id.toString());
        const service = this._filterById(id, this.services);
        if (service && service.length > 0) {
            this.currentService = service[0];
            //    this.montants = service[0].amounts;
            this.getMontantByService(this.currentService.id, this.currentEntity.id);
        }
    }

    getMontantByService(serviceId: number, companyId) {
        const data = {
            service: serviceId,
            company: companyId
        };
        this.itemListProvider.save('montant/service', data).subscribe(
            (result) => {
                this.montants = result;
                // console.log(result);
            }
        );
    }

    getMontant(event: any) {
        const id = event.target.value;
        const montant = this._filterById(id, this.montants);
        if (montant && montant.length > 0) {
            this.currentMontant = montant[0];
        }
    }

    /**
     * Recupere moyen payement
     * @param event
     */
    public getMeanPayement(event: any) {
        this.meanPayement = parseInt(event.target.value);
    }


    private _filterById(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.id.toString().indexOf(filterValue) === 0);
    }

    private _filterByCompany(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.id.toString().indexOf(filterValue) === 0);
    }
}
