import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AfricleanHttpProvider } from './africlean-http.provider';

@Injectable()
export class AfricleanHttpPayementProvider extends AfricleanHttpProvider {

    /**
     * Récupère un payement à partir de son identifiant
     * @param url url
     * @param options options
     */
    public getProspectById(url: string, options?: {}): Observable<{ user: any,
                                                                    payement: any}> {
        return this.get(url, options);
    }

    /**
     * Enrégistre d'un payement
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }
    /**
     * Enrégistre d'un payement
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public postPaiment(url: string, body: {}, options?: {}): Observable<any[]> {
        return this.post(url, body, options);
    }

    /**
     * Recupere le reçut
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public getRecutPdf(url: string, body: {}, options?: {}): any {
        return this.post(url, body, options);
    }

    /**
     * Récupère toutes les payement
     * @param url url
     * @param options options
     */
    public getAll(url: string, options?: {}): Observable<{ user: any,
                                                           payement: any}[]> {
        return this.get(url, options);
    }
}
