import {Component, OnInit} from '@angular/core';
import {AfricleanConfigProvider} from '../../../providers/africlean-config/africlean-config.provider';
import {AfricleanObservablesProvider} from '../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanMessageFormComponent} from '../africlean-message-form/africlean-message-form.component';
import {NavController} from '@ionic/angular';
import {AfricleanModalProvider} from '../../../providers/africlean-modal/africlean-modal.provider';

@Component({
    selector: 'app-africlean-slide',
    templateUrl: './africlean-slide.component.html',
    styleUrls: ['./africlean-slide.component.scss'],
})
export class AfricleanSlideComponent implements OnInit {

    carouselOptions = {
        margin: 25,
        loop: true,
        center: true,
        dots: false,
        autoHeight: true,
        autoWidth: true,
        responsive: {
            0: {
                items: 1,
            },
            600: {
                items: 1,
            },
            1000: {
                items: 1,
            }
        }
    };

    imageUrls: any[] = [
        /* {
             url: './assets/icon/slide1.png',
         },*/
      /*  {
            url: './assets/icon/slide2.png',
        },*/
        {
            url: './assets/icon/slide4.png',
        },
        {
            url: './assets/icon/slide3.jpg',
        },
        /*    {
                url: './assets/icon/slide2.png',
                caption: 'The first slide',
                href: '#config'
            },*/
    ];
    height = '410px';
    minHeight: string;
    arrowSize = '10px';
    showArrows = true;
    disableSwiping = false;
    autoPlay = true;
    autoPlayInterval = 9333;
    stopAutoPlayOnSlide = true;
    debug = false;
    backgroundSize = 'cover';
    backgroundPosition = 'center center';
    backgroundRepeat = 'no-repeat';
    showDots = true;
    dotColor = '#FFF';
    showCaptions = true;
    captionColor = '#FFF';
    captionBackground = 'rgba(0, 0, 0, .35)';
    lazyLoad = false;
    hideOnNoSlides = false;
    width = '100%';
    fullscreen = false;
    enableZoom = false;
    enablePan = false;
    noLoop = false;

    /**
     * Test si la platforme est de type web
     */
    public isWeb = true;

    constructor(private config: AfricleanConfigProvider,
                private navCtrl: NavController,
                private modal: AfricleanModalProvider,
                private observableProvider: AfricleanObservablesProvider) {
    }

    ngOnInit() {
        // Test la platforme
        this.isWeb = this.config.isWeb();
        this.height = (this.isWeb) ? '410px' : '270px';

        (this.isWeb) ?  setTimeout(() => {
            this.imageUrls.push('./assets/icon/home12.svg');
        }, 2000) : setTimeout(() => {
            this.imageUrls.push('./assets/icon/homeMobile.svg');
        }, 2000) ;
        // rediriger vers le bon ancre
        this.observableProvider.ongletEmiter.subscribe(
            (onglet) => {
                this.scrollTo(onglet);
            }
        );
    }

    testEvent(event) {
    }

    scrollTo(elementId: string) {
        if (elementId !== 'contact') {
            const y = document.getElementById(elementId).offsetTop - 85;
            document.querySelector('ion-content').scrollToPoint(0, y, 1000);
        }
    }

    async contact() {
        const props = {
            title: 'Message sur africlean'
        };
        await this.modal.create(AfricleanMessageFormComponent, 'my-custom-modal-css', props);
    }

    connect() {
        const props = {
            display: true
        };
        this.navCtrl.navigateRoot('/authentication');
    }
}
