import {Component, ViewChild, OnInit, Input, Output, EventEmitter, Inject, LOCALE_ID, AfterViewInit} from '@angular/core';
import { CalendarComponent } from 'ionic2-calendar/calendar';
import { AlertController } from '@ionic/angular';
import { formatDate } from '@angular/common';
import { AfricleanModalProvider } from 'src/providers/africlean-modal/africlean-modal.provider';
import { ManageCalendarComponent } from './manage-calendar/manage-calendar.component';
import { AfricleanHttpItemListProvider } from '../../../providers/africlean-http/africlean-http-item-list.provider';
import { AfricleanSessionProvider } from '../../../providers/africlean-session/africlean-session.provider';
import { AfricleanHttpUserProvider } from '../../../providers/africlean-http/africlean-http-user.provider';
import { AfricleanDateProvider } from '../../../providers/africlean-date/africlean-date.provider';
import { AfricleanHttpSubscribeProvider } from '../../../providers/africlean-http/africlean-http-subscribe.provider';
import { AfricleanAdressComponent } from '../africlean-adress/africlean-adress.component';
import { AfricleanClientManagerComponent } from '../manager-components/africlean-client-manager/africlean-client-manager.component';
import { AfricleanHttpPlanningProvider } from '../../../providers/africlean-http/africlean-http-planning.provider';
import { AfricleanObservablesProvider } from '../../../providers/africlean-observables/africlean-observables.provider';

import { registerLocaleData } from '@angular/common';
import localeFr from '@angular/common/locales/fr';
import {UserRole} from '../../../providers/utils/commonEnum';
registerLocaleData(localeFr, 'fr');

@Component({
    selector: 'app-africlean-calendar',
    templateUrl: './africlean-calendar.component.html',
    styleUrls: ['./africlean-calendar.component.scss'],
})
export class AfricleanCalendarComponent implements OnInit, AfterViewInit {
    /**
     * Liste des adresses
     */
    @Input() public adresse: any;
    /**
     * Liste des employes
     */
    public employes: any[] = [];

    /**
     * Liste des clients
     */
    public clients: any[] = [];
    /**
     * Id de l'utilisateur
     */
    @Input() public userId: number;

    /**
     * client
     */
    @Input() public client: any;

    /**
     * Liste des employes selectionnés
     */
    public employeSelected: string[] =  [];

    /**
     * Liste des clients selectionnés
     */
    public clientSelected: string[] =  [];


    /**
     * role de l'Employe
     */
    public employeRole: string[];

    /**
     * les differents type d'employé
     */
    public employeTypes: string [];

    /**
     * Formulaire de recherche du client
     */
    @Input() public displayForm = false;

    /**
     * client concerné
     */
    @Input() public userTMP: any;

    /**
     * Liste des abonnements liée a un secteur
     */
    @Input() public subscribes: {
        client: any,
        adresses: any,
        subscribe: any
    }[];

    /**
     * Type de planning
     */
    public planningType: string;

    /**
     * Liste Type de planning (intervention, prospection, Incident, personnaliser)
     */
    public planningTypeList = [];
    /**
     * Permet de savoir c'est une plateforme web
     */
    public isWeb: boolean;

    public event = {
        planningCode: 0,
        title: '',
        desc: '',
        startTime: '',
        endTime: '',
        authorCode: { authorId: 0, role: 0 }
    };

    minDate = new Date().toISOString();

    public eventSource: {
        planningCode: number
        title: string,
        desc: string,
        startTime: Date,
        endTime: Date,
        authorCode: { authorId: number, role: number }
    }[] = [];
    viewTitle;

    calendar = {
        mode: 'month',
        currentDate: new Date(),
    };
    public todayDate = '';

    @ViewChild (CalendarComponent, {static: false}) myCal: CalendarComponent;

    public collapseCard = true;
    /**
     * utilisateur
     */
    public user: any;

    /**
     * Role de l'utilisateur
     */
    @Input() public role: number;

    /**
     * Secteur de plannification
     */
    @Input() sctorId: number;

    /**
     * autheur du planning
     */
    public usersIds: {userId: number, role: number}[] = [];
    /**
     * Liste des plannings
     */
    public plannings: any[] = [];
    /**
     * Verifie si l'utilisateur peut modifier un événement
     */
    public  updated = false;
    @Input() public action = 'planning';


    /**
     * Emet la position de la page actuel
     */
    @Output() public position = new EventEmitter<any>();

    @Input() showClientList = false;

    /**
     * Liste des clients
     */
    @Input() public clientsList: any[];
    /**
     * Emet la position de la page actuel
     */
    @Output() public validselection = new EventEmitter<any>();

    /**
     * Emet l'affiche du composant contant la liste des clients
     */
    @Output() public showClientSelection = new EventEmitter<any>();

    /**
     * Position de la page courante
     */
    @Input()public currentPageClient = 1;

    /**
     * Annonces temporaires
     */
    @Input()public clientsTmp: any[];

    /**
     * La limite pour récuperer les données
     */
    @Input()public limit = 14;

    /**
     * Données à ignorer
     */
    @Input()public skip = 0;

    /**
     * Le nombre d'annonces par pages
     */
    @Input()public nbrClientsPerPage = 7;

    /**
     * Cacher les boutons back et next pour le planning
     */
    public hidden = false;

    public currentMonth = 0;
    public currentYear = 1900;

    constructor(private alertCtrl: AlertController,
                @Inject(LOCALE_ID) private locale: string,
                private modal: AfricleanModalProvider,
                private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private planningProvider: AfricleanHttpPlanningProvider,
                private observableProvider: AfricleanObservablesProvider,
                private subscribeProvider: AfricleanHttpSubscribeProvider,
                private dateProvider: AfricleanDateProvider) { }

    ngOnInit() {
        this.employeTypes = ['MANAGER', 'INTERVENANT', 'COMMERCIAL'];
        this.resetEvent();
        this.observableProvider.loadPlanning.subscribe(
            (load) => {
                if (load) {
                    this.getAllPlanning();
                }
            }
        );
    }

    ngAfterViewInit() {
        const dateCal: Date = this.myCal['_currentDate'];
        this.currentMonth = dateCal.getMonth();
        this.currentYear = dateCal.getFullYear();
        this.getAllPlanning();
    }

    /**
     * Initialisation de l'objet calendar
     */
    private async init() {
        const date = new Date();
        this.todayDate = date.getDay() + '/' + (date.getMonth() + 1) + '/' + date.getFullYear();

        this.planningTypeList = ['INTERVENTION', 'PROSPECTION', 'PERSONALISER', 'INCIDENT'];
        this.eventSource = [];
        this.plannings.forEach(planning => {
            const event = {
                planningCode: planning.id,
                title: planning.title,
                desc: planning.message,
                startTime: new Date(planning.startDate),
                endTime: new Date(planning.endDate),
                authorCode: planning.authorCode
            };
            this.eventSource.push(event);
            this.myCal.loadEvents();

        });
        this.hidden = false;
    }

    public resetEvent() {
        this.event = {
            planningCode: 0,
            title: '',
            desc: '',
            startTime: new Date().toISOString(),
            endTime: new Date().toISOString(),
            authorCode: { authorId: 0, role: 0 }
        };
    }

    // Create the right event format and reload source
    public addEvent() {
        this.getUserIds();
        let body = {};
        body = {
            title: this.event.title,
            message: this.event.desc,
            planningType: this.planningType,
            startDate: new Date(this.event.startTime),
            endDate: new Date(this.event.endTime),
            userIds: this.usersIds,
            authorCode: { authorId: this.userId, role: this.role }
        };
        const url = 'planning/save';

        this.planningProvider.save(url, body).subscribe(
            (planning) => {
                const eventCopy = {
                    planningCode: planning.id,
                    title: this.event.title,
                    authorCode: planning.authorCode,
                    startTime:  new Date(this.event.startTime),
                    endTime: new Date(this.event.endTime),
                    desc: this.event.desc
                };
                this.eventSource.push(eventCopy);
                this.myCal.loadEvents();
                this.resetEvent();
            });
    }



    // Change current month/week/day
    next() {
        this.hidden = true;
        const swiper = document.querySelector('.swiper-container')['swiper'];
        swiper.slideNext();
        if (this.currentMonth === 11) {
            this.currentYear += 1;
            this.currentMonth = 0;
        } else {
            this.currentMonth += 1;
        }
        this.getAllPlanning();
    }

    back() {
        this.hidden = true;
        const swiper = document.querySelector('.swiper-container')['swiper'];
        swiper.slidePrev();
        if (this.currentMonth === 0) {
            this.currentYear -= 1;
            this.currentMonth = 11;
        } else {
            this.currentMonth -= 1;
        }
        this.getAllPlanning();
    }

// Change between month/week/day
    changeMode(mode) {
        this.calendar.mode = mode;
    }

// Focus today
    today() {
        this.calendar.currentDate = new Date();
    }

// Selected date reange and hence title changed
    onViewTitleChanged(title) {
        this.viewTitle = title;
    }

// Calendar event was clicked
    public async onEventSelected(event) {
        // Use Angular date pipe for conversion
        const begin = formatDate(event.startTime, 'medium', this.locale);
        const fin = formatDate(event.endTime, 'medium', this.locale);
        if (event.authorCode.authorId === this.userId || this.role === UserRole.MANAGER || this.role === UserRole.ADMIN) {
            this.updated = true;
        }
        const props = {
            planningCode: event.planningCode,
            start: begin,
            end: fin,
            title: event.title,
            description: event.desc,
            updated: this.updated
        };
        this.modal.create(ManageCalendarComponent, 'my-custom-modal-css', props);
    }

// Time slot was clicked
    public onTimeSelected(ev) {
        const selected = new Date(ev.selectedTime);
        this.event.startTime = selected.toISOString();
        selected.setHours(selected.getHours() + 1);
        this.event.endTime = (selected.toISOString());
    }


    /**
     * Retourn le role des employer à plannifier
     * @param event
     */
    public getEmployeRole(event: any) {
        this.employeRole = event.target.value;
        this.getEmployes();
    }

    /**
     * Liste des employés selectionés
     * @param event
     */
    public getEmployeSelected(event: any) {
        this.employeSelected = event.target.value;
    }

    /**
     * Liste des clients selectionés
     * @param event
     */
    public getClientSelected(event: any) {
        this.clientSelected = event.target.value;
    }

    /**
     * Retourne tous les employer de l'entreprise
     */
    public getEmployes() {
        let i;
        this.employes = [];
        for (i = 0; i < this.employeRole.length; i++) {
            const role = this.employeRole[i];
            const url = 'user/employe/' + role;
            this.userProvider.getAll(url).subscribe(
                (employesList) => {
                    this.employes = this.employes.concat(employesList);
                }
            );

        }
    }



    /**
     * Retourne tous les abonnées et leur abonnement dans un secteur
     */
    public getAbonnements() {
        this.clients = [];
        const url = 'abonnement/sector/' + this.sctorId;
        this.subscribeProvider.getAllSubscribeDescription(url).subscribe(
            (abonnementsList) => {
                this.subscribes = abonnementsList;
                this.subscribes.forEach(subscribe => {
                    this.clients.push(subscribe.client);
                });
            }
        );
    }
    /**
     *
     * @param event
     */
    public getPlannigType(event) {
        this.planningType = event.target.value;
    }

    /**
     * Ajoute une nouvelle adresse
     */
    public async addAdress() {
        const props = {
            displayButton: true,
            action: 'planning'
        };
        await this.modal.create(AfricleanAdressComponent, 'my-custom-modal-css', props);
        const data = await this.modal.getData();
        this.sctorId = data.adresseId.sectorId;
        this.adresse = data.adressInfo;
        this.getAbonnements();
    }
    /**
     * Ajoute une nouvelle adresse
     */
    public async addClients() {
        const props = {
            displayButton: true,
            action: 'planning'
        };
        this.showClientList = true;
        this.showClientSelection.emit(true);
        /*  await this.modal.create(AfricleanClientManagerComponent, 'my-custom-modal-css', props);
          const data = await this.modal.getData();
          this.clients = data;*/
    }

    public getClient(event: any) {
        // this.clients = event.target.value;
    }

    /**
     * Retourn la liste des Id des utilisateurs à plannifier
     */
    public getUserIds() {
        if (this.planningType === 'INTERVENTION' || this.clientSelected.length < 1) {
            this.clients.forEach(client => {
                const userId = {userId: client.id, role: client.typeOfUser};
                this.usersIds.push(userId);
            });
        } else {
            this.clientSelected.forEach(client => {
                const element = client.split('-');
                const userId = {userId: parseInt(element[0]), role: parseInt(element[1])};
                this.usersIds.push(userId);
            });
        }
        this.employeSelected.forEach(employe => {
            const element = employe.split('-');
            const userId = {userId: parseInt(element[0]), role: parseInt(element[1])};
            this.usersIds.push(userId);
        });
    }

    /**
     * Retourn la liste de tous les planning
     */
    public async getAllPlanning() {
        this.userId = await this.session.getItem('userId');
        this.role = await this.session.getItem('role');
        this.user = await this.session.getItem('user');
        const month1 = this.dateProvider.getMonthComplet(this.currentMonth);
        const year = this.currentYear;
        const uri = 'planning/planing-user-month';
        const body = {
            userCode: this.userId,
            role: this.role,
            month: month1,
            year: year + ''
        };
        this.planningProvider.getAllPlanning(uri, body).subscribe(
            (planningList) => {
                this.plannings = planningList;
                this.init();
            });
    }

    clientsSelects(event) {
        this.showClientList = false;
        this.clients = event;
    }
}
