import { Injectable, EventEmitter } from '@angular/core';

@Injectable()

/**
 * Service encapsulant l'ensemble des observables que l'application à besoin
 */
export class AfricleanObservablesProvider {

    /**
     * Observable qui emet un événement lorque la langue change
     */
    public translateEmiter: EventEmitter<string>;

    /**
     * Observable qui emet un événement lorque la valeur de l'input de type radio est renseigné
     */
    public inputRadioEmiter: EventEmitter<string>;

    /**
     * Emet lorsque un utilisateur est connecté
     */
    public isAuth: EventEmitter<boolean>;

    /**
     * Emet lorsque un utilisateur est fait une demande d'abonnement ou de résiliation
     */
    public subscriptionRequest: EventEmitter<boolean>;

    /**
     * Emet lorsque un utilisateur fait agit au niveau de ces adresses
     */
    public loadAdresses: EventEmitter<boolean>;

    /**
     * Emet lorsque un utilisateur fait agit au niveau des incidents
     */
    public loadIncident: EventEmitter<boolean>;

    /**
     * Emet lorsque un abonnement est enregistrer
     */
    public loadAbonnement: EventEmitter<boolean>;

    /**
     * Emet lorsque un payement a été modifier
     */
    public loadPayement: EventEmitter<any>;
    /**
     * Emet lorsque un payement a été modifier
     */
    public loadPayementEmploye: EventEmitter<any>;

    /**
     * Emet lorsque qu'une prospection à éte modifier
     */
    public loadProspection: EventEmitter<boolean>;

    /**
     * Emet lorsque qu'un client à éte modifier
     */
    public loadClient: EventEmitter<boolean>;
    /**
     * Emet lorsque qu'un emloye à éte modifier
     */
    public loadEmploye: EventEmitter<boolean>;

    /**
     * Emet lorsque qu'une planning à éte modifier
     */
    public loadPlanning: EventEmitter<boolean>;

    /**
     * Emet lorsque qu'une intervention à éte ajouter ou modifier
     */
    public loadIntervention: EventEmitter<boolean>;
    /**
     * Emet lorsque qu'une intervention à éte ajouter ou modifier
     */
    public showClientsSelection: EventEmitter<any>;
    public hidClientsSelection: EventEmitter<any>;


    /**
     * Observable qui emet un événement lorque l'on click sur un onglet
     */
    public ongletEmiter: EventEmitter<string>;

    /**
     * Emet lorsque un payement a été modifier
     */
    public loadFinance: EventEmitter<any>;

    constructor() {
        this.translateEmiter = new EventEmitter<string>();
        this.inputRadioEmiter = new EventEmitter<string>();
        this.isAuth = new EventEmitter<boolean>();
        this.subscriptionRequest = new EventEmitter<boolean>();
        this.loadAdresses = new EventEmitter<boolean>();
        this.loadIncident = new EventEmitter<boolean>();
        this.loadAbonnement = new EventEmitter<boolean>();
        this.loadPayement = new EventEmitter<any>();
        this.loadPayementEmploye = new EventEmitter<any>();
        this.loadProspection = new EventEmitter<boolean>();
        this.loadClient = new EventEmitter<boolean>();
        this.loadEmploye = new EventEmitter<boolean>();
        this.loadPlanning = new EventEmitter<boolean>();
        this.loadIntervention = new EventEmitter<boolean>();
        this.showClientsSelection = new EventEmitter<any>();
        this.hidClientsSelection = new EventEmitter<any>();
        this.loadFinance = new EventEmitter<any>();
        this.ongletEmiter = new EventEmitter<string>();
    }
}
