import { Component, OnInit } from '@angular/core';
import { AfricleanIncidentComponent } from '../../africlean-incident/africlean-incident.component';
import { AfricleanObservablesProvider } from '../../../../providers/africlean-observables/africlean-observables.provider';
import { AfricleanModalProvider } from '../../../../providers/africlean-modal/africlean-modal.provider';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-africlean-incident-manager',
  templateUrl: './africlean-incident-manager.component.html',
  styleUrls: ['./africlean-incident-manager.component.scss'],
})
export class AfricleanIncidentManagerComponent implements OnInit {

  constructor( private observableProvider: AfricleanObservablesProvider,
               private modal: AfricleanModalProvider,
               private route: ActivatedRoute) { }

  ngOnInit() {}

   public addIncident() {
      const props = {
        source: 'consult-incident-management',
        displayForm: true,
        collapseCard: false
      };
      this.modal.create(AfricleanIncidentComponent, 'my-custom-modal-css', props);
    }
}
