import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AfricleanHttpProvider } from './africlean-http.provider';

@Injectable()
export class AfricleanHttpSubscribeProvider extends AfricleanHttpProvider {

    /**
     * Récupère un abonnement par son identifiant
     * @param url url
     * @param options options
     */
    public getSubscribeById(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Récupère tous les abonnements
     * @param url url
     * @param options options
     */
    public getAll(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Récupère les adresses d'un 'utilisateur
     * @param url url
     * @param options options
     */
    public getUserHome(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Enrégistre un abonnement ||
     * Enregistre un TerminationSubscribe (Fin d'abonnement)
     * @param url url
     * @param body corp de la requête
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }

    /**
     * Retourne un abonnement à partir d'une adresse
     * @param url url
     * @param options options
     */
    public findSubscribeByAdress(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Retourne la liste de tous les types d'abonnement
     * @param url url
     * @param options options
     */
    public getSubscribeByUser(url: string, options?: {}): Observable<{
        enCours: any[],
        validee: any[],
        enCoursResiliation: any[],
        resilie: any[]
    }> {
        return this.get(url, options);
    }


    /**
     * Renvoie la liste des adresses des abonnement
     * Renvoie la liste des clients
     * Renvoie la liste des abonnement
     * @param url url
     * @param options options
     */
    public getAllSubscribeDescription(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }


    /**
     * Suppression d'un abonnement
     * @param url url
     * @param options options
     */
    public delateSubscribe(url: string, options?: {}): Observable<any> {
        return this.delete(url);
    }
}
