import {Pipe, PipeTransform} from '@angular/core';
import {FinanceType} from '../../providers/utils/commonEnum';

@Pipe({
    name: 'financeType'
})
export class FinanceTypePipe implements PipeTransform {

    transform(value: any, ...args: any[]): any {
        switch (value) {
            case FinanceType.SALAIRE:
                return 'Salaire';
            case FinanceType.CARBURANT:
                return 'Carburant';
            case FinanceType.TELEPHONE:
                return 'Téléphone';
            case FinanceType.PARKING:
                return 'Parking';
            case FinanceType.SOUS_EXPO:
                return 'Sous-exploitation';
            case FinanceType.TAXE:
                return 'Ttaxe';
            case FinanceType.POINT_COLLECT:
                return 'Point de collect';
            case FinanceType.SUBSCRIBE:
                return 'Abonnement';
            case FinanceType.DIVERS:
                return 'Divers';
            case FinanceType.MENSUEL:
                return 'Mensualité';
            case FinanceType.HEURE_SUP:
                return 'Heures Supplementaires';
            case FinanceType.SERVICE_SUP:
                return 'Services suplementaire';
            default:
                return 'Autre';
        }
    }

}
