import { Injectable } from '@angular/core';
import { AfricleanDateProvider } from '../africlean-date/africlean-date.provider';

@Injectable()
export class AfricleanFilterProvider {


    constructor(private dateManager: AfricleanDateProvider) {}

    /**
     * Compare deux object en fonction du champ lastName
     * @param obj1
     * @param obj2
     */
    public compareByLastName(obj1: any, obj2: any) {

        if (obj1.lastName > obj2.lastName) {
            return 1;
        } else {
            return -1;
        }
    }

    /**
     * Compare deux object en fonction du champ lastName
     * @param obj1
     * @param obj2
     */
    public compareByClientLastName(obj1: any, obj2: any) {

        if (obj1.client.lastName > obj2.client.lastName) {
            return 1;
        } else {
            return -1;
        }
    }

    /**
     * Compare deux object en fonction du champ lastName
     * @param obj1
     * @param obj2
     */
    public compareByUserLastName(obj1: any, obj2: any) {

        if (obj1.user.lastName > obj2.user.lastName) {
            return 1;
        } else {
            return -1;
        }
    }

    /**
     * Compare deux object en fonction du champ dateOfCreation
     * @param obj1
     * @param obj2
     */
    public compareByCreationDate(obj1: any, obj2: any) {
        const dateManager = new AfricleanDateProvider();
        return dateManager.formatDateTime(obj1.dateOfCreation)
             - dateManager.formatDateTime(obj2.dateOfCreation);
    }
    /**
     * Compare deux object en fonction du champ dateOfCreation
     * @param obj1
     * @param obj2
     */
    public compareBySubscribeCreationDate(obj1: any, obj2: any) {
        const dateManager = new AfricleanDateProvider();
        return dateManager.formatDateTime(obj1.subscribe.dateOfSbscription)
            - dateManager.formatDateTime(obj2.subscribe.dateOfSbscription);
    }
    /**
     * Compare deux object en fonction du champ dateOfSbscription
     * @param obj1
     * @param obj2
     */
    public compareByIncidentCreationDate(obj1: any, obj2: any) {
        const dateManager = new AfricleanDateProvider();
        return dateManager.formatDateTime(obj1.incident.incidentDate)
            - dateManager.formatDateTime(obj2.incident.incidentDate);
    }
    /**
     * Compare deux object en fonction du champ dateOfPayement
     * @param obj1
     * @param obj2
     */
    public compareByPayementCreationDate(obj1: any, obj2: any) {
        const dateManager = new AfricleanDateProvider();
        return dateManager.formatDateTime(obj1.payement.dateOfPayment)
            - dateManager.formatDateTime(obj2.payement.dateOfPayment);
    }

    /**
     * Compare deux object en fonction du champ dateToSubscribePayment
     * @param obj1
     * @param obj2
     */
    public compareByDatePayed(obj1: any, obj2: any) {
        const dateManager = new AfricleanDateProvider();
        return dateManager.formatDateTime(obj1.payement.dateToSubscribePayment)
            - dateManager.formatDateTime(obj2.payement.dateToSubscribePayment);
    }
}
