import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'customizeDate'
})
export class CustomizeDatePipe implements PipeTransform {

    /**
     * Liste des mois
     */
    private monthsComplet = {
        0: 'Janvier',
        1: 'Fevrier',
        2: 'Mars',
        3: 'Avril',
        4: 'Mai',
        5: 'Juin',
        6: 'Juillet',
        7: 'Août',
        8: 'Septembre',
        9: 'Octobre',
        10: 'Novembre',
        11: 'Decembre',
    };

    transform(value: any, ...args: any[]): any {
        const date = value.split('T');
        const elements = date[0].split('-');
        const index = parseInt(elements[1], 10) >= 0 && parseInt(elements[1], 10) <= 9 ?
            elements[1].slice(1) : elements[1];
        return elements[2] + ' ' + this.monthsComplet[index - 1] + ' ' + elements[0];
    }

}
