import {
    AfterViewInit,
    Component,
    EventEmitter,
    Inject,
    Input,
    LOCALE_ID,
    OnInit,
    Output,
    ViewChild
} from '@angular/core';
import {AfricleanHttpInterventionProvider} from '../../../../providers/africlean-http/africlean-http-intervention.provider';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanConfigProvider} from '../../../../providers/africlean-config/africlean-config.provider';
import {AfricleanFilterProvider} from '../../../../providers/africlean-filter/africlean-filter.provider';
import {AfricleanDateProvider} from '../../../../providers/africlean-date/africlean-date.provider';
import {AfricleanHttpUserProvider} from '../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanModalProvider} from '../../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanObservablesProvider} from '../../../../providers/africlean-observables/africlean-observables.provider';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {CalendarComponent} from 'ionic2-calendar/calendar';
import {ManageCalendarComponent} from '../../africlean-calendar/manage-calendar/manage-calendar.component';
import {formatDate} from '@angular/common';
import {AlertController} from '@ionic/angular';

@Component({
    selector: 'app-africlean-intervention-management',
    templateUrl: './africlean-intervention-management.component.html',
    styleUrls: ['./africlean-intervention-management.component.scss'],
})
export class AfricleanInterventionManagementComponent implements OnInit, AfterViewInit {
    /**
     * Liste des interventions
     */
    @Input() public interventions: any[];
    /**
     *  * Liste des interventions
     */
    @Input() public interventionsTMP: any[];

    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrInterventionsPerPage;
    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;
    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrClientsPerPage;
    /**
     * Position de la page courante
     */
    @Input() public currentPageClient = 1;
    /**
     * Emet la position de la page actuel
     */
    @Output() public position = new EventEmitter<any>();


    /**
     * Source d'appel du component
     */
    @Input() public source = 'report-incident';

    /**
     * l'id de user
     */
    public userId: number;

    /**
     * Role de l'utilisateur
     */
    private role: number;
    /**
     * Liste des filtres
     */
    public filters: {
        filter1: string,
        filter2: string[]
    }[];

    /**
     * Choix pour filtrer
     */
    public choice: {
        filter1: string,
        filter2: string
    }[];

    /**
     * User
     */
    public user: any;

    /**
     * Test si la platforme est de type web
     */
    public isWeb: boolean;

    /**
     * Condition pour afficher le filtre
     */
    public showFiltre = false;

    /**
     * action de l'utilisateur
     */
    @Input() public action: string;
    /**
     * titre du planifiication
     */
    public title: string;
    /**
     * Description du planning
     */
    public desc: any;
    /**
     * Date de début d'intervention
     */
    public startDate: Date;
    /**
     * Date de fin d'intervention
     */
    public endDate: Date;
    /**
     * Affichage du formulaire d'jout d'une intervention
     */
    @Input() public showAddFormuler = false;

    @Input() showClientList = false;


    /**
     * Liste des clients
     */
    @Input() public clientsList: any[] = [];
    /**
     * Emet la position de la page actuel
     */
    @Output() public validselection = new EventEmitter<any>();

    /**
     * Emet la position de la page actuel
     */
    @Output() public positionClient = new EventEmitter<any>();
    /**
     * Emet l'affiche du composant contant la liste des clients
     */
    @Output() public showSelection = new EventEmitter<any>();

    searchControl = new FormControl();
    searches = [];
    allSearches = [];
    filteredSearches: Observable<string[]>;
    separatorKeysCodes: number[] = [];
    keyword: any;
    filteredOptions: Observable<any[]>;

    filterType: any;
    placeHolder = 'Rechercher par prenom/nom';
    public currentMonth = 0;
    public currentYear = 1900;
    viewMonth: any;
    configIntervention = {
        id: 'custom8',
        itemsPerPage: 5,
        currentPage: 1,
        totalItems: 0
    };

    public event = {
        planningCode: 0,
        title: '',
        desc: '',
        startTime: '',
        endTime: '',
        authorCode: {authorId: 0, role: 0}
    };

    minDate = new Date().toISOString();

    public eventSource: {
        planningCode: any
        title: string,
        desc: string,
        startTime: Date,
        endTime: Date,
        authorCode: { authorId: any, role: any }
    }[] = [];
    viewTitle;

    calendar = {
        mode: 'month',
        currentDate: new Date(),
    };
    public todayDate = '';

    @ViewChild(CalendarComponent, {static: false}) myCal: CalendarComponent;

    constructor(private alertCtrl: AlertController,
                @Inject(LOCALE_ID) private locale: string,
                private interventionProvider: AfricleanHttpInterventionProvider,
                private session: AfricleanSessionProvider,
                private config: AfricleanConfigProvider,
                private filterProvider: AfricleanFilterProvider,
                private dateProvider: AfricleanDateProvider,
                private userProvider: AfricleanHttpUserProvider,
                private modal: AfricleanModalProvider,
                private observableProvider: AfricleanObservablesProvider) {
    }

    ngOnInit() {
        // Initialisation du role
        this.session.getItem('role').then(
            (role) => {
                if (this.action !== 'update') {
                    this.role = role;
                    //  this.getInterventions();
                }
            }
        );
        // Test la platforme
        this.isWeb = this.config.isWeb();

        // Initialise le filtres
        this.initializeFilters();

        // Initialise le choix
        this.choice = [];

        this.startDate = new Date();
        this.endDate = new Date();

        const dateCal = new Date();
        this.viewMonth = this.dateProvider.getMonthComplet(dateCal.getMonth());
        this.currentMonth = dateCal.getMonth();
        this.currentYear = dateCal.getFullYear();
        this.getInterventions();
    }

    /**
     * Retourne tous les incidents liés à un user
     */
    public async getInterventions() {
        this.interventions =  [];
        this.interventionsTMP =  [];
        this.role = await this.session.getItem('role');
        this.userId = await this.session.getItem('userId');
        this.user = await this.session.getItem('user');
        const month1 = this.dateProvider.getMonthComplet(this.currentMonth);
        this.viewMonth = month1;
        const year = this.currentYear;
        const uri = 'intervention/intervention-user-month';
        if (this.user && this.user.entity) {
            const role1 = (this.role === 3) ? 2 : this.role;
            const body1 = {
                user: this.user,
                role: role1,
                month: month1,
                year: year + '',
                company: this.user.entity.id
            };
            this.interventionProvider.save(uri, body1).subscribe(
                (interventionList) => {
                    console.log(interventionList);
                    if (interventionList && interventionList.length > 0) {
                        if (this.role === 5 || this.role === 4) {
                            interventionList.forEach(item => {
                                const inter = this._filterByUserType(this.userId, item.userInterventions);
                                if (inter && inter.length > 0) {
                                   this.interventions.push(item);
                                   this.interventionsTMP.push(item);
                               }
                            });
                        } else {
                            this.interventions = interventionList.slice();
                            this.interventionsTMP = interventionList.slice();
                        }
                        this.init(this.interventions);
                        this.configIntervention.totalItems = this.interventions.length;
                    }
                }
            );
        }
    }


    private _filterByUserType(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.user.id.toString().indexOf(filterValue) === 0);
    }
    /**
     * Recupere le choix pour filtrer les données
     * @param value évenement émis
     * @param type type de filtre
     */
    public getFilters(value: any, type: string) {

        const choice = {
            filter1: value.filter1,
            filter2: value.filter2
        };

        if (type === 'adresse') {

        } else {
            this.choice.push(choice);
        }
        this.filter(choice);
    }

    /**
     * Rafraichir la recherche
     */
    public refresh() {
        this.choice = [];
        this.getInterventions();
    }

    /**
     * Filtre la liste des incidents
     */
    private filter(choice: { filter1: string, filter2: string }) {
        // Represente le resultat a un instant t
        const currentInterventions: { state: string, intervention: any }[] = this.interventions;
        this.interventions = null;
        this.sort(choice, currentInterventions);

    }

    // tslint:disable-next-line:max-line-length
    private sort(choice: { filter1: string, filter2: string }, currentInterventions: any[]) {

        if (choice.filter1 === 'Nom') {
            this.interventions = currentInterventions;
            /*if ( choice.filter2 === 'Croissant (A -> Z)') {
              this.incidents.sort(this.filterProvider.compareByLastName);
            }
            if ( choice.filter2 === 'Décroissant (Z -> A)') {
              this.incidents.sort(this.filterProvider.compareByLastName).reverse();
            }*/

        }

        if (choice.filter1 === 'Status') {
            this.interventions = [];
            /*currentInterventions.forEach(intervention => {
              if (incident.incident.stateOfIncident === choice.filter2) {
                this.incidents.push(incident);
              }
            });*/
        }

        if (choice.filter1 === 'Date d\'incident') {
            /* this.incidents = currentIncidents;
             if ( choice.filter2 === 'Croissant') {
               this.incidents.sort(this.filterProvider.compareByIncidentCreationDate);
             }
             if ( choice.filter2 === 'Décroissant') {
               this.incidents.sort(this.filterProvider.compareByIncidentCreationDate).reverse();
             }
       */
        }
    }

    /**
     * Initialise l'objet filters
     */
    private initializeFilters() {
        this.filters = [];
        this.filters.push(
            {
                filter1: 'Nom',
                filter2: ['Croissant (A -> Z)', 'Décroissant (Z -> A)']
            },
            {
                filter1: 'Status',
                filter2: ['NOUVEAU', 'EN COURS', 'TRAITER', 'CLOTURER']
            },
            {
                filter1: 'Adresse',
                filter2: []
            },
            {
                filter1: 'Date d\'incident',
                filter2: ['Croissant', 'Décroissant']
            },
        );
    }

    public showFiltres() {
        this.showFiltre = this.showFiltre !== true;
    }

    public async addIntervention() {
        this.showAddFormuler = !this.showAddFormuler;
    }

    sendPosition(position) {
        this.position.emit(position);
    }

    sendPositionClient(position) {
        this.positionClient.emit(position);
    }

    showClientSelectionClick(event) {
        this.showSelection.emit(event);
        // document.getElementById('show').click();
        //  this.observableProvider.showClientsSelection.emit(true);

    }

    // Change current month/week/day
    next() {
        const swiper = document.querySelector('.swiper-container')['swiper'];
        swiper.slideNext();
        if (this.currentMonth === 11) {
            this.currentYear += 1;
            this.currentMonth = 0;
        } else {
            this.currentMonth += 1;
        }
        this.interventions =  [];
        this.interventionsTMP =  [];
        this.getInterventions();
    }

    back() {
        const swiper = document.querySelector('.swiper-container')['swiper'];
        swiper.slidePrev();
        if (this.currentMonth === 0) {
            this.currentYear -= 1;
            this.currentMonth = 11;
        } else {
            this.currentMonth -= 1;
        }
        this.interventions =  [];
        this.interventionsTMP =  [];
        this.getInterventions();
    }

    /**
     * Initialisation de l'objet payement
     */
    private async init(interventions) {
        const date = new Date();
        this.todayDate = date.getDay() + '/' + (date.getMonth() + 1) + '/' + date.getFullYear();
        this.eventSource = [];
        this.role = await this.session.getItem('role');
        interventions.forEach(item => {
            const event = (this.role  === 2 || this.role  === 3 || this.role === 1) ? {
                planningCode: item.id,
                title: item.title,
                desc: item.message,
                startTime: new Date(item.startDate),
                endTime: new Date(item.endDate),
                authorCode: item.authorCode
            } :  {
                planningCode: item.id,
                title: item.title,
                desc: item.message,
                startTime: new Date(item.startDate),
                endTime: new Date(item.endDate),
                authorCode: item.authorCode
            };
            this.eventSource.push(event);

        });
        this.myCal.loadEvents();
    }

    public resetEvent() {
        this.event = {
            planningCode: 0,
            title: '',
            desc: '',
            startTime: new Date().toISOString(),
            endTime: new Date().toISOString(),
            authorCode: {authorId: 0, role: 0}
        };
    }

// Selected date reange and hence title changed
    onViewTitleChanged(title) {
        this.viewTitle = title;
    }

// Calendar event was clicked
    public async onEventSelected(event) {
        // Use Angular date pipe for conversion
        const begin = formatDate(event.startTime, 'medium', this.locale);
        const fin = formatDate(event.endTime, 'medium', this.locale);
        const props = {
            planningCode: event.planningCode,
            start: begin,
            end: fin,
            title: event.title,
            description: event.desc,
            updated: false
        };
        this.modal.create(ManageCalendarComponent, 'my-custom-modal-css', props);
    }

// Time slot was clicked
    public onTimeSelected(ev) {
        const selected = new Date(ev.selectedTime);
        this.event.startTime = selected.toISOString();
        selected.setHours(selected.getHours() + 1);
        this.event.endTime = (selected.toISOString());
    }

    ngAfterViewInit() {
        this.observableProvider.loadIntervention.subscribe(
            (loadIncident) => {
                if (loadIncident) {
                    this.getInterventions();
                }
            });
    }
}
