import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AfricleanHttpIncidentProvider} from 'src/providers/africlean-http/africlean-http-incident.provider';
import {AfricleanSessionProvider} from 'src/providers/africlean-session/africlean-session.provider';
import {AfricleanObservablesProvider} from 'src/providers/africlean-observables/africlean-observables.provider';
import {AfricleanConfigProvider} from '../../../providers/africlean-config/africlean-config.provider';
import {AfricleanFilterProvider} from '../../../providers/africlean-filter/africlean-filter.provider';
import {AfricleanDateProvider} from '../../../providers/africlean-date/africlean-date.provider';
import {AfricleanHttpUserProvider} from '../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanModalProvider} from '../../../providers/africlean-modal/africlean-modal.provider';
import {map, startWith} from 'rxjs/operators';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {AddressType, InterventionType, StateAction, StateAdress, UserRole} from '../../../providers/utils/commonEnum';

@Component({
    selector: 'africlean-incidents-list',
    templateUrl: './africlean-incidents-list.component.html',
    styleUrls: ['./africlean-incidents-list.component.scss'],
})
export class AfricleanIncidentsListComponent implements OnInit {

    /**
     * Liste des incidents d'un utilisateurs
     */
    @Input() public incidents: any[];
    /**
     * Liste des incidents d'un utilisateurs
     */
    @Input() public incidentsTMP: any[];

    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrIncidentsPerPage = 5;

    /**
     * Source d'appel du component
     */
    @Input() public source = 'report-incident';

    /**
     * l'id de user
     */
    public userId: number;

    /**
     * Role de l'utilisateur
     */
    private role: number;
    /**
     * Liste des filtres
     */
    public filters: {
        filter1: string,
        filter2: string[]
    }[];

    /**
     * Choix pour filtrer
     */
    public choice: {
        filter1: string,
        filter2: string
    }[];

    /**
     * Liste possible des actions
     */
    @Input() public actionList: number[];

    /**
     * Action de modification selectionner
     */
    public actionSelected: number;
    /**
     * adresse à rechercher
     */
    public adressToSearch: {
        country: any,
        region: any,
        commune: any,
        district: any,
        sector: any
    };

    /**
     * User
     */
    public user: any;

    /**
     * Test si la platforme est de type web
     */
    public isWeb: boolean;

    /**
     * Condition pour afficher le filtre
     */
    public showFiltre = false;

    /**
     * action de l'utilisateur
     */
    @Input() public action: string;

    /**
     * role de l'Employe
     */
    public employeRole: number[];

    /**
     * les differents type d'employé
     */
    public employeTypes: any [];
    /**
     * Liste des employes
     */
    public employes: any[] = [];

    /**
     * Liste des employes
     */
    public employeSelectedList: any[] = [];
    /**
     * titre du planifiication
     */
    public title: string;
    /**
     * Description du planning
     */
    public desc: any;
    /**
     * Date de début d'intervention
     */
    public startDate: Date;
    /**
     * Date de fin d'intervention
     */
    public endDate: Date;

    /**
     * autheur du planning
     */
    public usersIds: { userId: number, role: number }[] = [];


    /**
     * Liste des employes selectionnés
     */
    public employeSelected: number[] = [];
    /**
     * Leader du group d'intervention
     */
    @Input() public employeSelectedLeader: any;

    @Input() public leaderCode: { userId: number; role: number };


    /**
     * Le nombre d'incidents par pages
     */
    @Input() public nbrIncidentPerPage: any;

    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;

    /**
     * Controler le click du bouton valider
     */
    public hideValidateButton = false;

    /**
     * Emet la position de la page actuel
     */
    @Output() public position = new EventEmitter<any>();
    hidButton = false;

    searchControl = new FormControl();
    searches = [];
    allSearches = [];
    filteredSearches: Observable<string[]>;
    separatorKeysCodes: number[] = [];
    keyword: any;
    filteredOptions: Observable<any[]>;

    filterType: any;
    placeHolder = 'Rechercher par prenom/nom';
    showFilterField = false;

    constructor(private incidentProvider: AfricleanHttpIncidentProvider,
                private session: AfricleanSessionProvider,
                private config: AfricleanConfigProvider,
                private filterProvider: AfricleanFilterProvider,
                private dateProvider: AfricleanDateProvider,
                private userProvider: AfricleanHttpUserProvider,
                private modal: AfricleanModalProvider,
                private observableProvider: AfricleanObservablesProvider) {
    }

    ngOnInit() {
        // Initialisation du role
        this.session.getItem('role').then(
            (role) => {
                if (this.action !== 'update') {
                    this.role = role;
                    // this.getIncidents();
                }
            }
        );

        // tslint:disable-next-line:max-line-length triple-equals
        this.actionList = (this.role === UserRole.MANAGER || UserRole.ADMIN) ? [StateAction.PLANIFIED, StateAction.CLOTURED,
            StateAction.INPROGRESS, StateAction.TRAITED] : [StateAction.PLANIFIED, StateAction.INPROGRESS, StateAction.TRAITED];

        this.employeTypes = [UserRole.INTERVENANT, UserRole.COMMERCIAL, UserRole.MANAGER];

        // Test la platforme
        this.isWeb = this.config.isWeb();

        // Initialise le filtres
        this.initializeFilters();

        // Initialise le choix
        this.choice = [];

        this.startDate = new Date();
        this.endDate = new Date();
        this.observableProvider.loadIncident.subscribe(
            (loadIncident) => {
                if (loadIncident) {
                    this.getIncidentsByFilter();
                }
            });
    }

    private getIncidentsByFilter() {
        let body: any = {};
        this.nbrIncidentPerPage = 5;
        body = {
            limit: 10,
            skip: 0
        };
        const url = 'incident/incidents';
        this.incidentProvider.save(url, body).subscribe(
            (incidents) => {
                this.incidents = incidents;
            }
        );
    }

    /**
     * Retourne tous les incidents liés à un user
     */
    public async getIncidents() {
        let userId: string;
        this.role = await this.session.getItem('role');
        if (this.action === 'update') {

        } else {
            if (this.role === UserRole.CLIENT) {
                userId = await this.session.getItem('userId');
                const url = 'incident/user-incident/' + userId;
                this.incidentProvider.getIncidentByIdUser(url).subscribe(
                    (incidentsList) => {
                        this.incidents = incidentsList;
                    }
                );
            } else {
                const url = 'incident/';
                this.incidentProvider.getAllIncidentsWithDescription(url).subscribe(
                    (incidentsList) => {
                        this.incidents = incidentsList;
                    }
                );
            }
        }

    }


    /**
     * Recupere le choix pour filtrer les données
     * @param value évenement émis
     * @param type type de filtre
     */
    public getFilters(value: any, type: string) {

        const choice = {
            filter1: value.filter1,
            filter2: value.filter2
        };

        if (type === 'adresse') {
            this.adressToSearch = value;
        } else {
            this.choice.push(choice);
        }
        this.filter(choice);
    }

    /**
     * Rafraichir la recherche
     */
    public refresh() {
        this.choice = [];
        this.adressToSearch = null;
        if (this.role !== UserRole.CLIENT) {
            this.getIncidentsByFilter();
        } else {
            this.getIncidents();
        }
    }

    /**
     * Filtre la liste des incidents
     */
    private filter(choice: { filter1: string, filter2: string }) {
        // Represente le resultat a un instant t
        const currentIncidents: { client: any, incident: any }[] = this.incidents;
        this.incidents = null;
        this.sort(choice, currentIncidents);

    }

    private sort(choice: { filter1: string, filter2: string },
                 currentIncidents: { client: any, incident: any }[]) {

        if (choice.filter1 === 'Nom') {
            this.incidents = currentIncidents;
            if (choice.filter2 === 'Croissant (A -> Z)') {
                this.incidents.sort(this.filterProvider.compareByLastName);
            }
            if (choice.filter2 === 'Décroissant (Z -> A)') {
                this.incidents.sort(this.filterProvider.compareByLastName).reverse();
            }

        }

        if (choice.filter1 === 'Status') {
            this.incidents = [];
            currentIncidents.forEach(incident => {
                if (incident.incident.stateOfIncident === choice.filter2) {
                    this.incidents.push(incident);
                }
            });
        }

        if (choice.filter1 === 'Date d\'incident') {
            this.incidents = currentIncidents;
            if (choice.filter2 === 'Croissant') {
                this.incidents.sort(this.filterProvider.compareByIncidentCreationDate);
            }
            if (choice.filter2 === 'Décroissant') {
                this.incidents.sort(this.filterProvider.compareByIncidentCreationDate).reverse();
            }

        }
    }

    /**
     * Initialise l'objet filters
     */
    private initializeFilters() {
        this.filters = [];
        this.filters.push(
            {
                filter1: 'Nom',
                filter2: ['Croissant (A -> Z)', 'Décroissant (Z -> A)']
            },
            {
                filter1: 'Status',
                filter2: ['NOUVEAU', 'EN COURS', 'TRAITER', 'CLOTURER']
            },
            {
                filter1: 'Adresse',
                filter2: []
            },
            {
                filter1: 'Date d\'incident',
                filter2: ['Croissant', 'Décroissant']
            },
        );
    }

    public showFiltres() {
        this.showFiltre = this.showFiltre !== true;
    }

    /**
     * Permet d'afficher le texte de résiliaion
     */
    public getActionSelect(event: any) {
        this.actionSelected = parseInt(event.target.value);
    }


    /**
     * Retourn le role de l'Employé à payer
     * @param event
     */
    public getEmployeRole(event: any) {
        this.employeRole = event.target.value;
        this.getEmployes();
    }

    /**
     * les employes intervenant
     * @param event
     */
    public getEmploye(event: any) {
        // this.employes = event.target.value;
    }

    /**
     * Retourne tous les employer de l'entreprise
     */
    public async getEmployes() {
        this.employes = [];
        let body: any = {};
        this.user = await this.session.getItem('user');
        const entity = (this.user) ? this.user.entity : null;
        body = {
            entity
        };
        this.userProvider.save('user/employe/entity', body).subscribe(
            (employes) => {
                if (employes && employes.length > 0) {
                    console.log(employes);
                    console.log(this.employeRole);
                    this.employeRole.forEach(item => {
                        const user = this._filterBYRole(item, employes);
                        this.employes = (this.employes.length > 0) ? this.employes.concat(user) : user;
                    });
                }
            }
        );
    }

    private _filterBYRole(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.typeOfUser.toString().indexOf(filterValue) === 0);
    }

    private _filterById(value: any, list: any[]): any[] {
        const filterValue = value.toString();
        return list.filter(option => option.id.toString().indexOf(filterValue) === 0);
    }

    /**
     * Modification du resultat de l'incident
     */
    public async updateIncident() {
       // this.getUserIds();
        let url = 'incident/';
        const body = {};
        this.hidButton = true;
        if (this.actionSelected === StateAction.PLANIFIED && this.employeSelectedList.length > 0) {
            const author = await this.session.getItem('user');
            const users = this.employeSelectedList.concat(this.incidents[0].client);

            const data = {
                title: this.title,
                interventionType: InterventionType.INCIDENT,
                message: this.desc,
                company: author.entity,
                startDate: this.startDate,
                endDate: this.endDate,
                interventionLeader: this.employeSelectedLeader,
                intervenant: users,
                author,
                incident: this.incidents[0],
                adressCode: {adress: this.incidents[0].home, adressType: AddressType.HOME}
            };


            url = 'intervention/' + 'save';
            console.log(data);
            this.hideValidateButton = true;
            this.incidentProvider.save(url, data).subscribe(
                (incident) => {
                    this.updateState('incident/');
                 //   this.observableProvider.loadIncident.emit(true);
                    this.modal.dismiss({displayIncidentConfirmation: true});
                });

        } else {
            this.updateState(url);
        }

    }

    updateState(url) {
       const body = {
            stateOfIncident: this.actionSelected
        };
       url = url + 'update-state/' + this.incidents[0].id;
       this.incidentProvider.save(url, body).subscribe(
            (incident) => {
                this.modal.dismiss({displayIncidentConfirmation: true});
            });
    }

    /**
     * Liste des employés selectionés
     * @param event
     */
    public getEmployeSelected(event: any) {
        this.employeSelected = event.target.value;
        this.getUsers();
        console.log(this.employeSelectedList);
    }

    getUsers() {
        this.employeSelectedList = [];
        this.employeSelected.forEach(item => {
            const user = this._filterBYRole(item, this.employes);
            this.employeSelectedList = (this.employeSelectedList.length > 0) ? this.employeSelectedList.concat(user) : user;
        });
    }


    /**
     * Retourn la liste des Id des utilisateurs à plannifier
     */
    public getUserIds() {
        /* this.employeSelected.forEach(employe => {
            const element = employe.split('-');
            const userId = {userId: element[0], role: element[1]};
            this.usersIds.push(userId);
        });
        if (this.employeSelectedLeader !== '') {
            const leader = this.employeSelectedLeader.split('-');
            this.leaderCode = {userId: leader[0], role: leader[1]};
        }*/
    }

    public getEmployeSelectedLeader(event: any) {
        const user = this._filterById(event.target.value, this.employeSelectedList);
        if (user && user.length > 0) {
            this.employeSelectedLeader = user[0];
            console.log(this.employeSelectedLeader);
        }
    }

    sendPosition(position) {
        this.position.emit(position);
    }

    searchIncident($event: KeyboardEvent) {
        if (this.keyword.trim() !== '') {
            this.filteredOptions = this.searchControl.valueChanges
                .pipe(
                    startWith(''),
                    map(item => item ? this._filter(item) : this.incidents.slice()));
            this.filteredOptions.subscribe(
                (result) => {
                    this.sendPosition(1);
                    this.incidents = result;
                    // this.clientsTmp = result;
                });
        } else {
            this.incidents = this.incidentsTMP;
        }

    }

    getFilterType(value) {
        this.filterType = value;
        this.showFilterField = true;
        this.showFiltre = false;
        // tslint:disable-next-line:max-line-length
        this.placeHolder = (value === 'status') ? 'Rechercher par status' : ((value === 'phone') ? 'Rechercher par Tétéphone' : 'Rechercher par Nom/Prénom');
    }

    private _filter(name: any): any[] {
        const filterValue = name.toLowerCase();
        // tslint:disable-next-line:max-line-length
        return (this.filterType === 'name') ? (this.incidentsTMP.filter(item => (item.client.lastName.toLowerCase().indexOf(filterValue) === 0
            // tslint:disable-next-line:max-line-length
            || item.client.firstName.toLowerCase().indexOf(filterValue) === 0))) : ((this.filterType === 'phone') ? (this.incidentsTMP.filter(item => (item.client.phoneNumber.toLowerCase().indexOf(filterValue) === 0))) : (this.incidentsTMP.filter(item => (item.incident.stateOfIncident.toLowerCase().indexOf(filterValue) === 0))));
    }

    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({displayIncident: false});
    }

}
