import {Component, Input, OnInit} from '@angular/core';
import {AfricleanHttpItemListProvider} from '../../../../../../providers/africlean-http/africlean-http-item-list.provider';
import {AfricleanDataProvider} from '../../../../../../providers/africlean-data/africlean-data.provider';
import {map, mergeMap} from "rxjs/operators";

@Component({
    selector: 'app-africlean-add-item-list',
    templateUrl: './africlean-add-item-list.component.html',
    styleUrls: ['./africlean-add-item-list.component.scss'],
})
export class AfricleanAddItemListComponent implements OnInit {
    @Input() itemType: string;
    errorMsg: any;
    @Input() action: string;

    actionItem = 'create';

    itemListType: any;
    value: any;
    public itemSetList = [];
    @Input() public showAddFormuler = false;
    @Input() public itemList = [];
    @Input() public payementTranches = [];
    @Input() public meanPayementList = [];

    expansionPaiementMontant = false;
    expansionPaiement = false;
    expansionMeanPaiement = false;
    expansionIncidentType = false;

    public currentItem;

    constructor(private itemListProvider: AfricleanHttpItemListProvider,
                private data: AfricleanDataProvider) {
    }

    ngOnInit() {
        this.itemSetList = this.data.getItemsetList();
    }

    /**
     * Renvoi la liste de toutes les itemList
     */
    public getItemListValue(body: { typeOfItemList: string }) {
        this.itemListProvider.getItemListByTypeOfItem('itemList/itemlist-type', body).pipe(
            map(
                (itemList) => {
                    const payementTMP = [];
                    if (body.typeOfItemList === 'PRIX ABONNEMENT' || body.typeOfItemList === 'PRIX PAYEMENT') {
                        this.payementTranches = itemList;

                    } else if (body.typeOfItemList === 'MOYENNE DE PAYEMENT') {
                        this.meanPayementList = itemList;
                    }
                    this.itemList = itemList;
                }
            ),
            mergeMap(
                (_) => {
                    return this.itemList;
                }
            )
        ).subscribe(
            (itemList) => {
            }
        );
    }

    add() {
        const data = {
            typeOfItemList: this.itemListType,
            itemListValue: this.value
        };
        const url = 'itemList/save';
        if (this.itemListType && this.value && this.value.trim() !== '') {
            this.itemListProvider.save(url, data).subscribe(
                (result) => {
                    this.showAddFormuler = false;
                }
            );
        }

    }

    updateItem() {
        if (this.currentItem.objectId && this.value && this.value.trim() !== '') {
            const url = 'itemList/update/' + this.currentItem.objectId;
            this.currentItem.itemListValue = this.value;
            if (this.itemListType && this.value) {
                this.itemListProvider.putItem(url, this.currentItem).subscribe(
                    (result) => {
                        this.showAddFormuler = false;
                    }
                );
            }

        }
    }

    getItemTypeList(event) {
        this.itemListType = event.target.value;
    }

    addList() {
        this.showAddFormuler = !this.showAddFormuler;
    }

    toogleSubscrbeMontant() {
        this.expansionPaiementMontant = !this.expansionPaiementMontant;
        const body = {typeOfItemList: 'PRIX ABONNEMENT'};
        this.getItemListValue(body);
        this.expansionPaiement = false;
        this.expansionIncidentType = false;
        this.expansionMeanPaiement = false;
    }

    tooglePaiement() {
        this.expansionPaiement = !this.expansionPaiement;
        const body = {typeOfItemList: 'PRIX PAYEMENT'};
        this.getItemListValue(body);
        this.expansionPaiementMontant = false;
        this.expansionIncidentType = false;
        this.expansionMeanPaiement = false;
    }

    toogleMeanPaiement() {
        this.expansionMeanPaiement = !this.expansionMeanPaiement;
        const body = {typeOfItemList: 'MOYENNE DE PAYEMENT'};
        this.getItemListValue(body);
        this.expansionPaiement = false;
        this.expansionIncidentType = false;
        this.expansionPaiementMontant = false;
    }

    toogleIncident() {
        this.expansionIncidentType = !this.expansionIncidentType;
        const body = {typeOfItemList: 'INCIDENT TYPE'};
        this.getItemListValue(body);
        this.expansionPaiement = false;
        this.expansionMeanPaiement = false;
        this.expansionPaiementMontant = false;
    }

    update(item: any, itemType: any) {
        this.showAddFormuler = !this.showAddFormuler;
        this.itemListType = item.typeOfItemList;
        this.value = item.itemListValue;
        this.actionItem = 'update';
        this.currentItem = item;
    }

    delete(item: any, itemType: any) {
        if (item.objectId) {
            const url = 'itemList/delete/' + item.objectId;
            this.itemListProvider.delate(url).subscribe(
                (result) => {
                    this.itemList = [];
                    const body = {typeOfItemList: item.typeOfItemList};
                    this.getItemListValue(body);
                }
            );
        }

    }
}
