import {Component, Input, OnInit} from '@angular/core';
import {NavController} from '@ionic/angular';
// tslint:disable-next-line:max-line-length
import {AfricleanControlRegistrationProvider} from '../../../../../providers/africlean-control-fields/africlean-control-registration.provider';
import {TranslateService} from '@ngx-translate/core';
import {AfricleanModalProvider} from '../../../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanHttpUserProvider} from '../../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanUserModel} from '../../../../../models/africlean-user.model';
// tslint:disable-next-line:max-line-length
import {AfricleanAbonnementSubscriptionComponent} from '../../../africlean-abonnement-subscription/africlean-abonnement-subscription.component';

@Component({
    selector: 'app-africlean-add-abonnement',
    templateUrl: './africlean-add-abonnement.component.html',
    styleUrls: ['./africlean-add-abonnement.component.scss'],
})
export class AfricleanAddAbonnementComponent implements OnInit {
    /**
     * Message d'erreur à afficher si un problème survient
     */
    public errorMsg: string;

    /**
     * Etat du spinner de chargement
     */
    public hideSpinner = true;

    /**
     * Homme ou Femme
     */
    public gender = 'Homme';

    /**
     * lastname de l'utilisateur
     */
    public lastName: string;

    /**
     * firstname de l'utilisateur
     */
    public firstName: string;

    /**
     *  Adresse mail de l'utilisateur
     */
    public email: string;

    /**
     * Numéro de téléphone de la personne
     */
    public phone: string;

    /**
     * Numéro de téléphone de la personne
     */
    public phoneCode = '224';

    /**
     * id du client
     */
    public clientId: string;
    /**
     * Client qui doit être abonnée
     */
    public client: AfricleanUserModel;

    constructor(private navCtrl: NavController,
                private controlFiels: AfricleanControlRegistrationProvider,
                private translateService: TranslateService,
                private httpUser: AfricleanHttpUserProvider,
                private modal: AfricleanModalProvider) {
    }


    /**
     * Initialisation de la page
     */
    ngOnInit() {
    }

    /**
     * Enregistre un nouvel abonnement
     */
    public async registration() {
        this.phone = '+' + this.phoneCode + ' ' + this.phone.replace(' ', '');
        let body = {};
        body = {
            firstName: this.firstName,
            lastName: this.lastName,
            gender: this.gender,
            phoneNumber: this.phone
        };
        this.getClient(body).subscribe(
            (user) => {
                this.client = user;
                this.modal.dismiss({displayRegistrationConfirmation: true});
                const props = {
                    source: 'consult-subscribe-management',
                    userId: user.objectId
                };
                this.modal.create(AfricleanAbonnementSubscriptionComponent, 'my-custom-modal-css', props);
                /* const data: { displayAbonnementConfirmation: boolean } = this.modal.getData();
                 if (data.displayAbonnementConfirmation) {

                   }*/
            }
        );
    }


    /**
     * Close la modal
     */
    public closeModal() {
        this.modal.dismiss({displaySubscribeConfirmation: true});
    }

    /**
     * Retourn  un client en fonction du argument body
     * @param body corps de la requête
     */
    private getClient(body: {}) {
        return this.httpUser.getClient('user/client/login', body);
    }

    private _filter(value: string, list: any[]): any[] {
        const filterValue = value;
        return list.filter(option => option.dialCode.indexOf(filterValue) === 0);
    }

    telInputObject(obj) {
        const list = obj.p;
        if (this.phone) {
            const country = this._filter(this.phoneCode.replace('+', ''), list);
            if (country.length > 0) {
                obj.setCountry(country[0].iso2);
                this.phoneCode = country[0].dialCode;
            }

        }

    }

    getNumber(event) {
    }

    onCountryChange(event: any) {
        const phone = this.phone;
        const p2 = phone.split(' ');
        this.phoneCode = event.dialCode;
    }

}
