import { Pipe, PipeTransform } from '@angular/core';
import {UserGender} from '../../providers/utils/commonEnum';

@Pipe({
  name: 'userGender'
})
export class UserGenderPipe implements PipeTransform {

  transform(value: any, ...args: any[]): any {
    switch (value) {
      case UserGender.HOMME:
        return 'Homme';
      case UserGender.FEMME:
        return 'Femme';
      default:
        return 'Autre';
    }
  }

}
