import { Component, OnInit } from '@angular/core';
import {AfricleanModalProvider} from '../../../../providers/africlean-modal/africlean-modal.provider';

@Component({
  selector: 'app-africlean-legal-mention',
  templateUrl: './africlean-legal-mention.component.html',
  styleUrls: ['./africlean-legal-mention.component.scss'],
})
export class AfricleanLegalMentionComponent implements OnInit {
  constructor( private modal: AfricleanModalProvider) { }

  ngOnInit() {}

  /**
   * Close la modal
   */
  public closeModal() {
    this.modal.dismiss({ displayRegistrationConfirmation: false });
  }

}
