import {Component, Input, OnInit} from '@angular/core';
import {AfricleanSessionProvider} from '../../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanDateProvider} from '../../../../../providers/africlean-date/africlean-date.provider';
import {AfricleanModalProvider} from '../../../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanHttpUserProvider} from '../../../../../providers/africlean-http/africlean-http-user.provider';
import {AfricleanObservablesProvider} from '../../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanHttpLocalisationProvider} from '../../../../../providers/africlean-http/africlean-http-localisation.provider';

@Component({
    selector: 'app-africlean-localisation-manager',
    templateUrl: './africlean-localisation-manager.component.html',
    styleUrls: ['./africlean-localisation-manager.component.scss'],
})
export class AfricleanLocalisationManagerComponent implements OnInit {
    /**
     * Expansion des pays
     */
    public expansionCountry = false;
    /**
     * Expansion des communes
     */
    public expansionCommune = false;

    /**
     * Expansion des Regions
     */
    public expansionRegion = false;

    /**
     * Expansion des quartiers
     */
    public expansionDistrict = false;

    /**
     * Expansion des secteurs
     */
    public expansionSector = false;

    /**
     * Expansion des maisons
     */
    public expansionHome = false;

    /**
     * Expansion des maisons
     */
    public expansionuserHome = false;

    public userId: number;
    private role: number;
    private userTMP: any;
    /**
     * Affichage du formulaire d'jout d'une localisation
     */
    @Input() public showAddFormuler = false;
    public countries = [];
    public regions = [];
    public communes = [];
    public districts = [];
    public sectors = [];

    private homes = [];
    private userHomes = [];


    constructor(private dateManager: AfricleanDateProvider,
                private modal: AfricleanModalProvider,
                private session: AfricleanSessionProvider,
                private userProvider: AfricleanHttpUserProvider,
                private localisation: AfricleanHttpLocalisationProvider,
                private observableProvider: AfricleanObservablesProvider) {
    }

    ngOnInit() {
        this.getUser();
    }

    /**
     * Utilisateur temporaire
     */
    public async getUser() {
        this.role = await this.session.getItem('role');
        this.userId = await this.session.getItem('userId');
    }

    /**
     * Etent ou non la partie région
     */
    public toogleCountry() {
        this.expansionCountry = !this.expansionCountry;
        this.expansionRegion = false;
        this.expansionCommune = false;
        this.expansionSector = false;
        this.expansionDistrict = false;
        this.expansionHome = false;
        this.showAddFormuler = false;
        this.getCountries();
    }

    /**
     * Etent ou non la partie commune
     */
    public toogleTown() {
        this.expansionCommune = !this.expansionCommune;
        this.expansionRegion = false;
        this.expansionCountry = false;
        this.expansionSector = false;
        this.expansionDistrict = false;
        this.expansionHome = false;
        this.showAddFormuler = false;
        this.getCommunes();
    }

    /**
     * Etent ou non la partie Regionn
     */
    public toogleRegion() {
        this.expansionRegion = !this.expansionRegion;
        this.expansionCommune = false;
        this.expansionCountry = false;
        this.expansionSector = false;
        this.expansionDistrict = false;
        this.expansionHome = false;
        this.showAddFormuler = false;
        this.getRegions();
    }

    /**
     * Etent ou non la partie quartier
     */
    public toogleDistrict() {
        this.expansionDistrict = !this.expansionDistrict;
        this.expansionRegion = false;
        this.expansionCountry = false;
        this.expansionSector = false;
        this.expansionCommune = false;
        this.expansionHome = false;
        this.showAddFormuler = false;
        this.getDistricts();
    }

    /**
     * Etent ou non la partie Secteur
     */
    public toogleSector() {
        this.expansionSector = !this.expansionSector;
        this.expansionRegion = false;
        this.expansionCountry = false;
        this.expansionCommune = false;
        this.expansionDistrict = false;
        this.showAddFormuler = false;
        this.expansionHome = false;
        this.getSectors();
    }

    /**
     * Etent ou non la partie maison
     */
    public toogleHome() {
        this.expansionHome = !this.expansionHome;
        this.expansionRegion = false;
        this.expansionCountry = false;
        this.expansionCommune = false;
        this.expansionDistrict = false;
        this.expansionSector = false;
        this.showAddFormuler = false;
        this.getHomes();
    }

    /**
     * Etent ou non la partie maison
     */
    public toogleuserHome() {
        this.expansionuserHome = !this.expansionuserHome;
        this.expansionRegion = false;
        this.expansionCountry = false;
        this.expansionCommune = false;
        this.expansionDistrict = false;
        this.expansionSector = false;
        this.showAddFormuler = false;
        this.getHomeUsers();
    }

    /* public async addLocation() {
       const props = {
         source: 'configuration-management',
         action: 'add',
       };
       await this.modal.create(AfricleanAddLocalisationComponent, 'my-custom-modal-css', props);
       const data: { displayLocationConfirmation: boolean } = await this.modal.getData();
       if (data && data.displayLocationConfirmation) {
         this.observableProvider.loadAdresses.emit(true);
       }

     }
   */
    addLocalisation(action) {
        this.showAddFormuler = !this.showAddFormuler;
    }


    /**
     * Renvoi tous les secteurs d'un quartier
     * @param event évenement émis
     */
    public getSectors() {
        const url = 'secteur/';
        this.localisation.getSectorsByDistrictId(url).subscribe(
            (sectors) => {
                this.sectors = sectors;
            }
        );
    }

    /**
     * Renvoi tous les secteurs d'un quartier
     * @param event évenement émis
     */
    public getHomeUsers() {
        const url = 'home-user/';
        this.localisation.getSectorsByDistrictId(url).subscribe(
            (userHomes) => {
                this.userHomes = userHomes;
            }
        );
    }

    /**
     * Renvoi la liste des quartiers
     */
    public getDistricts() {
        const url = 'quartier/';
        this.localisation.getDistrictsByCommuneId(url).subscribe(
            (districts) => {
                this.districts = districts;
            }
        );
    }

    /**
     * Renvoi toutes les communes d'une région
     * @param event évenemnt emis
     */
    public getCommunes() {
        const url = 'commune/';
        this.localisation.getCommunesByRegionId(url).subscribe(
            (communes) => {
                this.communes = communes;
            }
        );
    }

    /**
     * Recupere toutes les regions d'un pays
     * @param event évenement émis
     */
    public getRegions() {
        // Recupères toutes les regions
        const url = 'region/';
        this.localisation.getRegionsByCountryId(url).subscribe(
            (regions) => {
                this.regions = regions;
            }
        );
    }

    /**
     * Récupère tous les pays
     */
    private getCountries() {
        this.localisation.getCountries('country').subscribe(
            (countries) => {
                this.countries = countries;
            }
        );
    }

    /**
     * Récupère tous les pays
     */
    private getHomes() {
        this.localisation.getCountries('foyer/').subscribe(
            (homes) => {
                this.homes = homes;
            }
        );
    }
    update(item: any, itemType: any) {
        this.showAddFormuler = !this.showAddFormuler;
    }

    delete(item: any, localisation: any) {
        if (localisation === 'country') {
            const url = 'country/delete/' + item.id;
            this.localisation.delateLocation(url).subscribe(
                (result) => {
                    this.getCountries();
                });
        }
        if (localisation === 'region') {
            const url = 'region/delete/' + item.id;
            this.localisation.delateLocation(url).subscribe(
                (result) => {
                    this.getRegions();
                });
        }
        if (localisation === 'commune') {
            const url = 'commune/delete/' + item.id;
            this.localisation.delateLocation(url).subscribe(
                (result) => {
                    this.getCommunes();
                });
        }
        if (localisation === 'district') {
            const url = 'quartier/delete/' + item.id;
            this.localisation.delateLocation(url).subscribe(
                (result) => {
                    this.getDistricts();
                });
        }

        if (localisation === 'home') {
            const url = 'foyer/delete/' + item.id;
            this.localisation.delateLocation(url).subscribe(
                (result) => {
                    this.getSectors();
                });
        }

        if (localisation === 'sector') {
            const url = 'secteur/delete/' + item.id;
            this.localisation.delateLocation(url).subscribe(
                (result) => {
                    this.getSectors();
                });
        }

        if (localisation === 'userHome') {
            const url = 'home-user/delete/' + item.id;
            this.localisation.delateLocation(url).subscribe(
                (result) => {
                    this.getSectors();
                });
        }

    }
}
